// src/events/Events.ts
import { TypedEventBus } from './EventBus'; //   
import { SkillExecutedEvent } from '../core/types/battle.types';

// ---   (World Scene) ---
export type WorldEvents = {
    CITY_SELECTED: { id: string };
    ARMY_MOVED: { from: { r: number, q: number }, to: { r: number, q: number } };
};

// ---   (Battle Scene) ---
export type BattleEvents = {
    //   id      
    UNIT_HOVER: { id: string; position: { x: number; y: number } };
    UNIT_MOUSEMOVE: { position: { x: number; y: number } };
    UNIT_OUT: { id: string };
    UNIT_CLICK: { id: string };
    UNIT_DIE: { id: string };
    
    TOOLTIP_UPDATE: { id: string };

    HEX_HOVER: { id: string; position: { x: number; y: number } };
    HEX_MOUSEMOVE: { position: { x: number; y: number } };
    HEX_OUT: { id: string };
    HEX_CLICK: { id: string };

    MOUSE_MOVE: { x: number; y: number };

    SKILL_EXECUTED: SkillExecutedEvent;
    STAMINA_DRAIN: { unitId: string; amount: number };
};

// ---   (System) ---
export type GlobalEvents = {
    CHANGE_SCENE: { scene: 'world' | 'battle' };
    RESOURCES_UPDATED: { gold: number };
};

/**
 *  
 */

//   
export const GlobalBus = new TypedEventBus<GlobalEvents>();

//    "let",      (destroy/new)
//     '    
export let BattleBus: TypedEventBus<BattleEvents>;

//       
export const initBattleBus = () => {
    if (BattleBus) BattleBus.destroy(); //     
    BattleBus = new TypedEventBus<BattleEvents>();
};