
import { PlanningContext, ActionCandidate, PlanningState, AIActionType, IActionGenerator, IEvaluator } from "@/core/battle/ai/types/ai.types";
import { Hex, PathInfo } from "@/core/types/battle.types";
import { coordsToId, idToCoords } from "@/core/utils/hex.utils";

export class MoveActionGenerator implements IActionGenerator {
    public generatorType: AIActionType = AIActionType.Move;
    // constructor(
    //     private evaluator: PositionEvaluator
    // ) {}

    public generate(state: PlanningState, ctx: PlanningContext, evaluator: IEvaluator<PathInfo>): ActionCandidate[] {
        //   
        const candidates: ActionCandidate[] = [];
        const startId = coordsToId(state.position);
        const { battleMap, reachableMap } = ctx;

        for (const [hexId, moveCost] of reachableMap.entries()) {
            if (hexId === coordsToId(state.position)) continue;
            
            const target = battleMap.get(hexId);
            if (!target || target.isObstacle || (target.entityId && hexId !== startId)) continue;
            
            const pathData = ctx.pathHexes.get(hexId);

            // 1.   (Backtracking)
            const path: string[] = [];
            let current: string | null = hexId;
            while (current !== null && current !== startId) {
                path.push(current);
                current = ctx.parentMap.get(current) || null;
            }
            const reversedPath = path.reverse();

            // 2.  PathInfo
            const pathInfo: PathInfo = {
                path: reversedPath,
                lastStepId: hexId,
                cost: moveCost //    reachableMap
            };

            // 3.  PathInfo  
            //         
            const evalResult = evaluator.evaluate(state, ctx, pathInfo);

            const newPos = idToCoords(hexId);
            // const [q, r] = hexId.split(':').map(Number);
            
            candidates.push({
                action: {
                    type: AIActionType.Move,
                    unitId: ctx.unit.unitId,
                    targetHexId: hexId,
                    targetPath: pathInfo,
                },
                utility: evalResult.utility,
                staminaAfter: state.stamina - moveCost,
                position: newPos,
                components: {
                    ...evalResult.components,
                    total: evalResult.utility,
                },
                metadata: {
                    type: pathData ? 'path-based' : 'opportunistic',
                    pathAlignment: !!pathData,
                },
            });
        }

        return candidates;
    }
}