import React from 'react';
import { useShallow } from 'zustand/react/shallow';
import { SmartBlock } from './SmartBlock';
import { StaminaCore } from './StaminaCore';
import { UnitStatsPortrait } from './UnitStatsPortrait';
import { BattleState, useBattleStore } from '@/store/useBattleStore';

import './BottomPanel.css';
import { FactionType } from '@/core/types/battle.types';

/**
 * BottomPanel -    
 * 
 * Layout: 25% | 35% | 40%
 * - SmartWindow:   (LOG, SKILL, ENEMY)
 * - StaminaCore:   stamina  preview
 * - UnitAnchor: stats + portrait  
 */
export function BottomPanel() {
  
  const { unit, skill, previewCost } = useBattleStore(useShallow((state: BattleState) => {
    
    const selectedUnitId = state.selectedUnitId;
    const selectedUnit = selectedUnitId ? state.units.get(selectedUnitId) : null;
    
    if (!selectedUnit) {
      return { unit: null, skill: null, previewCost: 0 };
    }

    let currentSkill = null;
    let cost = 0;

    if (state.selectedSkillId) {
      currentSkill = selectedUnit.skills.find((s: any) => s.id === state.selectedSkillId) || null;
      cost = currentSkill?.cost || 0;
    } else if (state.selectedHexId && state.selectedPath) {
      cost = state.selectedPath.cost || 0;
    }

    return { 
      unit: selectedUnit, 
      skill: currentSkill, 
      previewCost: cost 
    };
  }));

  if (!unit || unit.faction !== FactionType.Player) {
    return <div className="bottom-panel"/>;
  }

  return (
    <div className="bottom-panel">
      {/* Left 30%: Smart Block */}
      <SmartBlock skill={skill} unit={unit} />
      
      {/* Center 40%: Unit Name + Stamina */}
      <StaminaCore 
        unitName={unit.unitId}
        unitId={unit.unitId}
        currentStamina={unit.currentStamina}
        maxStamina={unit.stats.stamina}
        previewCost={previewCost}
      />
      
      {/* Right 30%: Stats + HP + Portrait */}
      <UnitStatsPortrait unit={unit} />
    </div>
  );
}
