import { CombatCalculator } from "@/core/battle/CombatCalculator";
import { PathCalculator } from "@/core/battle/PathCalculator";
import { BattleUnit, Hex } from "@/core/types/battle.types";
import { idToCoords } from "@/core/utils/hex.utils";



interface ActiveEnemy {
    instance: BattleUnit;
    damageTable: any;
    reachableMap: Map<string, number>;
}


export class ThreatMapCalculator {

    /**
     *  
     */
    public static calculate(
        unitReachableNodes: Map<string, number>, 
        enemies: BattleUnit[],
        defender: BattleUnit,
        battleMap: Map<string, Hex>
    ): Map<string, number> {
        const threatMap = new Map<string, number>();

        // Pre-computation
        const activeEnemies: ActiveEnemy[] = enemies.map(enemy => ({
            instance: enemy,
            damageTable: this.precomputeDamageKnapsack(enemy, defender),  //   defender
            reachableMap: PathCalculator.getReachableNodes(
                {q: enemy.q, r: enemy.r},
                {currentStamina: enemy.currentStamina, speed: enemy.stats.speed},
                battleMap
            ).reachableMap
        }));

        //   
        for (const defenderHex of unitReachableNodes) {
            const threat = this.evaluateSingleHex(defenderHex, activeEnemies, battleMap);
            if (threat > 0) {
                threatMap.set(defenderHex[0], threat);  // defenderHex[0] = hexId
            }
        }

        return threatMap;
    }

    /**
     *   
     */
    private static evaluateSingleHex(
        defenderHex: [string, number], 
        enemies: ActiveEnemy[],
        battleMap: Map<string, Hex>,
    ): number {
        
        const enemyOptions: Array<{
            enemy: ActiveEnemy;
            options: Array<{ slot: string; damage: number }>;
        }> = [];

        for (const enemyData of enemies) {
            const options: Array<{ slot: string; damage: number }> = [];
            const maxRange = enemyData.damageTable.maxRange;

            const pos = idToCoords(defenderHex[0]);
            const potentialAttackSlots = PathCalculator.getRadius(
                pos, 
                maxRange, 
                battleMap,
                true
            );

            //   Map
            potentialAttackSlots.forEach((dist, slotId) => {  //   
                const moveCost = enemyData.reachableMap.get(slotId);
                if (moveCost === undefined) return;

                const staminaLeft = enemyData.instance.currentStamina - moveCost;
                if (staminaLeft < 0) return;

                const damageTable = enemyData.damageTable.tables.get(dist);  //   access
                if (!damageTable) return;

                const potentialDamage = this.getMaxDamageForStamina(damageTable, staminaLeft);
                
                if (potentialDamage > 0) {
                    options.push({ slot: slotId, damage: potentialDamage });
                }
            });

            if (options.length > 0) {
                options.sort((a, b) => b.damage - a.damage);
                enemyOptions.push({ enemy: enemyData, options });
            }
        }


        // Constraint-based sorting
        enemyOptions.sort((a, b) => {
            const optA = a.options[0];
            const optB = b.options[0];

            // TypeScript  ,     , optA  optB 
            if (!optA || !optB) return 0; 

            if (a.options.length !== b.options.length) {
                return a.options.length - b.options.length;
            }
            return optB.damage - optA.damage;
        });

        // Resource locking
        let totalThreat = 0;
        const occupiedSlots = new Set<string>();

        for (const entry of enemyOptions) {
            const bestMove = entry.options.find(opt => !occupiedSlots.has(opt.slot));
            if (bestMove) {
                totalThreat += bestMove.damage;
                occupiedSlots.add(bestMove.slot);
            }
        }

        return totalThreat;
    }

    /**
     * Knapsack DP    defender
     */

    private static precomputeDamageKnapsack(enemy: BattleUnit, defender: BattleUnit) {
        const STEP = 5;
        //      ,     
        const maxStaminaSteps = Math.floor(enemy.currentStamina / STEP);
        const damageTablesByRange = new Map<number, number[]>();

        const skillsByRange = enemy.skills.reduce((acc, skill) => {
            if (!acc[skill.range]) acc[skill.range] = [];
            //     "" ( ,     )
            const stepCost = Math.ceil(skill.cost / STEP);
            acc[skill.range]!.push({
                stepCost: stepCost,
                damage: CombatCalculator.calculateRawDamage(enemy, defender, skill.powerMult || 1.0, skill.armorPenetration || 0, true)
            });
            return acc;
        }, {} as Record<number, {stepCost: number, damage: number}[]>);

        const sortedRanges = Object.keys(skillsByRange).map(Number).sort((a, b) => b - a);
        if (sortedRanges.length === 0) return { tables: damageTablesByRange, maxRange: 0 };

        const maxRange = sortedRanges[0];
        let lastDp = new Array(maxStaminaSteps + 1).fill(0);

        for (let dist: number = maxRange!; dist >= 1; dist--) {
            if (skillsByRange[dist]) {
                const nextDp = [...lastDp]; 
                for (const skill of skillsByRange[dist]!) {
                    for (let s = skill.stepCost; s <= maxStaminaSteps; s++) {
                        const potentialDmg = nextDp[s - skill.stepCost] + skill.damage;
                        if (potentialDmg > nextDp[s]) {
                            nextDp[s] = potentialDmg;
                        }
                    }
                }
                //  
                for (let s = 1; s <= maxStaminaSteps; s++) {
                    nextDp[s] = Math.max(nextDp[s], nextDp[s - 1]);
                }
                lastDp = nextDp;
            }
            damageTablesByRange.set(dist, lastDp);
        }

        return { tables: damageTablesByRange, maxRange, step: STEP };
    }

    private static getMaxDamageForStamina(damageTable: number[], staminaLeft: number, step: number = 5): number {
        const s = Math.floor(staminaLeft / step);
        if (s < 0) return 0;
        if (s >= damageTable.length) return damageTable[damageTable.length - 1] || 0;
        return damageTable[s] || 0;
    }
}
