import { UtilityComponents } from "@/core/battle/ai/AIDecisionLogger";
import { BehaviorAnalyzer } from "@/core/battle/ai/BehaviorAnalyzer";
import { 
    PlanningContext, 
    EvaluatedCandidate, 
    PlanningState, 
    HexDistanceInfo, 
    PathData, 
    IEvaluator 
} from "@/core/battle/ai/types/ai.types";
import { AIWeights, UTILITY_CONSTANTS } from "@/core/battle/ai/utils/AIConfig";
import { BattleUnit, PathInfo } from "@/core/types/battle.types";
import { coordsToId, getHexDistance, idToCoords } from "@/core/utils/hex.utils";

/**
 * Movement Evaluator
 * 
 * Evaluates movement candidates based on:
 * 1. Progress toward enemy (closing distance)
 * 2. Path alignment (strategic positioning)
 * 3. Risk factors (threat, opportunity attacks)
 * 4. Stamina efficiency (move cost)
 */
export class MovementEvaluator implements IEvaluator<PathInfo> {
    
    evaluate(
        state: PlanningState,
        ctx: PlanningContext,
        target: PathInfo
    ): EvaluatedCandidate {
        if (!target.lastStepId) {
            return { utility: 0 };
        }
        
        const CV = UTILITY_CONSTANTS.CREATURE_VALUE;
        const stackHP = ctx.unit.stats.hp * ctx.unit.count;
        const targetPos = idToCoords(target.lastStepId);
        
        // Get precomputed data
        const oppRisk = ctx.oppRiskCache.get(target.lastStepId) || 0;
        const distanceInfo = ctx.distanceMatrix.get(target.lastStepId);
        const pathData = ctx.pathHexes.get(target.lastStepId);
        
        // Calculate utility components
        const components = this.calculateComponents(
            state,
            ctx,
            targetPos,
            target.cost,
            distanceInfo,
            pathData,
            oppRisk,
            stackHP,
            CV
        );
        
        // Sum total utility
        let utility = Object.values(components)
            .reduce((sum, val) => sum + (val || 0), 0);

        if (ctx.goal?.type === "ELIMINATE") {
            utility += 1
        }
        
        components.total = utility;
        
        return { utility, components };
    }
    
    // 
    // COMPONENT CALCULATION
    // 
    
    private calculateComponents(
        state: PlanningState,
        ctx: PlanningContext,
        targetPos: { q: number; r: number },
        moveCost: number,
        distanceInfo: HexDistanceInfo | undefined,
        pathData: PathData | undefined,
        oppRisk: number,
        stackHP: number,
        CV: number
    ): UtilityComponents {
        const { unit, enemies, weights, threatMap } = ctx;
        
        // Get distances to nearest enemy
        const { minDistBefore, minDistAfter } = this.calculateDistances(
            state.position,
            targetPos,
            enemies,
            distanceInfo
        );
        
        // 1. Movement economics (progress vs cost)
        const movement = this.evaluateMovement(
            unit,
            state.position,
            targetPos,
            moveCost,
            minDistBefore,
            minDistAfter,
            CV,
            pathData
        );
        
        // 2. Strategic value (path alignment)
        const pathBonus = this.evaluatePathAlignment(
            unit,
            moveCost,
            CV,
            weights,
            pathData
        );
        
        // 3. Entering attack range
        const rangeBonus = this.evaluateRangeEntry(
            unit,
            minDistBefore,
            minDistAfter,
            CV,
            weights
        );
        
        // 4. Risk assessment
        const risk = this.evaluateRisk(
            state.position,
            targetPos,
            oppRisk,
            threatMap,
            stackHP,
            CV,
            weights
        );
        
        return {
            progress: movement.progress,
            moveCost: movement.cost,
            pathAlignment: pathBonus,
            enteringRange: rangeBonus,
            threatDelta: risk.threatDelta,
            threatZone: risk.threatZone,
            oppRisk: risk.oppRisk,
            total: 0    // just for typing, real total calculates in parent function
        };
    }
    
    // 
    // DISTANCE CALCULATIONS
    // 
    
    private calculateDistances(
        fromPos: { q: number; r: number },
        toPos: { q: number; r: number },
        enemies: BattleUnit[],
        distanceInfo?: HexDistanceInfo
    ): { minDistBefore: number; minDistAfter: number } {
        if (distanceInfo) {
            // Use precomputed distance to target
            const minDistAfter = distanceInfo.minDist;
            
            // Calculate distance from current position
            let minDistBefore = Infinity;
            for (const enemy of enemies) {
                const dist = getHexDistance(fromPos, { q: enemy.q, r: enemy.r });
                minDistBefore = Math.min(minDistBefore, dist);
            }
            
            return { minDistBefore, minDistAfter };
        }
        
        // Fallback: calculate both
        let minDistBefore = Infinity;
        let minDistAfter = Infinity;
        
        for (const enemy of enemies) {
            const enemyPos = { q: enemy.q, r: enemy.r };
            const distBefore = getHexDistance(fromPos, enemyPos);
            const distAfter = getHexDistance(toPos, enemyPos);
            minDistBefore = Math.min(minDistBefore, distBefore);
            minDistAfter = Math.min(minDistAfter, distAfter);
        }
        
        return { minDistBefore, minDistAfter };
    }
    
    // 
    // MOVEMENT ECONOMICS
    // 
    
    /**
     * Evaluate movement efficiency: progress toward enemy vs stamina cost
     * 
     * Core idea: Moving toward enemy should be roughly stamina-neutral
     * - Progress reward  stamina cost
     * - Small bonus to encourage movement (otherwise AI stands still)
     */
    private evaluateMovement(
        unit: BattleUnit,
        fromPos: { q: number; r: number },
        toPos: { q: number; r: number },
        moveCost: number,
        minDistBefore: number,
        minDistAfter: number,
        CV: number,
        pathData?: PathData
    ): { progress: number; cost: number } {
        const maxStamina = unit.stats.stamina;
        const staminaValue = CV / maxStamina; // Value of 1 stamina point
        
        // Cost is always negative
        const cost = -moveCost * staminaValue;
        
        // Calculate progress (hexes closer to enemy)
        const moveDist = getHexDistance(fromPos, toPos);
        let progressSteps = Math.max(0, minDistBefore - minDistAfter);
        
        // Special case: Maneuvering on strategic path
        if (progressSteps <= 0 && pathData) {
            // Give partial credit for staying on path
            const MANEUVER_EFFICIENCY = 0.9;
            progressSteps = moveDist * MANEUVER_EFFICIENCY;
        }
        
        // Calculate hex value (stamina per hex)
        const hexValue = progressSteps > 0 
            ? (moveCost / progressSteps) 
            : 30; // Default for non-progress moves
        
        // Progress reward
        const progress = progressSteps * hexValue * staminaValue;
        
        return { progress, cost };
    }
    
    // 
    // STRATEGIC PATH ALIGNMENT
    // 
    
    /**
     * Evaluate strategic value of following path to high-value targets
     * 
     * Factors:
     * - Target priority (based on potential damage)
     * - Distance to target (decay over distance)
     * - Flexibility (multiple targets in area)
     */
    private evaluatePathAlignment(
        unit: BattleUnit,
        moveCost: number,
        CV: number,
        weights: AIWeights,
        pathData?: PathData
    ): number {
        if (!pathData) return 0;
        
        // Net damage potential (our damage - enemy retaliation)
        const netMaxDmg = pathData.maxDmg - Math.sqrt(pathData.enemyMaxDmg * CV);
        
        // Target priority (logarithmic scale)
        const targetPriority = Math.log(1 + Math.max(0, netMaxDmg) / CV) / 
                              Math.log(UTILITY_CONSTANTS.PATH_LOG_BASE);
        
        // Distance decay (farther = less attractive)
        const distanceDecay = 1 + (pathData.dist * 0.08 * weights.PATH_ALIGNMENT_BONUS);
        const pathValue = targetPriority / distanceDecay;
        
        // Flexibility bonus (multiple weak targets better than one)
        let flexBonus = 0;
        const netSumDmg = (pathData.sumDmg - pathData.enemySumDmg) / CV;
        if (netSumDmg > 0) {
            flexBonus = Math.log(1 + netSumDmg) / 
                       Math.log(UTILITY_CONSTANTS.PATH_LOG_BASE) * 0.3;
        }
        
        // Convert to CV units based on actual terrain cost
        const actualStepCost = pathData.dist > 0 && moveCost > 0 
            ? (moveCost / pathData.dist) 
            : 30;
        
        const staminaValue = CV / unit.stats.stamina;
        const stepValue = actualStepCost * staminaValue;
        
        return (pathValue + flexBonus) * stepValue;
    }
    
    // 
    // RANGE ENTRY
    // 
    
    /**
     * Bonus for entering attack range
     * Helps AI close the gap to start attacking
     */
    private evaluateRangeEntry(
        unit: BattleUnit,
        minDistBefore: number,
        minDistAfter: number,
        CV: number,
        weights: AIWeights
    ): number {
        const maxRange = BehaviorAnalyzer.getMaxSkillRange(unit);
        
        const wasOutside = minDistBefore > maxRange;
        const nowInside = minDistAfter <= maxRange;
        
        if (wasOutside && nowInside) {
            return 1.5 * CV * weights.ENTERING_RANGE_WEIGHT;
        }
        
        return 0;
    }
    
    // 
    // RISK ASSESSMENT
    // 
    
    /**
     * Evaluate danger of moving to this position
     * 
     * Components:
     * 1. Threat Delta - change in danger level (prefer safer moves)
     * 2. Threat Zone - absolute danger (avoid red zones)
     * 3. Opportunity Risk - damage from leaving current position
     */
    private evaluateRisk(
        fromPos: { q: number; r: number },
        toPos: { q: number; r: number },
        oppRisk: number,
        threatMap: Map<string, number>,
        stackHP: number,
        CV: number,
        weights: AIWeights
    ): { threatDelta: number; threatZone: number; oppRisk: number } {
        const currentThreat = threatMap.get(coordsToId(fromPos)) || 0;
        const targetThreat = threatMap.get(coordsToId(toPos)) || 0;
        
        // 1. Threat Delta (change in danger)
        const rawDelta = targetThreat - currentThreat;
        const relativeDelta = rawDelta / stackHP;
        const threatDelta = -Math.tanh(relativeDelta * 2) * weights.RISK_WEIGHT;
        
        // 2. Threat Zone (absolute danger level)
        const threatLevel = targetThreat / stackHP;
        const threatZone = -threatLevel * CV * weights.THREAT_ZONE_WEIGHT;
        
        // 3. Opportunity Risk (damage from movement)
        let oppRiskUtility = 0;
        if (oppRisk > 0) {
            const damageComponent = oppRisk / stackHP;
            const interruptPenalty = UTILITY_CONSTANTS.INTERRUPT_PENALTY_BASE;
            oppRiskUtility = -(damageComponent + interruptPenalty) * CV * weights.OPPORTUNITY_RISK_WEIGHT;
        }
        
        return {
            threatDelta,
            threatZone,
            oppRisk: oppRiskUtility
        };
    }
}