// planning/SequencePlanner.ts
import type { StateSimulator } from './StateSimulator';

import type { PruningStrategy } from './PruningStrategy';
import type { PlanningResult, PlanningMetadata } from './PlanningResult';
import type { 
    PlanningState, 
    PlanningContext, 
    ActionSequence, 
    ActionCandidate,
    ActionComponent 
} from '../types/ai.types';
import { AIActionType } from '../types/ai.types';
import { PLANNING_CONFIG } from '../utils/AIConfig';

/**
 * Recursive multi-step action planner
 * Plans sequences like: Move  Attack, Attack  Wait
 * 
 * Uses depth-limited search with:
 * - Constraint filtering (movemove, after wait)
 * - Adaptive pruning (reduce branching)
 * - State simulation (track stamina, position)
 * 
 * Returns PlanningResult with metadata for debugging
 */
export class SequencePlanner {
    /** Metadata for current planning session (root depth only) */
    private currentMetadata: PlanningMetadata = {
        candidatesGenerated: 0,
        topCandidates: [],
        prunedCount: 0,
        remainingCount: 0
    };
    
    constructor(
        private stateSimulator: StateSimulator,
        private pruningStrategy: PruningStrategy
    ) {}
    
    /**
     * Plan best sequence of actions (entry point)
     * Returns best action sequence + metadata (if debug enabled)
     */
    planBestSequence(
        initialState: PlanningState,
        context: PlanningContext,
        components: ActionComponent[],
        maxDepth: number = 3
    ): PlanningResult {
        // Reset metadata for new planning session
        this.currentMetadata = {
            candidatesGenerated: 0,
            topCandidates: [],
            prunedCount: 0,
            remainingCount: 0
        };
        
        // Execute recursive planning
        const sequence = this.planRecursive(
            initialState, 
            context, 
            components, 
            0, 
            maxDepth
        );
        
        // Return with metadata
        return {
            sequence,
            metadata: this.currentMetadata
        };
    }
    
    /**
     * Recursive planning with constraints and pruning
     * Explores action tree depth-first
     */
    private planRecursive(
        state: PlanningState,
        ctx: PlanningContext,
        components: ActionComponent[],
        depth: number,
        maxDepth: number
    ): ActionSequence | null {
        // 
        // TERMINAL CONDITIONS
        // 
        
        if (depth >= maxDepth) {
            return null;
        }
        
        if (state.stamina <= 0) {
            return null;
        }
        
        // 
        // CONSTRAINT: Nothing after Wait
        // 
        
        const lastAction = state.actionsUsed[state.actionsUsed.length - 1];
        
        if (lastAction?.type === AIActionType.Wait) {
            return null; // Stop planning after wait
        }
        
        // 
        // GENERATE CANDIDATES (with inline constraints)
        // 
        
        let allCandidates: ActionCandidate[] = [];
        
        for (const { generator, evaluator } of components) {
            // CONSTRAINT: No move after move

            if (lastAction?.type === AIActionType.Move && 
                generator.generatorType === AIActionType.Move) {
                continue;
            }
            
            const candidates = generator.generate(state, ctx, evaluator);
            allCandidates.push(...candidates);
        }
        
        if (allCandidates.length === 0) {
            return null;
        }
        
        // 
        // SORT & PRUNE
        // 
        
        allCandidates.sort((a, b) => b.utility - a.utility);
        
        if (depth === 0 && PLANNING_CONFIG.debug) {
            this.currentMetadata.candidatesGenerated = allCandidates.length;
            // Store top 10 candidates for logging
            this.currentMetadata.topCandidates = allCandidates.slice(0, 10);
        }
        
        // Prune candidates
        const prunedCandidates = this.pruningStrategy.prune(
            allCandidates,
            depth,
            ctx
        );
        
        if (depth === 0 && PLANNING_CONFIG.debug) {
            this.currentMetadata.prunedCount = allCandidates.length - prunedCandidates.length;
            this.currentMetadata.remainingCount = prunedCandidates.length;
        }
        
        // 
        // EXPLORE BRANCHES
        // 
        
        let bestSequence: ActionSequence | null = null;
        let bestUtility = -Infinity;
        
        for (const candidate of prunedCandidates) {
            // Simulate action outcome
            const newState = this.stateSimulator.simulate(state, candidate);
            
            // Check if terminal action (Wait)
            if (candidate.action.type === AIActionType.Wait) {
                const sequence: ActionSequence = {
                    actions: [candidate.action],
                    totalUtility: candidate.utility,
                    finalStamina: newState.stamina
                };
                
                if (sequence.totalUtility > bestUtility) {
                    bestUtility = sequence.totalUtility;
                    bestSequence = sequence;
                }
                continue;
            }
            
            // Recurse for non-terminal actions
            const futureSequence = this.planRecursive(
                newState,
                ctx,
                components,
                depth + 1,
                maxDepth
            );
            
            // Calculate total utility
            const totalUtility = futureSequence
                ? candidate.utility + futureSequence.totalUtility
                : candidate.utility;
            
            // Track best sequence
            if (totalUtility > bestUtility) {
                bestUtility = totalUtility;

                bestSequence = {
                    actions: futureSequence 
                        ? [candidate.action, ...futureSequence.actions]
                        : [candidate.action],
                    totalUtility,
                    finalStamina: futureSequence?.finalStamina ?? newState.stamina
                };
            }
        }
        
        return bestSequence;
    }
}