import { UnitStack } from '@/view/renderers/UnitStack';
import { FactionType } from '@/core/types/battle.types';
import { axialToPixel } from '@/core/utils/hex.utils';
import { useBattleStore } from '@/store/useBattleStore';
import * as PIXI from 'pixi.js';


export class UnitFactory {
    private static configCache: Map<string, any> = new Map();

    static async createUnit(unitType: string, q: number, r: number, count: number, faction: FactionType = FactionType.Player): Promise<UnitStack> {
        const jsonUrl = `assets/units/${unitType}.json`;
        const pngUrl = `assets/units/${unitType}.png`;

        let config = this.configCache.get(unitType);
        if (!config) {
            config = await PIXI.Assets.load(jsonUrl);
            this.configCache.set(unitType, config);
        }

        const texture = await PIXI.Assets.load(pngUrl);
        const unitId = `${unitType}_${Date.now()}_${Math.floor(Math.random() * 1000)}`;
        const pos = axialToPixel(q, r);
        // 1.    STORE
        useBattleStore.getState().addUnit({
            unitId: unitId,
            unitType: unitType,
            count: count,
            currentUnitHp: config.stats.hp,
            currentStamina: config.stats.stamina,
            q: q,
            r: r,
            isDead: false,
            faction: faction,
            stats: { ...config.stats },
            skills: [ ...config.skills ]
        });

        // 2.   
        const unit = new UnitStack(texture, config, unitId, faction, q, r, pos.x, pos.y);

        return unit;
    }
}