import { BattleStatus, BattleUnit, Hex, FactionType, TurnPool, PathInfo } from '@/core/types/battle.types';
import { BattleMut, getInitialState, initialPathInfo } from '@/store/battleMutations';
import { create } from 'zustand';
import { devtools } from 'zustand/middleware'
import { subscribeWithSelector } from 'zustand/middleware';

export interface BattleState {
    // === STATE (data only) ===
    status: BattleStatus;
    units: Map<string, BattleUnit>;
    battleMap: Map<string, Hex>;
    
    // UI state
    selectedUnitId: string | null;
    selectedSkillId: string | null;
    selectedHexId: string | null;
    selectedPath: PathInfo;
    
    // Turn state
    round: number;
    activeFaction: FactionType;
    turnQueue: TurnPool[];
    finishedPools: TurnPool[];
    
    // Pathfinding state
    reachableHexes: string[];
    reachableHexesWithCost: Map<string, number>;
    targetHexes: string[];
    
    // === SIMPLE SETTERS (no complex logic) ===
    setStatus: (status: BattleStatus) => void;
    setSelectedUnit: (id: string | null) => void;
    setSelectedHex: (id: string | null) => void;
    setSelectedSkill: (id: string | null) => void;
    setSelectedPath: (pathInfo: PathInfo) => void;
    resetSelectedPath: () => void;
    
    setReachableHexes: (
        reachableIds: string[],
        reachableMap: Map<string, number>
    ) => void;
    
    setTargetHexes: (hexes: string[]) => void;
    
    // Units
    addUnit: (unit: BattleUnit) => void;
    setUnits: (units: BattleUnit[]) => void;
    updateUnit: (id: string, updates: Partial<BattleUnit>) => void;
    removeUnit: (id: string) => void;
    
    // Hexes
    setBattleMap: (map: Map<string, Hex>) => void;
    updateHex: (hexId: string, data: Partial<Hex>) => void;
    
    // Turn state (simple setters - TurnManager calls these)
    setTurnQueue: (queue: TurnPool[]) => void;
    setRound: (round: number) => void;
    setActiveFaction: (faction: FactionType) => void;
    

    // Complex setters
    applyUnitDeath: (data: { 
        unitId: string; 
        hexId: string; 
        newQueue: TurnPool[]; 
    }) => void;

    apply: (fn: (state: BattleState) => void) => void;
    
    // Reset
    reset: () => void;
}


export const useBattleStore = create<BattleState>()(
    devtools(
        subscribeWithSelector((set, get) => ({
            ...getInitialState(),

            // Generic apply -  
            apply: (fn) => set((state) => {
                fn(state);        //   
                return { ...state }; //     
            }),
            
            // Simple setters -   
            updateHex: (id, patch) => set(state => {
                BattleMut.updateHex(state, id, patch);
                return { ...state };
            }),
            
            updateUnit: (id, patch) => set(state => {
                BattleMut.updateUnit(state, id, patch);
                return { ...state };
            }),
            
            resetSelectedPath: () => set(state => {
                BattleMut.resetSelectedPath(state);
                return { ...state };
            }),
            
            setReachableHexes: (hexes, costMap) => set(state => {
                BattleMut.setReachableHexes(state, hexes, costMap);
                return { ...state };
            }),

            // === SIMPLE SETTERS ===
            setStatus: (status) => set({ status }),
            setSelectedUnit: (id) => set({ selectedUnitId: id }),
            setSelectedHex: (id) => set({ selectedHexId: id }),
            setSelectedSkill: (id) => set({ selectedSkillId: id }),

            setSelectedPath: (pathInfo) => set({ 
                selectedPath: pathInfo 
            }),

            // resetSelectedPath: () => set({ 
            //     selectedPath: initialPathInfo 
            // }),
            
            // setReachableHexes: (reachableIds, reachableMap) => set({
            //     reachableHexes: reachableIds,
            //     reachableHexesWithCost: reachableMap
            // }),
            
            setTargetHexes: (hexes) => set({ targetHexes: hexes }),
            
            // === UNITS ===
            addUnit: (unit) => set((state) => {
                const newUnits = new Map(state.units);
                newUnits.set(unit.unitId, unit);
                return { units: newUnits };
            }),

            setUnits: (unitsArray) => {
                const unitsMap = new Map();
                unitsArray.forEach(u => unitsMap.set(u.unitId, u));
                set({ units: unitsMap });
            },

            // updateUnit: (id, updates) => set((state) => {
            //     const newUnits = new Map(state.units);
            //     const unit = newUnits.get(id);
            //     if (unit) {
            //         newUnits.set(id, { ...unit, ...updates });
            //     }
            //     return { units: newUnits };
            // }),
            
            removeUnit: (id) => set((state) => {
                const newUnits = new Map(state.units);
                newUnits.delete(id);
                return { units: newUnits };
            }),
            
            // === HEXES ===
            setBattleMap: (map) => set({ battleMap: map }),

            // updateHex: (hexId, data) => set((state) => {
            //     const hex = state.battleMap.get(hexId);
            //     if (!hex) return state;

            //     const newMap = new Map(state.battleMap);
            //     newMap.set(hexId, { ...hex, ...data });
            //     return { battleMap: newMap };
            // }),

            // === Complex setters
            applyUnitDeath: (data: { unitId: string, hexId: string, newQueue: TurnPool[] }) => set((state) => {
                const { unitId, hexId, newQueue } = data;
                
                const newUnits = new Map(state.units);
                const unit = newUnits.get(unitId);
                if (unit) {
                    newUnits.set(unitId, { 
                        ...unit, 
                        isDead: true, 
                        count: 0, 
                        currentUnitHp: 0 
                    });
                }

                // 2.    (  battleMap  Map)
                const newBattleMap = new Map(state.battleMap);
                const hex = newBattleMap.get(hexId);
                if (hex) {
                    newBattleMap.set(hexId, { ...hex, entityId: undefined });
                }

                return {
                    units: newUnits,
                    battleMap: newBattleMap,
                    turnQueue: newQueue,
                    //       
                    selectedUnitId: state.selectedUnitId === unitId ? null : state.selectedUnitId
                };
            }),
            
            // === TURN STATE (simple setters) ===
            setTurnQueue: (queue) => set({ turnQueue: queue }),
            setRound: (round) => set({ round }),
            setActiveFaction: (faction) => set({ activeFaction: faction }),

            // === RESET ===
            reset: () => set(getInitialState()),
        })),
        { name: 'BattleGame', enabled: true }
    )
);