import { EFFECTIVE_COUNT } from "@/core/constants/battle.constants";
import type { BattleUnit, Skill, CombatResult } from "@/core/types/battle.types";
import { getHexDistance, idToCoords } from "@/core/utils/hex.utils";



export class CombatCalculator {

    /** *    (  AI) 
     */
    static getHitChance(attacker: BattleUnit, defender: BattleUnit, skill: Skill): number {
        const isRanged = skill.range > 2;
        const attackSkill = isRanged ? attacker.stats.rangedSkill : attacker.stats.meleeSkill;
        const fatigueMod = attacker.currentStamina < (attacker.stats.stamina * 0.2) ? -5 : 0;
        
        let hitChance = attackSkill + (skill.hitChanceBonus || 0) - defender.stats.evasion + fatigueMod;
        return Math.max(5, Math.min(95, hitChance));
    }

    static getEffectiveCount(attacker: BattleUnit) {
        return Math.pow(attacker.count, EFFECTIVE_COUNT)
    }

    static calculateSkillRawDamage(attacker: BattleUnit, defender: BattleUnit, skill: Skill, isAverage: boolean = true): number {
        return this.calculateRawDamage(attacker, defender, skill.powerMult, skill.armorPenetration, isAverage)
    }

    static calculateOpportunityRawDamage(attacker: BattleUnit, defender: BattleUnit, isAverage: boolean = true): number {
        return this.calculateRawDamage(attacker, defender, 0.5, 0.1, isAverage);
    }

    /**
     *    (    Armor Penetration)
     * @param isAverage -  true,    ( AI),  false -  ( )
     */
    static calculateRawDamage(attacker: BattleUnit, defender: BattleUnit, powerMult: number, armorPenetration: number, isAverage: boolean = true): number {
        const effectiveCount = this.getEffectiveCount(attacker);
        
        const baseDamage = isAverage 
            ? (attacker.stats.damageMin + attacker.stats.damageMax) / 2
            : attacker.stats.damageMin + Math.random() * (attacker.stats.damageMax - attacker.stats.damageMin);

        const rawDamage = baseDamage * effectiveCount * (powerMult || 1);

        //    
        const pen = armorPenetration || 0;
        const penetratingDamage = rawDamage * pen;
        let blockedDamage = rawDamage * (1 - pen);
        
        //  
        const mitigation = Math.min(0.8, defender.stats.armor / 100);
        blockedDamage = blockedDamage * (1 - mitigation);

        return Math.round(penetratingDamage + blockedDamage);
    }

    static calculateAttack(attacker: BattleUnit, defender: BattleUnit, skill: Skill): CombatResult {
        const hitChance = this.getHitChance(attacker, defender, skill);
        const roll = Math.random() * 100;

        if (roll > hitChance) {
            return this.emptyResult(defender, true, `Miss!`);
        }

        const finalDamage = this.calculateSkillRawDamage(attacker, defender, skill, false);
        
        const isCrit = Math.random() < (attacker.stats.meleeSkill / 1000); 
        const totalDamage = isCrit ? Math.round(finalDamage * 1.5) : finalDamage;

        return this.applyDamageToStack(defender, totalDamage, isCrit);
    }

    static checkOpportunityAttackersOnHex(
        movingUnitId: string,
        hexId: string,
        units: Map<string, BattleUnit>,
    ): Array<{ attackerId: string }> {
        const movingUnit = units.get(movingUnitId);
        if (!movingUnit) return [];
        
        const hex = idToCoords(hexId);

        const attackers: Array<{ attackerId: string }> = [];
        
        for (const [unitId, unit] of units.entries()) {
            if (unit.faction === movingUnit.faction) continue; // Skip allies
            if (unit.isDead) continue;
            if (unit.opportunityAttackUsed) continue; // Already used this round
            
            // const meleeSkill = getMeleeSkill(unit);
            // if (!meleeSkill) continue; // No melee capability
            
            const distance = getHexDistance(hex, unit);
            
            if (distance <= 1) {
                attackers.push({ attackerId: unitId });
            }
            // if (distance <= meleeSkill.range) {
            //     attackers.push({ attackerId: unitId, skill: meleeSkill });
            // }
        }
        
        return attackers;
    }

    private static applyDamageToStack(defender: BattleUnit, damage: number, isCrit: boolean): CombatResult {
        const totalHpBefore = (defender.count - 1) * defender.stats.hp + defender.currentUnitHp;
        const totalHpAfter = Math.max(0, totalHpBefore - damage);

        const newCount = Math.ceil(totalHpAfter / defender.stats.hp);
        const newCurrentHp = totalHpAfter % defender.stats.hp || (totalHpAfter > 0 ? defender.stats.hp : 0);

        return {
            targetId: defender.unitId,
            damage,
            isCrit,
            isMiss: false,
            finalDefenderHp: newCurrentHp,
            finalDefenderCount: newCount,
            isDead: totalHpAfter <= 0,
            details: `${isCrit ? 'CRIT! ' : ''}Dealt ${damage} damage.`
        };
    }

    private static emptyResult(defender: BattleUnit, isMiss: boolean, details: string): CombatResult {
        return {
            targetId: defender.unitId,
            damage: 0, 
            isCrit: false, 
            isMiss, 
            finalDefenderHp: defender.currentUnitHp,
            finalDefenderCount: defender.count,
            isDead: false,
            details
        };
    }
}