
import { useGlobalStore } from '../../store/useGlobalStore';
import { GameScene } from '../../core/types/global.types';
import { WorldUI } from './world/WorldUI';
import { BattleUI } from './battle/BattleUI';
import { useLayoutEffect } from 'react';

interface RootUIProps {
  onReady: (newScene: GameScene) => void;
}

export const RootUI = ({ onReady }: RootUIProps) => {
    const currentScene = useGlobalStore((state) => state.currentScene);
    useLayoutEffect(() => {
        onReady(currentScene);
    }, [currentScene]);

    return (
        <>
            {currentScene === GameScene.World && <WorldUI />}
            {currentScene === GameScene.Battle && <BattleUI />}
        </>
    );
};