import { type TurnPool, Point } from "@/core/types/battle.types"


export function mulberry32(seed: number) {
  return function () {
    let t = (seed += 0x6d2b79f5);
    t = Math.imul(t ^ (t >>> 15), t | 1);
    t ^= t + Math.imul(t ^ (t >>> 7), t | 61);
    return ((t ^ (t >>> 14)) >>> 0) / 4294967296;
  };
}

export function hashSeed(seed: string): number {
  let h = 2166136261;
  for (let i = 0; i < seed.length; i++) {
    h ^= seed.charCodeAt(i);
    h = Math.imul(h, 16777619);
  }
  return h >>> 0;
}

export const getSplinePoints = (points: Point[]): Point[] => {
    // Guard clause:   ,   
    if (points.length < 2) return [...points];

    const splinePoints: Point[] = [];
    
    //   Catmull-Rom
    const catmullRom = (p0: number, p1: number, p2: number, p3: number, t: number): number => {
        const t2 = t * t; 
        const t3 = t2 * t;
        return 0.5 * ((2 * p1) + (-p0 + p2) * t + (2 * p0 - 5 * p1 + 4 * p2 - p3) * t2 + (-p0 + 3 * p1 - 3 * p2 + p3) * t3);
    };

    for (let i = 0; i < points.length - 1; i++) {
        // TypeScript ,  i  i+1    
        const p1 = points[i]!; 
        const p2 = points[i + 1]!;
        
        //  Nullish coalescing (??)  OR (||) 
        //  ,     0
        const p0 = points[i - 1] ?? p1;
        const p3 = points[i + 2] ?? p2;

        for (let t = 0; t <= 1; t += 0.05) {
            splinePoints.push({
                x: catmullRom(p0.x, p1.x, p2.x, p3.x, t),
                y: catmullRom(p0.y, p1.y, p2.y, p3.y, t)
            });
        }
    }
    return splinePoints;
};

export function generateTurnQueue(units: any[]): TurnPool[] {
    // 1.    (   )
    const sorted = [...units]
        .filter(u => u.count > 0)
        .sort((a, b) => b.stats.initiative - a.stats.initiative);

    const queue: TurnPool[] = [];
    if (sorted.length === 0) return queue;
    const faction = sorted[0].faction;
    let n = 1;
    // 2.   
    let currentPool: TurnPool = {
        id: `${faction}_${n}_${Date.now()}`,
        faction: faction,
        unitIds: [sorted[0].unitId]
    };

    // 3.   ,     
    for (let i = 1; i < sorted.length; i++) {
        const unit = sorted[i];
        if (unit.faction === currentPool.faction) {
            currentPool.unitIds.push(unit.unitId);
        } else {
            queue.push(currentPool);
            n = n + 1;
            currentPool = {
                id: `${unit.faction}_${n}_${Date.now()}`,
                faction: unit.faction,
                unitIds: [unit.unitId]
            };
        }
    }
    queue.push(currentPool);
    
    return queue;
}
