import { EventEmitter } from 'pixi.js';

export class TypedEventBus<T> {
    //  v8     EventEmitter 
    private emitter = new EventEmitter();

    emit<K extends keyof T>(event: K, data: T[K]): void {
        //   event  string,  EventEmitter  string | symbol
        this.emitter.emit(event as string, data);
    }

    on<K extends keyof T>(event: K, fn: (data: T[K]) => void): void {
        this.emitter.on(event as string, fn);
    }

    off<K extends keyof T>(event: K, fn: (data: T[K]) => void): void {
        this.emitter.off(event as string, fn);
    }

    destroy(): void {
        this.emitter.removeAllListeners();
    }
}