import { BattleUnit, FactionType, TurnPool } from '@/core/types/battle.types';
import { BattleState } from './useBattleStore'; //    



// 1.    (Unit Finished)
// :         ?
export const isUnitFinished = (state: BattleState, unitId: string): boolean => {
    return state.finishedPools.some((pool: TurnPool) => pool.unitIds.includes(unitId));
};

// 2.   (Unit Active)
// :     ,     ?
export const isUnitInActivePool = (state: BattleState, unitId: string): boolean => {
    const activePool = getActivePool(state);
    return activePool?.unitIds.includes(unitId) ?? false;
};

// 3.    (Active Pool)
// :  ' ,   .
export const getActivePool = (state: BattleState): TurnPool | null => {
    return state.turnQueue[0] || null;
};

export const getActiveUnit = (state: BattleState): BattleUnit | null => {
  if (!state.selectedUnitId) return null;
  
  const unit = state.units.get(state.selectedUnitId);
  return unit ?? null; //  undefined  Map.get  null
};

export const getActivePlayerUnit = (state: BattleState): BattleUnit | null => {
  const activeUnit = getActiveUnit(state);
  
  return activeUnit && activeUnit.faction === FactionType.Player ? activeUnit : null;
};