import { CombatExecutor } from "@/core/battle/CombatExecutor";
import { ControlStateMachine } from "@/core/battle/ControlStateMachine";
import { EntityRegistry } from "@/core/battle/EntityRegistry";
import { HighlightManager } from "@/core/battle/HighlightManager";
import { MovementExecutor } from "@/core/battle/MovementExecutor";
import { SkillManager } from "@/core/battle/SkillManager";
import { HEX_HIGHLIGHT_COLOR } from "@/core/constants/battle.constants";
import { FactionType } from "@/core/types/battle.types";
import { coordsToId } from "@/core/utils/hex.utils";
import { BattleBus } from "@/events/Events";
import { getActivePool, isUnitFinished } from "@/store/battleSelectors";
import { useBattleStore } from "@/store/useBattleStore";

/**
 * Handles all user input (hover, click, mouse)
 * Maintains input state machine
 */
export class InputController {
    private hotkeysInitialized = false;
    private lastMousePosition: { x: number; y: number } | null = null;
    
    constructor(
        private highlightManager: HighlightManager,
        private movementExecutor: MovementExecutor,
        private combatExecutor: CombatExecutor,
        private entityRegistry: EntityRegistry,
        private stateMachine: ControlStateMachine,
        private skillManager: SkillManager,
    ) {
        this.setupEventHandlers();
        // this.setupHotkeys();
    }

    public initializeHotkeys(): void {
        if (this.hotkeysInitialized) return;
        this.setupHotkeys();
        this.hotkeysInitialized = true;
    }

    private setupHotkeys(): void {
        this.onKeyDown = this.onKeyDown.bind(this); // 
        window.addEventListener('keydown', this.onKeyDown);
        
    }


    // --- '   OFF ---
    private handleHexHoverBound = (data: { id: string; position: { x: number; y: number } }) => this.handleHexHover(data.id);
    private handleHexClickBound = (data: { id: string}) => this.handleHexClick(data.id);
    private handleHexOutBound = (data: { id: string}) => this.handleHexOut(data.id);
    
    private handleUnitHoverBound = (data: { id: string; position: { x: number; y: number } }) => this.handleUnitHover(data.id);
    private handleUnitClickBound = (data: { id: string}) => this.handleUnitClick(data.id);
    private handleUnitOutBound = (data: { id: string}) => this.handleUnitOut(data.id);

    private handleMouseMoveBound = (position: { x: number; y: number }) => this.handleMouseMove(position);
    
    private setupEventHandlers(): void {
        BattleBus.on('HEX_HOVER', this.handleHexHoverBound);
        BattleBus.on('HEX_CLICK', this.handleHexClickBound);
        BattleBus.on('HEX_OUT', this.handleHexOutBound);
        BattleBus.on('UNIT_HOVER', this.handleUnitHoverBound);
        BattleBus.on('UNIT_OUT', this.handleUnitOutBound);
        BattleBus.on('UNIT_CLICK', this.handleUnitClickBound);
        BattleBus.on('MOUSE_MOVE', this.handleMouseMoveBound);
        // BattleBus.on('SKILL_EXECUTED', this.skillExecutedBound);   
    }

    public getLastMousePosition(): {x: number; y: number } | null {
        return this.lastMousePosition;
    }

    /**
     * Checks if hover interactions are allowed
     */
    private canHandleHover(): boolean {
        return this.stateMachine.canHandleInput(useBattleStore.getState().status);
    }

    /**
     * Checks if click interactions are allowed
     */
    private canHandleClick(): boolean {
        return this.stateMachine.canHandleInput(useBattleStore.getState().status);
    }

    // --- EVENT HANDLERS ---
        
    private handleHexHover(hexId: string): void {
        if (!this.canHandleHover()) return;
        
        const hex = useBattleStore.getState().battleMap.get(hexId);
        const unitId = hex?.entityId ?? null;
        
        this.highlightManager.updateHoverState(hexId, unitId);
        
        // Custom hex hover logic goes here
        // e.g., show movement path preview, range indicators, etc.
    }

    private handleHexOut(hexId: string): void {
        if (!this.canHandleHover()) return;
        
        this.highlightManager.clearHoverState();
        
        // Custom hex out logic goes here
    }

    private handleUnitHover(unitId: string): void {
        if (!this.canHandleHover()) return;
        
        const unit = useBattleStore.getState().units.get(unitId);
        if (!unit) return;
        const hexId = coordsToId(unit);
        
        this.highlightManager.updateHoverState(hexId, unitId);
        
        // Custom unit hover logic goes here
        // e.g., show unit stats tooltip, attack preview, etc.
    }

    private handleUnitOut(unitId: string): void {
        if (!this.canHandleHover()) return;
        
        this.highlightManager.clearHoverState();
        
        // Custom unit out logic goes here
    }

    /**
     * Track mouse position even when input is blocked
     * Used to restore hover highlights after animations
     */
    private handleMouseMove(data: { x: number; y: number }): void {
        this.lastMousePosition = { x: data.x, y: data.y };
    }

    private handleHexClick(hexId: string): void {
        
        if (!this.canHandleClick()) return;

        const hexVisual = this.entityRegistry.getHex(hexId);

        if (!hexVisual) return;

        const { 
            selectedUnitId, selectedSkillId, selectedHexId, 
            selectedPath, battleMap 
        } = useBattleStore.getState();
        
        if (!selectedUnitId) return;

        const hex = battleMap.get(hexId);
        if (!hex) return;

        // If hex has a unit - delegate to unit click logic
        if (hex.entityId) {
            this.handleUnitClick(hex.entityId);
            return;
        }

        // SKILL MODE: Don't allow movement, skill highlights stay visible
        if (selectedSkillId) {
            // Could add feedback here: "Cancel skill first" or "Invalid target"
            return;
        }

        // MOVEMENT MODE: Handle movement logic
        // this.highlightManager.resetAllHexes(); // Only clear when in movement mode

        if (selectedPath.lastStepId === hex.id) {
            // useBattleStore.getState().resetSelectedPath();
            this.movementExecutor.execute(selectedUnitId, selectedPath);
            return;
        }
        hexVisual.flashHighlight(HEX_HIGHLIGHT_COLOR, 0.1);
        useBattleStore.getState().setSelectedHex(hexId);
        
        // Custom hex click logic goes here
    }

    private handleUnitClick(unitId: string): void {
        if (!this.canHandleClick()) return;
        
        const state = useBattleStore.getState();
        const { 
            selectedUnitId, 
            selectedSkillId, 
        } = state;

        // If unit and skill selected - attempt attack
        if (selectedUnitId && selectedSkillId) {
            this.combatExecutor.execute(selectedUnitId, unitId, selectedSkillId);
            return;
        }
        const currentPool = getActivePool(state);
        
        if (currentPool?.faction === FactionType.Player && currentPool.unitIds.includes(unitId) && !isUnitFinished(state, unitId)) {
            state.setSelectedUnit(unitId);
        }
        
        // Custom unit click logic goes here
    }

    private onKeyDown(e: KeyboardEvent) {
        
        // Number keys 1-9 for skills
        if (e.key >= '1' && e.key <= '9') {
            const skillIndex = parseInt(e.key) - 1;
            this.selectSkillByIndex(skillIndex);
        }

        // ESC to cancel skill
        if (e.key === 'Escape') {
            e.preventDefault();
            const state = useBattleStore.getState();
            
            if (!this.canHandleClick()) return;

            if (state.selectedSkillId) {
                this.skillManager.deselectSkill();
            } else if (state.selectedHexId) {
                state.setSelectedHex(null);
            }
        }
    }

    private selectSkillByIndex(index: number): void {
        const selectedUnitId = useBattleStore.getState().selectedUnitId;
        if (!selectedUnitId) return;
        
        const unit = useBattleStore.getState().units.get(selectedUnitId);
        if (!unit || !unit.skills[index]) return;
        
        const skillId = unit.skills[index].id;
        this.skillManager.toggleSkill(skillId);
        if (this.lastMousePosition) {
            this.highlightManager.restoreHoverHighlightsAfterAnimation(this.lastMousePosition.x, this.lastMousePosition.y, true);
        }
    }
    
    
    destroy(): void {
        window.removeEventListener('keydown', this.onKeyDown);
        // 2.   
        BattleBus.off('HEX_HOVER', this.handleHexHoverBound);
        BattleBus.off('HEX_CLICK', this.handleHexClickBound);
        BattleBus.off('HEX_OUT', this.handleHexOutBound);
        BattleBus.off('UNIT_HOVER', this.handleUnitHoverBound);
        BattleBus.off('UNIT_CLICK', this.handleUnitClickBound);
        BattleBus.off('UNIT_OUT', this.handleUnitOutBound);
        BattleBus.off('MOUSE_MOVE', this.handleMouseMoveBound);
    }
}