import { useState, useEffect } from 'react';
import { useStore } from 'zustand';
import { useBattleStore } from '../../../store/useBattleStore';
import { useGlobalStore } from '../../../store/useGlobalStore';
import { GameScene } from '../../../core/types/global.types';
import { FactionType, BattleStatus } from '../../../core/types/battle.types';
import { InitiativeBar } from './InitiativeBar';
import { UnitActions } from './UnitActions';
import { BottomPanel } from '@/view/ui/battle/BottomPanel/BottomPanel';

export const BattleUI = () => {
    const { status } = useStore(useBattleStore);
    const setScene = useGlobalStore(s => s.setScene);

    //     
    const [showExitButton, setShowExitButton] = useState(false);

    useEffect(() => {
        if (status === BattleStatus.Victory || status === BattleStatus.Defeat) {
            //    
            const timer = setTimeout(() => setShowExitButton(true), 2500);
            return () => clearTimeout(timer);
        } else {
            //          
            setShowExitButton(false);
        }
    }, [status]);

    // --- 1.    ---
    if (status === BattleStatus.Victory || status === BattleStatus.Defeat) {
        return (<>
            <div className="ui-full-overlay">
                {showExitButton && (
                    <button 
                        onClick={() => setScene(GameScene.World)}
                        className="
                            self-end mb-5 mx-auto
                            /*   Pointer Events */
                            pointer-events-auto cursor-pointer
                            
                            /*    */
                            px-12 py-4 text-xl font-medium tracking-widest text-slate-200
                            
                            /*      (   ) */
                            bg-slate-900/60 backdrop-blur-md
                            
                            /* :      */
                            border-2 border-slate-400/40
                            
                            /* :   ,   ,     */
                            rounded-md
                            
                            /* :        */
                            shadow-[0_0_20px_rgba(30,41,59,0.5)]
                            
                            /* :   ,    */
                            hover:bg-slate-800/80
                            hover:border-slate-200
                            hover:text-white
                            hover:shadow-[0_0_25px_rgba(148,163,184,0.3)]
                            
                            /*    */
                            transition-all duration-500
                            active:scale-95
                            animate-fade-in
                        "
                        >
                        {/*   ""  ""   (   ) */}
                        <span className="opacity-40 mr-3"></span>
                        RETURN TO WORLD MAP
                        <span className="opacity-40 ml-3"></span>
                    </button>
                )}
                
            </div>
            <div className="bottom-panel-container">
                <div className="bottom-panel">
                </div>
            </div>
        </>
        );
    }

    // --- 2.     () ---
    if (status !== BattleStatus.Playing && status !== BattleStatus.Preparing) return null;

    return (
        <div className="bottom-panel-container">
            <BottomPanel />
        </div>
    );
};