import * as PIXI from 'pixi.js';
import { Viewport } from 'pixi-viewport';
import { StoreApi } from 'zustand';
import { BattleCameraManager } from '../BattleCameraManager';
import { BattleManager } from '../../core/battle/BattleManager';
import { TooltipManager } from '../../core/battle/TooltipManager';
import { PathRenderer } from '../../view/renderers/PathRenderer';
import { DamageText } from '../../view/renderers/DamageText';

import { HexMapGenerator } from '@/core/battle/HexMapGenerator';
import { BattleHex } from '../renderers/BattleHex';
import { UnitFactory } from '../../core/battle/UnitFactory';
import { UnitStack } from '../renderers/UnitStack';
import { useBattleStore } from '../../store/useBattleStore';
import type { BattleState } from '@/store/useBattleStore';
import { Hex, SkillExecutedEvent, FactionType, BattleStatus } from '../../core/types/battle.types';
import { BattleBus } from '../../events/Events';
import { AnimationController } from '../../core/battle/AnimationController';
import gsap from 'gsap';
import { EntityRegistry } from '@/core/battle/EntityRegistry';
import { HighlightManager } from '@/core/battle/HighlightManager';
import { InputController } from '@/core/battle/InputController';
import { MovementExecutor } from '@/core/battle/MovementExecutor';
import { ControlStateMachine } from '@/core/battle/ControlStateMachine';
import { CombatExecutor } from '@/core/battle/CombatExecutor';
import { TurnManager } from '@/core/battle/TurnManager';
import { coordsToId } from '@/core/utils/hex.utils';
import { generateTurnQueue } from '@/core/utils/battle.utils';
import { SkillManager } from '@/core/battle/SkillManager';
import ManagerRegistry from '@/core/battle/ManagerRegistry';
import { AIStrategicManager } from '@/core/battle/ai/strategic/AIStrategicManager';
import { AISystem } from '@/core/battle/ai/AISystem';
import { AITurnManager } from '@/core/battle/ai/AITurnManager';
import { TurnQueueRenderer } from '@/view/renderers/TurnQueueRenderer';
import { SkillBarRenderer } from '@/view/renderers/SkillBarRenderer';

export class BattleScene extends PIXI.Container {
    private viewport: Viewport;
    private mapLayer = new PIXI.Container();
    private groundLayer = new PIXI.Container();
    private unitLayer = new PIXI.Container();
    private vfxLayer = new PIXI.Container();
    private cameraManager: BattleCameraManager;
    private entityRegistry: EntityRegistry;
    private tooltipManager: TooltipManager;
    private pathRenderer: PathRenderer;
    private turnQueueRenderer: TurnQueueRenderer;
    private skillBarRenderer: SkillBarRenderer;
    private animationController: AnimationController;
    private damageText: DamageText;
    private highlightManager: HighlightManager;
    private controlStateMachine: ControlStateMachine;
    private inputController: InputController;
    private movementExecutor: MovementExecutor;
    private combatExecutor: CombatExecutor;
    private skillManager: SkillManager;
    private aiStrategicManager: AIStrategicManager;
    private aiSystem: AISystem;
    private aiTurnManager: AITurnManager;

    private turnManager: TurnManager;
    private battleManager: BattleManager;
    
    private uiLayer = new PIXI.Container();
    private endText: PIXI.Text | null = null;
    private statusUnsubscribe: (() => void) | null = null;

    constructor(private app: PIXI.Application) {
        super();

        this.app.renderer.on('resize', () => {
            this.cameraManager.updateSize(this.app.screen.width, this.app.screen.height);
            this.positionTurnQueue();
            this.positionSkillBar();
        });

        const battleStore: StoreApi<BattleState> = useBattleStore;
        
        this.viewport = new Viewport({
            screenWidth: this.app.screen.width,
            screenHeight: this.app.screen.height,
            worldWidth: 2000,
            worldHeight: 2000,
            events: this.app.renderer.events
        });

        

        this.addChild(this.viewport);
        this.addChild(this.uiLayer);
        this.viewport.addChild(this.mapLayer);
        this.viewport.addChild(this.groundLayer);
        this.viewport.addChild(this.unitLayer);
        this.viewport.addChild(this.vfxLayer);
        this.unitLayer.sortableChildren = true;
        this.vfxLayer.sortableChildren = true;

        this.entityRegistry = new EntityRegistry();
        this.pathRenderer = new PathRenderer(this.groundLayer);
        
        this.tooltipManager = new TooltipManager();
        this.animationController = new AnimationController();
        
        this.controlStateMachine = new ControlStateMachine();
        this.turnManager = new TurnManager();

        this.highlightManager = new HighlightManager(this.entityRegistry, this.controlStateMachine);

        this.skillManager = new SkillManager(this.controlStateMachine, this.pathRenderer);

        this.turnQueueRenderer = new TurnQueueRenderer({
            portraitSize: 52,

            // colors: { ... } // Optional color overrides
        });

        this.turnQueueRenderer.onLayoutChange = () => {
            this.positionTurnQueue();
        };

        this.skillBarRenderer = new SkillBarRenderer(this.skillManager, this.turnManager, this.controlStateMachine, {
            direction: 'vertical', // 'vertical'   
            gap: 8,
            iconSize: 64
        });

        this.skillBarRenderer.onLayoutChange = () => {
            this.positionSkillBar();
        };

        this.movementExecutor = new MovementExecutor(
            this.animationController, 
            this.pathRenderer, 
            this.entityRegistry,
            this.turnManager,
            this.controlStateMachine,
        );

        this.combatExecutor = new CombatExecutor(
            this.animationController, 
            this.entityRegistry,
            this.turnManager,
            this.controlStateMachine,
        );
        
        this.inputController = new InputController(
            this.highlightManager,
            this.movementExecutor, 
            this.combatExecutor,
            this.entityRegistry,
            this.controlStateMachine,
            this.skillManager,
        );

        // Strategic manager
        this.aiStrategicManager = new AIStrategicManager();
        
        // AI System (planning)
        this.aiSystem = AISystem.create(this.aiStrategicManager);
        
        // AI Turn Manager (execution)
        this.aiTurnManager = new AITurnManager(
            this.aiSystem,
            this.movementExecutor,
            this.combatExecutor,
            this.turnManager,
            this.controlStateMachine
        );

        this.cameraManager = new BattleCameraManager(this.viewport);
        
        this.battleManager = new BattleManager(
            this.pathRenderer, 
            this.inputController,
            this.skillManager,
            this.cameraManager,
            this.highlightManager,
            this.entityRegistry,
            this.controlStateMachine,
            this.turnManager,
            this.aiTurnManager,
        );

        // Register managers for React access
        ManagerRegistry.initialize(
            this.controlStateMachine,
            this.skillManager,
            this.turnManager
        );
        
        
        this.damageText = new DamageText(
            (unitId) => this.entityRegistry.getUnit(unitId)
        );
        this.vfxLayer.addChild(this.damageText);
        this.uiLayer.addChild(this.turnQueueRenderer);
        this.uiLayer.addChild(this.skillBarRenderer);
    }

    public async init() {
        console.log('[BattleScene] Init start');
        
        useBattleStore.getState().reset();
        
        // Create map and units FIRST
        this.initializeBattleMap();
        console.log('[BattleScene] Map initialized');

        await this.initializeUnits();
        console.log('[BattleScene] Units initialized');
        
        const mapBounds = this.mapLayer.getLocalBounds().rectangle;
        this.cameraManager.initConstraints(mapBounds);
        
        this.cameraManager.applyGridMask(this.mapLayer, mapBounds);
        
        await this.loadGameAssets();

        const unitPositions = this.getUnitPositions();
        await this.cameraManager.focusOnBattleCenter(unitPositions);
        this.statusUnsubscribe = useBattleStore.subscribe(
            state => state.status, 
            status => this.showEndGameVisuals(status)
        );
        
        this.initializeBattle();
        // this.positionTurnQueue();
        // this.positionSkillBar();

        this.inputController.initializeHotkeys();
        console.log('[BattleScene] Hotkeys initialized - scene ready');

        this.viewport.on('pointermove', (event) => {
            const globalPos = event.data.global;
            BattleBus.emit('MOUSE_MOVE', { x: globalPos.x, y: globalPos.y });
        });
    }
    

    private initializeBattleMap = () => {
        const mapGenerator = new HexMapGenerator({ size: 24, seed: "battle-of-kyi187" });
        const mapData = mapGenerator.generate();

        useBattleStore.getState().setBattleMap(mapData);

        for (const [id, data] of mapData) {
            const hexView = new BattleHex(data);
            this.mapLayer.addChild(hexView);
            this.entityRegistry.registerHex(id, hexView);
        }
    };

    private async initializeUnits() {
        const units = await Promise.all([
            UnitFactory.createUnit('archer', 16, -2, 10, FactionType.Enemy),
            UnitFactory.createUnit('peasant', 15, -2, 10, FactionType.Enemy),
            UnitFactory.createUnit('swordmen', 15, -3, 10, FactionType.Enemy),
            UnitFactory.createUnit('axewarrior', 15, -1, 10, FactionType.Enemy),
            UnitFactory.createUnit('archer', 7, 3, 10),
            UnitFactory.createUnit('peasant', 8, 1, 10),
            UnitFactory.createUnit('peasant', 8, 4, 10),
            UnitFactory.createUnit('swordmen', 8, 2, 10),
            UnitFactory.createUnit('axewarrior', 8, 3, 10),
        ]);

        units.forEach((unit: UnitStack) => {
            this.unitLayer.addChild(unit);
            this.entityRegistry.registerUnit(unit.unitId, unit);
            useBattleStore.getState().updateHex(coordsToId(unit), {entityId: unit.unitId});
        });

        this.unitLayer.sortChildren();
    };

    private initializeBattle() {
        const state = useBattleStore.getState();
        const unitsArray = Array.from(state.units.values());
        const queue = generateTurnQueue(unitsArray);
        const firstPool = queue[0];
        if (!firstPool) return;
        state.setTurnQueue(queue);
        state.setStatus(BattleStatus.Playing);
        
        state.setActiveFaction(firstPool.faction);
        
        state.setSelectedUnit(firstPool.unitIds[0] || null);
    }

    private getUnitPositions = () => {
        const units = Array.from(useBattleStore.getState().units.values());

        const unitPositions = units.map(u => {
            const unit = this.entityRegistry.getUnit(u.unitId);
            return { x: unit?.x || 0, y: unit?.y || 0 };
        });
        return unitPositions;
    };

    private positionTurnQueue() {
        const padding = 20;
        console.log("Global Y:", this.turnQueueRenderer.getGlobalPosition().y);

        this.turnQueueRenderer.position.set(
            this.app.screen.width - this.turnQueueRenderer.width - padding,
            // this.app.screen.height
            this.app.screen.height - padding
        );
    }

    private positionSkillBar() {
        const padding = 20;

        const h = this.skillBarRenderer.getLayoutHeight();

        this.skillBarRenderer.position.set(
            padding,
            this.app.screen.height - this.skillBarRenderer.height - padding
        );
    }

    public async showEndGameVisuals(status: BattleStatus) {
        if (status === BattleStatus.Playing || status === BattleStatus.Preparing) return;
        
        this.unitLayer.eventMode = 'none';
        this.cameraManager.lock();
        this.tooltipManager.destroy();

        const unitPositions = this.getUnitPositions();

        await Promise.all([
            this.cameraManager.focusOnBattleCenter(unitPositions),
        ]);
    
        const isWin = status === BattleStatus.Victory;
        const textStr = isWin ? "VICTORY" : "DEFEAT";
        const mainColor = isWin ? "#FFD700" : "#FF3333";

        this.viewport.animate({
            scale: 0.5,
            time: 2000,
            ease: 'easeInOutExpo'
        });
        
        await new Promise(resolve => setTimeout(resolve, 1500));

        const endGameStyle = new PIXI.TextStyle({
            fontFamily: 'Arial Black',
            fontSize: 100,
            fill: mainColor,
            stroke: { color: '#000000', width: 10 },
            dropShadow: { color: '#000000', blur: 5, distance: 12 }
        });

        this.endText = new PIXI.Text({ text: textStr, style: endGameStyle });
        this.endText.anchor.set(0.5);
        this.endText.x = this.app.screen.width / 2;
        this.endText.y = this.app.screen.height / 2;
        this.endText.alpha = 0;
        this.endText.scale.set(5);

        this.uiLayer.addChild(this.endText);
        

        const tl = gsap.timeline();

        tl.to(this.endText, {
            alpha: 1,
            scale: 1,
            duration: 1.2,
            ease: "elastic.out(1, 0.75)"
        });
    }

    private async loadGameAssets() {
        await PIXI.Assets.load([
            { alias: 'Cinzel', src: 'assets/fonts/cinzel/cinzel-regular.ttf' },
            { alias: 'CinzelBold', src: 'assets/fonts/cinzel/cinzel-bold.ttf' },
        ]);
    }

    /**
     *   
     */
    public _destroy(options?: PIXI.DestroyOptions) {
        console.warn("[BattleScene]  ...");
        
        // Unsubscribe from status changes
        if (this.statusUnsubscribe) {
            this.statusUnsubscribe();
        }

        // Destroy managers
        if (this.battleManager) {
            this.battleManager.destroy();
        }
        
        if (this.inputController) {
            this.inputController.destroy();
        }
        
        if (this.tooltipManager) {
            this.tooltipManager.destroy();
        }

        // Clear ManagerRegistry
        ManagerRegistry.destroy();

        // Stop animations
        gsap.killTweensOf(this.viewport);
        gsap.killTweensOf(this.viewport.scale);
        if (this.endText) gsap.killTweensOf(this.endText);

        // Destroy viewport
        if (this.viewport) {
            this.viewport.destroy({ children: true });
        }

        // Destroy options
        const destroyOptions: PIXI.DestroyOptions = {
            children: true,
            texture: false,
        };

        if (typeof options === 'object' && options !== null) {
            Object.assign(destroyOptions, options);
        } else if (typeof options === 'boolean') {
            destroyOptions.children = options;
        }

        this.pathRenderer.destroy();
        this.damageText.destroy();

        super.destroy(destroyOptions);

        console.log("[BattleScene] Pixi ' .");
    }

    /**
     *   
     *    ,      .
     */
    public destroy(options?: PIXI.DestroyOptions | boolean) {
        console.warn("[BattleScene]  ...");
        
        // 1.  (,     )
        if (this.statusUnsubscribe) {
            this.statusUnsubscribe();
            this.statusUnsubscribe = null;
        }

        //    PIXI/Viewport
        this.viewport.off('pointermove');
        this.app.renderer.off('resize'); //     renderer

        // 2.    
        // BattleManager    ,   
        this.battleManager?.destroy();
        this.inputController?.destroy();
        this.tooltipManager?.destroy();
        // this.aiTurnManager?.destroy(); //   AI,      
        // this.aiSystem?.destroy();
        // this.turnManager?.destroy();
        // this.skillManager?.destroy();
        // this.cameraManager?.destroy();
        
        //   ,  React-     '
        ManagerRegistry.destroy();

        // 3.  
        //   GSAP , '    
        gsap.killTweensOf(this);
        gsap.killTweensOf(this.viewport);
        gsap.killTweensOf(this.viewport.scale);
        if (this.endText) {
            gsap.killTweensOf(this.endText);
        }
        //  AnimationController (      )
        this.animationController?.cancelAll?.();

        // 4.   
        this.pathRenderer?.destroy();
        this.damageText?.destroy();
        this.turnQueueRenderer?.destroy();
        this.skillBarRenderer?.destroy();

        // 5.    (entityRegistry)
        // ,    UnitStack  BattleHex  
        this.entityRegistry?.destroy?.();

        // 6.   ' PIXI
        //   
        // Destroy options
        const destroyOptions: PIXI.DestroyOptions = {
            children: true,
            texture: false,
        };

        if (typeof options === 'object' && options !== null) {
            Object.assign(destroyOptions, options);
        } else if (typeof options === 'boolean') {
            destroyOptions.children = options;
        }

        //  Viewport  (    destroy)
        if (this.viewport) {
            this.viewport.destroy({ children: true });
        }

        //    PIXI.Container
        super.destroy(destroyOptions);

        console.log("[BattleScene]   .");
    }
}