import { BattleUnit, Hex } from "@/core/types/battle.types";
import { getAxialNeighbors, coordsToId } from "@/core/utils/hex.utils"; //  

export class ZocCalculator {

    /**
     *   Zone of Control.
     * Key: Hex ID ( "2:-1")
     * Value:  Average Damage ,    .
     */
    public static calculate(
        enemies: BattleUnit[], 
        battleMap: Map<string, Hex>
    ): Map<string, number> {
        const zocMap = new Map<string, number>();

        for (const enemy of enemies) {
            if (enemy.isDead) continue;

            // 1.    ( )
            //  ,   ,     
            const avgDmg = (enemy.stats.damageMin + enemy.stats.damageMax) * enemy.count / 2;

            // 2.   (Zone of Control -   adjacent)
            const neighbors = getAxialNeighbors(enemy.q, enemy.r);

            for (const neighborCoords of neighbors) {
                const hexId = coordsToId(neighborCoords);
                const hex = battleMap.get(hexId);

                // ZoC      
                //     !hex.isOccupied,  ,     ZoC
                if (hex && !hex.isObstacle) {
                    const currentThreat = zocMap.get(hexId) || 0;
                    //   (   )
                    zocMap.set(hexId, currentThreat + avgDmg);
                }
            }
        }

        return zocMap;
    }
}