// components/UnitPanel/StaminaCore.tsx
import React, { useState, useEffect } from 'react';
import { BattleBus } from '@/events/Events';
import { StaminaBar } from './StaminaBar';
import './StaminaCore.css';

interface StaminaCoreProps {
    unitName: string;
    unitId: string;  //   unitId  events
    currentStamina: number;
    maxStamina: number;
    previewCost: number;
}

// StaminaCore.tsx
export function StaminaCore({ 
    unitName,
    unitId,
    currentStamina, 
    maxStamina, 
    previewCost 
}: StaminaCoreProps) {
    const [animatedDrain, setAnimatedDrain] = useState(0);
    const [frozenPreview, setFrozenPreview] = useState(0);
    const [predictedStamina, setPredictedStamina] = useState<number | null>(null);
    const [isDraining, setIsDraining] = useState(false); //  NEW
    
    useEffect(() => {
        const handleDrainStart = (data: { unitId: string, amount: number }) => {
            if (data.unitId !== unitId) return;
            

            
            const newStamina = Math.max(0, currentStamina - data.amount);
            
            setPredictedStamina(newStamina);
            setFrozenPreview(previewCost);
            setAnimatedDrain(data.amount);
            setIsDraining(true); //  Mark as draining
            
            setTimeout(() => {
                console.log('[StaminaCore] Drain animation start');
                setAnimatedDrain(0);
            }, 0);
            
            setTimeout(() => {
                console.log('[StaminaCore] Drain complete - unfreezing');
                setFrozenPreview(0);
                setPredictedStamina(null);
                setIsDraining(false); //  Clear draining flag
            }, 900);
        };
        
        BattleBus.on('STAMINA_DRAIN', handleDrainStart);
        return () => BattleBus.off('STAMINA_DRAIN', handleDrainStart);
    }, [unitId, currentStamina, previewCost]);
    
    const effectiveStamina = predictedStamina !== null ? predictedStamina : currentStamina;
    const currentPercent = maxStamina > 0 ? (effectiveStamina / maxStamina) * 100 : 0;
    const drainPercent = maxStamina > 0 ? (animatedDrain / maxStamina) * 100 : 0;
    
    const activePreviewCost = frozenPreview > 0 ? frozenPreview : previewCost;
    const previewPercent = maxStamina > 0 ? (activePreviewCost / maxStamina) * 100 : 0;

    console.log('[StaminaCore] Rendering with:', {
      predictedStamina,
        currentStamina,
        maxStamina,
        animatedDrain,
        effectiveStamina,
        activePreviewCost,
        currentPercent,
        previewPercent,
        drainPercent,
        isDraining
    });
    
    return (
        <div className="unit-name-stamina">
            <div className="unit-name">{unitName.split('_')[0] || '-'}</div>
            
            <div className="stamina-bar-container">
                <StaminaBar
                    currentPercent={currentPercent}
                    drainWidthPercent={drainPercent}
                    previewWidthPercent={previewPercent}
                    isDraining={isDraining}
                    height={20}
                />
            </div>
            
            <div className="stamina-values">
                <span className="current">{effectiveStamina}</span>
                <span className="separator">/</span>
                <span className="max">{maxStamina}</span>
                <span className="preview">{previewCost > 0 ? ` >> ${Math.max(currentStamina - previewCost, 0)}` : ''}</span>
            </div>
        </div>
    );
}