import { Container, Graphics, Text, TextStyle } from 'pixi.js';
import { shallow } from 'zustand/shallow';
import { useBattleStore } from '@/store/useBattleStore';
import { BattleStatus, FactionType, type BattleUnit, type TurnPool } from '@/core/types/battle.types';

type Direction = 'horizontal' | 'vertical';

interface TurnQueueConfig {
    direction: Direction;
    portraitSize: number;
    portraitGap: number;
    poolGap: number;
    colors: {
        player: {
            primary: number;
            accent: number;
        };
        enemy: {
            primary: number;
            accent: number;
        };
        neutral: {
            primary: number;
            accent: number;
        };
        finished: number;
        background: number;
        text: number;
        border: number;
    };
    portraitOpacity: number;
    activePoolGlow: number;
}

const DEFAULT_CONFIG: TurnQueueConfig = {
    direction: 'vertical',
    portraitSize: 64,
    portraitGap: 8,
    poolGap: 12,
    colors: {
        player: {
            primary: 0xb88101,
            accent: 0xdcb14d,
        },
        enemy: {
            primary: 0x8a0d00,
            accent: 0xff8383,
        },
        neutral: {
            primary: 0x4a4a4a,
            accent: 0x2a2a2a,
        },
        finished: 0x808080,
        background: 0x1b1712,
        text: 0xd8c7a0,
        border: 0x8c6a2b
    },
    portraitOpacity: 1.0,
    activePoolGlow: 1.2,
};

interface UnitPortrait {
    container: Container;
    background: Graphics;
    border: Graphics;
    label: Text;
    unitId: string;
}

/**
 * TurnQueueRenderer - Clean FIFO version
 * 
 * - turnQueue[0] = current pool (no currentPoolId needed)
 * - finishedPools separate from active
 * - Portraits merge visually within pools
 */
export class TurnQueueRenderer extends Container {
    private config: TurnQueueConfig;
    private portraits: Map<string, UnitPortrait> = new Map();
    public layoutHeight: number = 0;
    public layoutWidth: number = 0;

    private unsubscribe: (() => void) | null = null;

    public onLayoutChange?: () => void;
    public onUnitClick: ((unitId: string) => void) | null = null;

    constructor(config: Partial<TurnQueueConfig> = {}) {
        super();
        this.config = { ...DEFAULT_CONFIG, ...config };
        this.setupStoreSubscription();
    }

    private setupStoreSubscription(): void {
        this.unsubscribe = useBattleStore.subscribe(
            (state) => [
                state.selectedUnitId,
                state.turnQueue,
                state.finishedPools,
                state.status,
            ] as const,
            ([selectedUnitId, turnQueue, finishedPools, status]) => {
                this.render(selectedUnitId, turnQueue, finishedPools, status);
            },
            { equalityFn: shallow }
        );
    }

    private render(
        selectedUnitId: string | null,
        turnQueue: TurnPool[],
        finishedPools: TurnPool[],
        status: BattleStatus
    ): void {
        this.clearPortraits();
        if (status !== BattleStatus.Playing) return;
        let offset = 0;

        // 1. Render active pools
        turnQueue.forEach((pool, poolIdx) => {
            // Filter out selected unit (shown in dedicated panel)
            const visibleUnits = pool.unitIds.filter(id => id !== selectedUnitId);
            if (visibleUnits.length === 0) return;

            const isCurrentPool = poolIdx === 0; // FIFO: [0] is always current
            const visiblePool: TurnPool = { ...pool, unitIds: visibleUnits };

            const poolSize = this.renderPool(visiblePool, isCurrentPool, false, offset);
            offset += poolSize + this.config.poolGap;
        });

        // 2. Render finished pools (by faction)
        finishedPools.forEach((pool) => {
            const poolSize = this.renderPool(pool, false, true, offset);
            offset += poolSize + this.config.poolGap;
        });

        // Calculate layout
        const finalSize = offset > 0 ? offset - this.config.poolGap : 0;
        this.layoutHeight = this.config.direction === 'vertical' ? finalSize : this.config.portraitSize;
        this.layoutWidth = this.config.direction === 'vertical' ? this.config.portraitSize : finalSize;

        this.onLayoutChange?.();
    }

    /**
     * Render pool as group of merged portraits
     */
    private renderPool(
        pool: TurnPool,
        isActive: boolean,
        isFinished: boolean,
        offset: number
    ): number {
        const { portraitSize, direction } = this.config;
        const unitCount = pool.unitIds.length;

        pool.unitIds.forEach((unitId, index) => {
            const pos = {
                isFirst: index === 0,
                isLast: index === unitCount - 1,
                isSingle: unitCount === 1
            };

            // Determine corners (which should be rounded)
            let corners: [number, number, number, number] = [0, 0, 0, 0]; // [tl, tr, br, bl]
            if (pos.isSingle) {
                corners = [1, 1, 1, 1];
            } else if (direction === 'vertical') {
                if (pos.isFirst) corners = [0, 0, 1, 1]; // Bottom rounded
                else if (pos.isLast) corners = [1, 1, 0, 0]; // Top rounded
            } else {
                if (pos.isFirst) corners = [1, 0, 0, 1]; // Left rounded
                else if (pos.isLast) corners = [0, 1, 1, 0]; // Right rounded
            }

            const portrait = this.createPortrait(
                unitId,
                pool.faction,
                isFinished,
                !isFinished, // isInteractive
                corners,
                isActive,
                pos
            );

            this.portraits.set(unitId, portrait);
            const stepInsidePool = index * portraitSize;

            // Position
            if (direction === 'vertical') {
                portrait.container.x = 0;
                portrait.container.y = -(offset + stepInsidePool + portraitSize);
            } else {
                portrait.container.x = offset + stepInsidePool;
                portrait.container.y = 0;
            }

            this.addChild(portrait.container);
        });

        return unitCount * portraitSize;
    }

    private createPortrait(
        unitId: string,
        faction: FactionType,
        isFinished: boolean,
        isInteractive: boolean,
        corners: [number, number, number, number],
        isActivePool: boolean,
        pos: { isFirst: boolean; isLast: boolean; isSingle: boolean }
    ): UnitPortrait {
        const container = new Container();
        const { portraitSize, portraitGap, direction } = this.config;

        const unit = this.getUnitData(unitId);
        const borderColor = this.getPortraitBorderColor(faction, isActivePool);
        const borderWidth = portraitGap / 2;

        // Apply glow to active pool
        const actualBorderColor = !isFinished
            ? this.brightenColor(borderColor, this.config.activePoolGlow)
            : borderColor;

        // Border with faction color
        const border = new Graphics();
        const borderRadius = 8;

        this.drawComplexRect(
            border,
            portraitSize,
            portraitSize,
            borderRadius,
            corners,
            actualBorderColor,
            pos,
            direction
        );

        container.addChild(border);

        // Background
        const inset = borderWidth;
        const innerSize = portraitSize - inset * 2;
        const innerRadius = Math.max(0, borderRadius - inset);

        const background = new Graphics();
        background.roundRect(inset, inset, innerSize, innerSize, innerRadius);
        background.fill(this.config.colors.background);
        background.stroke({
            width: 1,
            color: this.config.colors.border,
            alpha: 0.8,
            alignment: 0.5
        });
        
        // container.addChild(background);

        // Portrait text
        const portraitContent = this.createPortraitPlaceholder(unitId, unit);
        // container.addChild(portraitContent);

        const portraitContentContainer = new Container();
        portraitContentContainer.addChild(background);
        portraitContentContainer.addChild(portraitContent);

        portraitContentContainer.pivot.set(portraitSize/2, portraitSize/2);
        portraitContentContainer.position.set(portraitSize/2, portraitSize/2);

        container.addChild(portraitContentContainer);

        // Finished state
        if (isFinished) {
            container.alpha = 0.4;
            container.tint = this.config.colors.finished;
        } else {
            container.alpha = this.config.portraitOpacity;
        }

        // Interactivity
        if (isInteractive && !isFinished) {
            container.eventMode = 'static';
            container.cursor = 'pointer';
            container.on('pointerdown', () => this.handlePortraitClick(unitId));

            container.on('pointerover', () => {
                portraitContentContainer.scale.set(1.1);
            });
            container.on('pointerout', () => {
                portraitContentContainer.scale.set(1.0);
            });
        }

        return {
            container,
            background,
            border,
            label: portraitContent,
            unitId,
        };
    }

    private createPortraitPlaceholder(unitId: string, unit: BattleUnit | null): Text {
        const { portraitSize } = this.config;
        const displayText = unit?.unitId.substring(0, 2).toUpperCase() || unitId.charAt(0).toUpperCase();

        const style = new TextStyle({
            fontFamily: 'CinzelBold, sans-serif',
            fontSize: 18,
            fontWeight: 'bold',
            fill: this.config.colors.text,
            stroke: { color: 0x0a0a0a, width: 1 },
        });

        const text = new Text({ text: displayText, style });
        text.anchor.set(0.5);
        text.position.set(portraitSize / 2, portraitSize / 2);

        return text;
    }

    /**
     * Draw complex rect with selective rounded corners and edge extension
     */
    private drawComplexRect(
        g: Graphics,
        w: number,
        h: number,
        radius: number,
        corners: [number, number, number, number], //    (Tuple)
        color: number,
        pos: { isFirst: boolean; isLast: boolean; isSingle: boolean },
        direction: 'horizontal' | 'vertical'
    ): void {
        const extra = 8;
        const offset = extra / 2;

        let startX = 0;
        let startY = 0;
        let drawW = w;
        let drawH = h;

        //    
        if (direction === 'vertical') {
            startX = -offset;
            drawW = w + extra;
            if (pos.isSingle) {
                startY = -offset;
                drawH = h + extra;
            } else if (pos.isFirst) {
                startY = 0;
                drawH = h + offset;
            } else if (pos.isLast) {
                startY = -offset;
                drawH = h + offset;
            }
            // else:   0  h
        } else {
            startY = -offset;
            drawH = h + extra;
            if (pos.isSingle) {
                startX = -offset;
                drawW = w + extra;
            } else if (pos.isFirst) {
                startX = -offset;
                drawW = w + offset;
            } else if (pos.isLast) {
                startX = 0;
                drawW = w + offset;
            }
            // else:   0  w
        }

        //  TS ,    4 
        const [tlR, trR, brR, blR] = corners.map(c => c * radius) as [number, number, number, number];

        g.clear();
        //  
        g.moveTo(startX + tlR, startY);
        g.lineTo(startX + drawW - trR, startY);
        if (trR > 0) g.arcTo(startX + drawW, startY, startX + drawW, startY + trR, trR);
        
        g.lineTo(startX + drawW, startY + drawH - brR);
        if (brR > 0) g.arcTo(startX + drawW, startY + drawH, startX + drawW - brR, startY + drawH, brR);
        
        g.lineTo(startX + blR, startY + drawH);
        if (blR > 0) g.arcTo(startX, startY + drawH, startX, startY + drawH - blR, blR);
        
        g.lineTo(startX, startY + tlR);
        if (tlR > 0) g.arcTo(startX, startY, startX + tlR, startY, tlR);

        g.fill({ color, alpha: 0.65 });
    }

    private getPortraitBorderColor(faction: FactionType, isActive: boolean): number {
        switch (faction) {
            case FactionType.Player:
                return isActive ? this.config.colors.player.accent : this.config.colors.player.primary;
            case FactionType.Enemy:
                return this.config.colors.enemy.primary;
            case FactionType.Neutral:
                return this.config.colors.neutral.primary;
            default:
                return this.config.colors.neutral.primary;
        }
    }

    private brightenColor(color: number, factor: number): number {
        const r = Math.min(255, ((color >> 16) & 0xff) * factor);
        const g = Math.min(255, ((color >> 8) & 0xff) * factor);
        const b = Math.min(255, (color & 0xff) * factor);
        return (r << 16) | (g << 8) | b;
    }

    private getUnitData(unitId: string): BattleUnit | null {
        const state = useBattleStore.getState();
        return state.units.get(unitId) || null;
    }

    private handlePortraitClick(unitId: string): void {
        if (this.onUnitClick) {
            this.onUnitClick(unitId);
        } else {
            console.warn(`TurnQueueRenderer: onUnitClick not assigned. UnitId: ${unitId}`);
        }
    }

    private clearPortraits(): void {
        this.portraits.forEach((portrait) => {
            portrait.container.destroy({ children: true });
        });
        this.portraits.clear();
        this.removeChildren();
    }

    public destroy(options?: any): void {
        if (this.unsubscribe) {
            this.unsubscribe();
            this.unsubscribe = null;
        }
        this.clearPortraits();
        super.destroy(options);
    }
}