import { AIActionType, PlanningContext, ActionCandidate, IActionGenerator, IEvaluator, PlanningState } from "@/core/battle/ai/types/ai.types";
import { Hex } from "@/core/types/battle.types";
import { coordsToId } from "@/core/utils/hex.utils";

/**
 * Generates Wait action
 * Always available as fallback option
 */
export class WaitActionGenerator implements IActionGenerator {
    readonly generatorType = AIActionType.Wait;
    
    /**
     * Generate wait candidate
     * Evaluator determines utility (usually 0 or slightly negative)
     */
    public generate(state: PlanningState, ctx: PlanningContext, evaluator: IEvaluator<string>): ActionCandidate[] {
        const candidates: ActionCandidate[] = [];
        const hexId = coordsToId(state.position)
        // const hex = ctx.battleMap.get(hexId);
        
        // Evaluate staying in current position
        const evalResult = evaluator.evaluate(state, ctx, "wait");
        
        return [{
            action: {
                type: AIActionType.Wait,
                unitId: ctx.unit.unitId,
            },
            utility: evalResult.utility,
            staminaAfter: ctx.unit.currentStamina,
            position: { q: ctx.unit.q, r: ctx.unit.r },
            components: {
                ...evalResult.components,
                total: evalResult.utility,
            },
            metadata: {
                type: 'wait'
            }
        }];
    }
}