import './style.css';
import React from 'react';
import ReactDOM from 'react-dom/client';
import * as PIXI from 'pixi.js';
import { gsap } from "gsap";
import { PixiPlugin } from "gsap/PixiPlugin";
import { MotionPathPlugin } from 'gsap/MotionPathPlugin';
import { GameScene } from '@/core/types/global.types';
import { initBattleBus } from '@/events/Events';
import { BattleScene } from '@/view/scenes/BattleScene';
import { WorldScene } from '@/view/scenes/WorldScene';
import { RootUI } from '@/view/ui/RootUI';



//   GSAP
gsap.registerPlugin(MotionPathPlugin, PixiPlugin);
PixiPlugin.registerPIXI(PIXI);

//   ,    'as any'
interface IGameScene extends PIXI.Container {
    init?(): Promise<void> | void;
    resize?(width: number, height: number): void;
}

//      
let appInstance: PIXI.Application | null = null;
let rootReact: ReactDOM.Root | null = null;

async function initGame() {
    // 1.     (,   ),  
    if (appInstance) return;

    // 2.     (Fix  HMR)
    let appContainer = document.getElementById('app-container');
    let pixiWrapper = document.getElementById('pixi-wrapper');
    let uiRoot = document.getElementById('ui-root');

    if (!appContainer) {
        appContainer = document.createElement('div');
        appContainer.id = 'app-container';
        
        pixiWrapper = document.createElement('div');
        pixiWrapper.id = 'pixi-wrapper';
        
        uiRoot = document.createElement('div');
        uiRoot.id = 'ui-root';

        appContainer.appendChild(pixiWrapper);
        appContainer.appendChild(uiRoot);
        document.body.appendChild(appContainer);
    }

    // 3.  Pixi
    const app = new PIXI.Application();
    appInstance = app; //  

    await app.init({ 
        resizeTo: pixiWrapper as HTMLElement, 
        resolution: window.devicePixelRatio || 1,
        autoDensity: true,
        backgroundColor: 0x050505,
        // antialias: true, //     
    });

    //    (Fix  HMR)
    const oldCanvas = pixiWrapper!.querySelector('canvas');
    if (oldCanvas) oldCanvas.remove();

    pixiWrapper!.appendChild(app.canvas);

    // ========================================================================
    //  
    // ========================================================================

    let currentPixiScene: IGameScene | null = null;

    const syncPixiScene = async (newScene: GameScene) => {
        // A. 
        if (currentPixiScene) {
            console.info(`[Scene] Destroying old scene`);
            // : { children: true } ,    ' 
            currentPixiScene.destroy({ children: true }); 
            app.stage.removeChild(currentPixiScene);
            currentPixiScene = null;
        }

        // B.  :   
        // React   DOM ( useLayoutEffect), 
        //  app.screen      .
        app.resize(); 

        console.log(`[Scene] Init ${newScene}. Screen: ${app.screen.width}x${app.screen.height}`);

        // C. 
        if (newScene === GameScene.Battle) {
            initBattleBus(); 
            currentPixiScene = new BattleScene(app);
        } else {
            currentPixiScene = new WorldScene(app);
        }

        app.stage.addChild(currentPixiScene);

        // D. Init
        if (currentPixiScene.init) {
            await currentPixiScene.init();
        }
    };

    // 4.  React
    //      (HMR),  ,   
    if (!rootReact && uiRoot) {
        rootReact = ReactDOM.createRoot(uiRoot);
    }
    
    if (rootReact) {
        rootReact.render(
            <React.StrictMode>
                {/*     -      */}
                <RootUI onReady={syncPixiScene}/>
            </React.StrictMode>
        );
    }

    //    
    (window as any).app = app; 
}

initGame().catch(console.error);