import * as PIXI from 'pixi.js';
import { gsap } from "gsap";
import { TERRAINS, HEX_SIZE, HEX_HIGHLIGHT_COLOR } from '@/core/constants/battle.constants';
import { Hex } from '@/core/types/battle.types';
import { axialToPixel, coordsToId } from '@/core/utils/hex.utils';
import { BattleBus } from '@/events/Events';

export class BattleHex extends PIXI.Container {
  public readonly data: Hex;
  public readonly id: string;
  private background: PIXI.Graphics;
  private highlightOverlay: PIXI.Graphics; //    
  
  constructor(hexData: Hex) {
    super();

    this.data = hexData;
    this.id = this.data.id;

    const pos = axialToPixel(this.data.q, this.data.r);
    this.x = pos.x;
    this.y = pos.y;

    this.background = new PIXI.Graphics();
    this.highlightOverlay = new PIXI.Graphics();
    
    this.drawBackground(); //  
    this.drawOverlay(); //    ( )

    this.addChild(this.background);
    this.addChild(this.highlightOverlay);

    this.setupInteraction();
  }

  private drawBackground() {
    const def = TERRAINS[this.data.terrain];
    const color = this.data.isCliff ? 0x555555 : parseInt(def.color.replace('#', '0x'));

    this.background.clear();
    //  v8 PixiJS    , 
    //     v7/v8-,  beginFill  
    this.background.lineStyle(1, 0xffffff, 0.1);
    this.background.beginFill(color);

    for (let i = 0; i < 6; i++) {
      const angle = (Math.PI / 180) * (60 * i);
      const vx = HEX_SIZE * Math.cos(angle);
      const vy = HEX_SIZE * Math.sin(angle);
      if (i === 0) this.background.moveTo(vx, vy);
      else this.background.lineTo(vx, vy);
    }

    this.background.closePath();
    this.background.endFill();

    if (this.data.isObstacle) {
      this.background.beginFill(0x000000, 0.2);
      this.background.drawCircle(0, 0, HEX_SIZE * 0.5);
      this.background.endFill();
    }
  }

  private drawOverlay() {
    //     ,   
    //    tint  alpha  '
    this.highlightOverlay.clear();
    this.highlightOverlay.beginFill(0xffffff);
    
    for (let i = 0; i < 6; i++) {
        const angle = (Math.PI / 180) * (60 * i);
        const vx = HEX_SIZE * Math.cos(angle);
        const vy = HEX_SIZE * Math.sin(angle);
        if (i === 0) this.highlightOverlay.moveTo(vx, vy);
        else this.highlightOverlay.lineTo(vx, vy);
    }
    this.highlightOverlay.closePath();
    this.highlightOverlay.endFill();
    
    this.highlightOverlay.alpha = 0; //   
  }

  private setupInteraction() {
    this.eventMode = 'static';
    this.cursor = 'pointer';

    // :    (hitArea)   
    // this.hitArea = this.background.getBounds();

    this.on('pointerover', (e: PIXI.FederatedPointerEvent) => {
      //           
      // console.log(this.id);
      // const nativeEvent = e.nativeEvent;
      // if (document.elementFromPoint(nativeEvent.clientX, nativeEvent.clientY) !== app.view) {
      //     return; //   UI , 
      // }
      BattleBus.emit('HEX_HOVER', { id: this.id, position: { x: e.global.x, y: e.global.y } });
    });

    this.on('pointerout', (e: PIXI.FederatedPointerEvent) => {
      
      BattleBus.emit('HEX_OUT', { id: this.id });
    });

    this.on('pointertap', () => {
      BattleBus.emit('HEX_CLICK', { id: this.id });
    });
  }

  /**
   *     (  BattleManager)
   * @param color    0xRRGGBB  null   
   */  
  public setHighlight(color: number | null, alpha: number = 0.2, scale: number = 1.0) {
    if (color === null) {
      this.highlightOverlay.alpha = 0;
    } else {
      this.highlightOverlay.tint = color;
      this.highlightOverlay.alpha = alpha;
      
      //  . 
      //    drawOverlay   0,0, 
      //     .
      this.highlightOverlay.scale.set(scale);
    }
  }

  public flashHighlight(color: number, duration: number = 1) {
    this.highlightOverlay.tint = color;
    this.highlightOverlay.alpha = 0.2; //   
    
    gsap.to(this.highlightOverlay, {
        alpha: 0.1,
        duration: duration,
        ease: "power2.in",
        onComplete: () => {
            // this.setHighlight(null);
            this.setHighlight(HEX_HIGHLIGHT_COLOR, 0.1);
        }
    });
  }

  /**
   * ,      
   *        
   * @param x -  X 
   * @param y -  Y 
   * @returns true    
   */
  public containsPoint(x: number, y: number): boolean {
    //       
    const localPoint = this.toLocal(new PIXI.Point(x, y));
    
    //    PixiJS     
    return this.background.containsPoint(localPoint);
  }

  public destroy(): void {
    this.removeAllListeners();
    super.destroy({ children: true });
  }
  
}