import { ActionCandidate, PlanningContext } from "@/core/battle/ai/types/ai.types";
import { PLANNING_CONFIG } from "@/core/battle/ai/utils/AIConfig";

/**
 * Adaptive pruning based on utility distribution
 * Keeps top candidates within threshold of best
 */
export class PruningStrategy {
    /**
     * Prune candidates adaptively
     * - Keeps all if 3 candidates
     * - Uses utility threshold based on top 2
     * - More lenient when all utilities are negative
     */
    prune(
        sortedCandidates: ActionCandidate[],
        depth: number,
        context: PlanningContext
    ): ActionCandidate[] {
        const count = sortedCandidates.length;
        
        // Don't prune if too few candidates
        if (count <= 1) {
            return sortedCandidates;
        }
        
        if (count <= 3) {
            return sortedCandidates;
        }
        
        // Calculate threshold from top 2 utilities
        const bestUtility = sortedCandidates[0]!.utility;
        const secondBest = sortedCandidates[1]!.utility;
        const avgTop2 = (bestUtility + secondBest) / 2;
        
        let threshold = avgTop2 * PLANNING_CONFIG.pruneThreshold;
        threshold = Math.min(threshold, secondBest);
        
        // When all options are bad, be more lenient
        if (bestUtility < 0) {
            threshold = Math.min(threshold, bestUtility * 1.5);
        }
        
        // Debug logging at root
        if (depth === 0 && PLANNING_CONFIG.debug) {
            this.logPruning(sortedCandidates, threshold);
        }
        
        return sortedCandidates.filter(c => c.utility >= threshold);
    }
    
    /**
     * Log pruning details for debugging
     */
    private logPruning(
        candidates: ActionCandidate[],
        threshold: number
    ): void {
        const pruned = candidates.filter(c => c.utility >= threshold);
        
        console.log('[Pruning]', {
            before: candidates.length,
            after: pruned.length,
            threshold: threshold.toFixed(2),
            bestUtility: candidates[0]?.utility.toFixed(2),
            worstKept: pruned[pruned.length - 1]?.utility.toFixed(2)
        });
        
        console.log('[Pruning] Top candidates:', 
            candidates.slice(0, 40).map(c => ({
                type: c.action.type,
                subtype: c.metadata?.type,
                target: c.action.type === 'move' 
                    ? (c.action as any).targetHexId 
                    : c.metadata?.targetEnemy,
                utility: c.utility.toFixed(1),
                components: c.components,
            }))
        );
    }
}