import { PlanningContext, EvaluatedCandidate, IEvaluator, PlanningState } from "@/core/battle/ai/types/ai.types";
import { UTILITY_CONSTANTS } from "@/core/battle/ai/utils/AIConfig";
import { CombatCalculator } from "@/core/battle/CombatCalculator";
import { BattleUnit, Skill } from "@/core/types/battle.types";

export class AttackEvaluator implements IEvaluator<BattleUnit> {
    evaluate(
        state: PlanningState,
        ctx: PlanningContext,
        target: BattleUnit,
        skill: Skill,
    ): EvaluatedCandidate {
        const damage = CombatCalculator.calculateSkillRawDamage(
            // { ...ctx.unit, q: state.position.q, r: state.position.r },   //TODO: do we need updated position from state?
            ctx.unit,
            target,
            skill,
            true
        );

        const attackerSkill = Math.max(
            ctx.unit.stats.rangedSkill,
            ctx.unit.stats.meleeSkill
        );
        const hitChance = Math.max(
            5,
            (attackerSkill + (skill.hitChanceBonus || 0)) - target.stats.evasion
        );
        const hitChanceFactor = hitChance / 100;
        const expectedDamage = damage * hitChanceFactor;

        const CV = UTILITY_CONSTANTS.CREATURE_VALUE;
            
        // 1. Base value: creatures killed
        const creaturesKilled = Math.min(
            expectedDamage / target.stats.hp,
            target.count
        );
        const baseDamage = creaturesKilled * CV;
        
        // 2. Apply damage weight
        const weightedDamage = baseDamage * ctx.weights.DAMAGE_WEIGHT;
        
        // 3. Strategic target bonuses
        let targetBonus = 0;
        
        // One-shot kill bonus
        if (creaturesKilled >= target.count) {
            targetBonus += UTILITY_CONSTANTS.ONESHOT_BONUS * CV * ctx.weights.TARGET_VALUE_WEIGHT;
        }
        
        // Target priority based on relative strength
        const targetTotalHP = target.stats.hp * target.count;
        const attackerTotalHP = ctx.unit.stats.hp * ctx.unit.count;
        const relativeStrength = targetTotalHP / attackerTotalHP;
        
        if (relativeStrength > 1.5) {
            targetBonus += UTILITY_CONSTANTS.STRONG_TARGET_BONUS * CV * ctx.weights.TARGET_VALUE_WEIGHT;
        } else if (relativeStrength < 0.5) {
            targetBonus += UTILITY_CONSTANTS.WEAK_TARGET_BONUS * CV * ctx.weights.TARGET_VALUE_WEIGHT;
        } else {
            targetBonus += UTILITY_CONSTANTS.NORMAL_TARGET_BONUS * CV * ctx.weights.TARGET_VALUE_WEIGHT;
        }
        
        // 4. Focus fire bonus (wounded targets)
        let woundedBonus = 0;
        const healthPercent = target.currentUnitHp / target.stats.hp;
        if (healthPercent < 0.5) {
            const woundedFactor = 1 - healthPercent;
            woundedBonus = woundedFactor * UTILITY_CONSTANTS.WOUNDED_BONUS_MAX * CV * ctx.weights.FOCUS_FIRE_BONUS;
        }
        
        const utility = weightedDamage + targetBonus + woundedBonus;
        
        return {
            utility,
            components: {
                baseDamage,
                damageWeight: weightedDamage - baseDamage,
                targetBonus,
                woundedBonus,
                total: utility
            }
        };
    }
}