import { BehaviorAnalyzer } from "@/core/battle/ai/BehaviorAnalyzer";
import { DistanceMatrixCalculator } from "@/core/battle/ai/calculators/DistanceMatrixCalculator";
import { OpportunityRiskCalculator } from "@/core/battle/ai/calculators/OpportunityRiskCalculator";
import { PathsToEnemyCalculator } from "@/core/battle/ai/calculators/PathsToEnemyCalculator";
import { ThreatMapCalculator } from "@/core/battle/ai/calculators/ThreatMapCalculator";
import { ZocCalculator } from "@/core/battle/ai/calculators/ZocCalculator";

import { Goal } from "@/core/battle/ai/strategic/AIGoals";
import { StrategicStance, PlanningContext, PrecomputedHexData, HexDistanceInfo } from "@/core/battle/ai/types/ai.types";
import { getStanceWeights } from "@/core/battle/ai/utils/AIConfig";
import { PathCalculator } from "@/core/battle/PathCalculator";
import { BattleUnit, Hex } from "@/core/types/battle.types";

export class AIContext {

    public static create(
        unit: BattleUnit,
        units: Map<string, BattleUnit>,
        battleMap: Map<string, Hex>,
        stance: StrategicStance,
        goal: Goal | null, 
    ): PlanningContext {
        
        // 1.      (Stance)
        const weights = getStanceWeights(stance);
        
        const behavior = BehaviorAnalyzer.analyzeBehavior(unit);

        const enemies: BattleUnit[] = [];
        const allies: BattleUnit[] = [];
        for (const u of units.values()) {
            if (u.isDead) continue;
            if (u.faction === unit.faction) allies.push(u);
            else enemies.push(u);
        }
        
        const target = enemies.find(e => e.unitId === goal?.targetId);

        const { reachableIds, reachableMap, parentMap } = PathCalculator.getReachableNodes(
            unit, 
            {currentStamina: unit.currentStamina, speed: unit.stats.speed},
            battleMap
        )
        
        const zocMap = ZocCalculator.calculate(enemies, battleMap);

        const { pathHexes, pathsToEnemies } = PathsToEnemyCalculator.calculate(
            unit, 
            unit,
            enemies, 
            reachableMap,
            battleMap,
            zocMap,
        );

        const threatMap = ThreatMapCalculator.calculate(
            reachableMap, 
            enemies, 
            unit, 
            battleMap
        );
        
        // 4.     
        const { distanceMatrix, oppRiskCache } = this.precomputeHexData(
            unit, 
            units, 
            enemies, 
            reachableIds, 
            battleMap
        );

        return {
            unit,
            goal,
            allies,
            enemies,
            target,
            battleMap,
            reachableMap,
            parentMap,
            stance,
            weights,
            behavior,
            threatMap,
            zocMap,
            distanceMatrix,
            oppRiskCache,
            pathHexes,
            pathsToEnemies,
        };
    }

    private static precomputeHexData(
        unit: BattleUnit,
        units: Map<string, BattleUnit>,
        enemies: BattleUnit[],
        reachableHexes: string[],
        battleMap: Map<string, Hex>,
    ): PrecomputedHexData {
        const distanceMatrix = new Map<string, HexDistanceInfo>();
        const oppRiskCache = new Map<string, number>();
        
        // Build distance matrix
        reachableHexes.forEach(hexId => {
            const hexDistanceInfo = DistanceMatrixCalculator.calculate(hexId, enemies);
            const opportunityRisk = OpportunityRiskCalculator.calculate(unit.unitId, units, unit, hexId, battleMap);

            oppRiskCache.set(hexId, opportunityRisk);
            distanceMatrix.set(hexId, hexDistanceInfo);
        });
        
        return { distanceMatrix, oppRiskCache };
    }

    
    
}