import React from 'react';
import type { BattleUnit } from '@/core/types/battle.types';
import './UnitStatsPortrait.css';

interface UnitStatsPortraitProps {
  unit: BattleUnit;
}

/**
 * UnitStatsPortrait - 30% 
 * 
 * Horizontal layout:
 * [Stats (left)] | [Vertical HP Bar (center)] | [Portrait 80x80 (right)]
 */
export function UnitStatsPortrait({ unit }: UnitStatsPortraitProps) {
  const hpPercent = (unit.currentUnitHp / unit.stats.hp) * 100;
  const borderColor = getFactionBorderColor(unit.faction);

  return (
    <div className="unit-stats-portrait">
      {/* Left: Stats column */}
      <div className="stats-column">
        <div className="stat-row">
          <span className="label">Armor</span>
          <span className="value">{unit.stats.armor}</span>
        </div>
        <div className="stat-row">
          <span className="label">Dmg</span>
          <span className="value">{unit.stats.damageMin}-{unit.stats.damageMax}</span>
        </div>
        <div className="stat-row">
          <span className="label">Speed</span>
          <span className="value">{unit.stats.initiative || 10}</span>
        </div>
      </div>

      {/* Center: Vertical HP Bar */}
      <div className="hp-bar-vertical">
        <div className="hp-bar-container-vertical">
          <div 
            className="hp-bar-fill-vertical" 
            style={{ height: `${hpPercent}%` }}
          />
        </div>
        {/* <div className="hp-values">
          <span className="current">{unit.currentUnitHp}</span>
          <span className="separator">/</span>
          <span className="max">{unit.stats.hp}</span>
        </div> */}
      </div>

      {/* Right: Portrait (80x80, centered vertically) */}
      <div className="portrait-wrapper">
        <div 
          className="portrait-border"
          // style={{ borderColor }}
        >
          <div className="portrait-background">
            {/* Placeholder: first letter */}
            {/* TODO: Replace with <img src={unit.portraitPath} /> */}
            <div className="portrait-placeholder">
              {unit.unitId.substring(0, 2).toUpperCase()}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

/**
 * Faction border colors
 */
function getFactionBorderColor(faction: string): string {
  switch (faction) {
    case 'PLAYER':
      return '#4a90e2'; // Blue (placeholder)
    case 'ENEMY':
      return '#e74c3c'; // Red
    case 'NEUTRAL':
      return '#95a5a6'; // Gray
    default:
      return '#95a5a6';
  }
}
