import * as PIXI from 'pixi.js';
import { gsap } from "gsap";

import { OutlineFilter } from 'pixi-filters';

import { FactionType, UnitState, Point } from '@/core/types/battle.types';
import { BattleBus } from '@/events/Events';
import { useBattleStore } from '@/store/useBattleStore';




const MOVEMENT_ANIMATION_DURATION_MULTIPLIER = 0.015;

export class UnitStack extends PIXI.Container {
    public readonly unitId: string;
    public readonly faction: FactionType;
    public readonly config: any;
    public q: number;
    public r: number;

    private visual: PIXI.Container;
    public view: PIXI.Sprite; // made public so AnimationController may optionally access
    private badgeContainer: PIXI.Container;
    private countText: PIXI.Text;
    private badgeBg: PIXI.Graphics;

    private _state: UnitState = UnitState.Idle;
    private unsubStore: () => void;

    private readonly filtersMap = {
        selected: new OutlineFilter(3, 0xffa500),
        hover: new OutlineFilter(2, 0xffffff),
        enemy: new OutlineFilter(2, 0xff0000),
    };

    private isHighlighted: boolean = false; //  BattleManager
    private readonly FORWARD_PX = 22;
    private readonly WINDUP_MS = 170;
    private readonly STRIKE_MS = 65;
    private readonly RECOVER_MS = 240;
    private readonly KNOCKBACK_PX = 18;
    private readonly CRIT_KNOCKBACK_MULT = 2.0;

    private impactPromise: Promise<void> | null = null;
    private resolveImpact: (() => void) | null = null;

    constructor(texture: PIXI.Texture, config: any, id: string, faction: FactionType, q: number, r: number, x: number, y: number) {
        super();

        this.x = x;
        this.y = y;
        this.zIndex = y;
        
        this.unitId = id;
        this.faction = faction;
        this.config = config;
        this.q = q;
        this.r = r;

        // :   ,      
        this.sortableChildren = true;

        // --- VISUAL CONTAINER: wrapper for breathing offset ---
        this.visual = new PIXI.Container();
        this.visual.zIndex = 0;
        this.addChild(this.visual);

        // --- 1.  ---
        this.view = new PIXI.Sprite(texture);
        const ax = config.anchor?.x ?? 0.5;
        const ay = config.anchor?.y ?? 0.7;
        this.view.anchor.set(ax, ay);
        this.view.zIndex = 0; //  0 ()

        if (this.faction === FactionType.Enemy) {
            this.view.scale.x = -1; 
        }

        this.visual.addChild(this.view);

        // --- 3. Hit Area ---
        if (config.hitArea) {
            const adjustedPoints = config.hitArea.map((val: number, index: number) => {
                return (index % 2 === 0) ? val - (256 * ax) : val - (256 * ay);
            });
            this.view.hitArea = new PIXI.Polygon(adjustedPoints);
        }

        // 2.   
        this.badgeContainer = new PIXI.Container();
        this.badgeContainer.zIndex = 10; //  
        
        // :      (    )
        this.badgeContainer.position.set(70, 50); 

        // 2.2 
        this.countText = new PIXI.Text('', {
            fontFamily: 'Georgia, "Times New Roman", serif',
            fontSize: 16,
            fill: 0xEAE1D0,
            fontWeight: 'bold',
            dropShadow: {
                color: 0x000000,
                blur: 2,
                distance: 1,
                angle: Math.PI / 6,
                alpha: 0.5
            },
            align: 'center'
        });
        this.countText.anchor.set(0.4, 0.55); //    
        
        // 2.1  ()
        this.badgeBg = new PIXI.Graphics();
        this.drawBadgeBackground(); //   
        this.badgeContainer.addChild(this.badgeBg);
        this.badgeContainer.addChild(this.countText);

        this.addChild(this.badgeContainer);

        // --- 5.  ---
        this.eventMode = 'static';
        this.cursor = 'pointer';
        this.on('pointerover', this.onPointerOver, this);
        this.on('pointerout', this.onPointerOut, this);
        this.on('pointermove', this.onPointerMove, this);
        this.on('pointertap', this.onPointerTap, this);

        // --- 6. Store Subscription ---
        this.unsubStore = useBattleStore.subscribe((state) => {
            const unitData = state.units.get(this.unitId);
            this.refreshVisuals(unitData);
        });

        // Initial render
        const state = useBattleStore.getState();
        this.refreshVisuals(state.units.get(this.unitId));
    }

    private drawBadgeBackground() {
        const bg = this.badgeBg;
        const padding = 8;
        const textWidth = Math.max(this.countText.width, 10);
        const badgeW = textWidth + padding * 2;
        const badgeH = 22;

        bg.clear();
        bg.beginFill(0x000000, 0.5);
        bg.drawRoundedRect(2, 2, badgeW, badgeH, 6);
        bg.endFill();

        bg.beginFill(0x2b2622);
        bg.lineStyle(1.5, 0x635a4a);
        bg.drawRoundedRect(0, 0, badgeW, badgeH, 5);
        bg.endFill();

        this.countText.position.set(badgeW / 2, badgeH / 2);
    }

    public refreshVisuals(unitData: any): void {
        if (!unitData) {
            console.warn(`No data for unit ${this.unitId}`);
            return; 
        }

        this.countText.text = unitData.count.toString();
        this.drawBadgeBackground();
        this.badgeContainer.visible = unitData.count > 0;

        if (unitData.count <= 0 && this._state !== UnitState.Dead) {
            this._state = UnitState.Dead;
            this.alpha = 0.5;
            this.eventMode = 'none';
            this.view.filters = [];
            return;
        }

        if (this._state === UnitState.Dead) return;

        this.updateFilters();
    }

    // --- Events handlers ---
    private onPointerOver(e: PIXI.FederatedPointerEvent) {
        if (this._state === UnitState.Dead) return;
        BattleBus?.emit('UNIT_HOVER', { id: this.unitId, position: { x: e.global.x, y: e.global.y } });
    }

    private onPointerOut() {
        BattleBus?.emit('UNIT_OUT', { id: this.unitId });
    }

    private onPointerMove(e: PIXI.FederatedPointerEvent) {
        BattleBus?.emit('UNIT_MOUSEMOVE', { position: { x: e.global.x, y: e.global.y } });
    }

    private onPointerTap() {
        if (this._state === UnitState.Dead) return;
        BattleBus?.emit('UNIT_CLICK', { id: this.unitId });
    }

    public updateGridPos(q: number, r: number, x: number, y: number) {
        this.q = q;
        this.r = r;
        this.x = x;
        this.y = y;
        this.zIndex = y; 
    }

    /**
     *    ( BattleManager)
     */
    public setHighlighted(highlighted: boolean): void {
        this.isHighlighted = highlighted;
        this.updateFilters();
    }

    /**
     *      (selected/highlighted)
     *    BattleManager  setHighlighted()
     */
    private updateFilters(): void {
        const state = useBattleStore.getState();
        const isSelected = state.selectedUnitId === this.unitId;
        const isEnemy = this.faction === FactionType.Enemy;
        const activeFilters: PIXI.Filter[] = [];

        if (isSelected) {
            activeFilters.push(isEnemy ? this.filtersMap.enemy : this.filtersMap.selected);
        } else if (this.isHighlighted) {
            activeFilters.push(isEnemy ? this.filtersMap.enemy : this.filtersMap.hover);
        }

        this.view.filters = activeFilters.length > 0 ? activeFilters : null;
    }

    // --------- ANIMATIONS --------------

    

    private triggerImpact() {
        this.resolveImpact?.();
        this.resolveImpact = null;
        this.impactPromise = null;
    }

    public waitForImpact(signal?: AbortSignal): Promise<void> {
        if (!this.impactPromise) {
            this.impactPromise = new Promise<void>((resolve) => {
                this.resolveImpact = resolve;
            });
        }

        if (signal) {
            signal.addEventListener("abort", () => {
                this.resolveImpact?.();
            }, { once: true });
        }

        return this.impactPromise;
    }

    public waitForIdle(signal?: AbortSignal): Promise<void> {
        return new Promise((resolve, reject) => {
            if (signal?.aborted) return reject(new Error("Aborted"));

            const timer = gsap.delayedCall(0.05, resolve);

            signal?.addEventListener('abort', () => {
                timer.kill();
                reject(new Error("Aborted"));
            }, { once: true });
        });
    }
//#TODO: check if async required here
    public prepareForAction(): Promise<void> {
        this.stopIdle();
        return this.resetPose();
    }

    public async finishAction(signal?: AbortSignal): Promise<void> {
        await this.resetPose(); //   
        // this.startIdle();       //  
        return this.waitForIdle(signal); //     
    }

    // === Idle / breathing (synonyms) ===
    public startIdle() {
        // clear previous
        gsap.killTweensOf(this.visual);
        gsap.to(this.visual, {
            y: -8,
            duration: 0.9,
            ease: "sine.inOut",
            yoyo: true,
            repeat: -1,
        });
    }

    public stopIdle() {
        gsap.killTweensOf(this.visual);
        this.visual.y = 0;
    }

    // aliases
    public startBreathing() { this.startIdle(); }
    public stopBreathing() { this.stopIdle(); }

    // === Animation API (promises) ===

    // Helper: normalize vector
    private norm(dx: number, dy: number) {
        const d = Math.hypot(dx, dy) || 1;
        return { x: dx / d, y: dy / d, dist: d };
    }

    public async animateMovement(
        splinePoints: Point[],
    ): Promise<void> {
        return new Promise(resolve => {
            gsap.to(this, {
                duration: splinePoints.length * MOVEMENT_ANIMATION_DURATION_MULTIPLIER,
                ease: "power1.inOut",
                motionPath: { path: splinePoints, autoRotate: false },
                onUpdate: () => { 
                    this.zIndex = Math.floor(this.y); 
                },
                onComplete: resolve
            });
        });
    }

//TODO: no async?
    // Reset pose (do not start breathing automatically)
    public resetPose(durationMs = 220): Promise<void> {
        return new Promise((resolve) => {
            //   
            gsap.killTweensOf([this.visual, this.visual.scale, this.view]);

            //   "  "   
            if (Math.abs(this.visual.x) < 0.01 && this.visual.rotation === 0) {
                this.visual.x = 0;
                this.visual.y = 0;
                this.rotation = 0;
                return resolve();
            }

            gsap.to(this.visual, {
                x: 0,
                y: 0,
                rotation: 0,
                duration: durationMs / 1000,
                ease: "power2.out",
                onComplete: resolve,
                onInterrupt: resolve, // :    
                // onOverwrite: resolve  //   GSAP
            });

            //      
            gsap.to(this.visual.scale, {
                x: 1,
                y: 1,
                duration: durationMs / 1000,
                ease: "power2.out"
            });
        });
    }
    

    // Attacker: play wind-up + strike (visual only). resolves when strike finishes (we keep visual offset until resetPose)
    public playAttackTowards(target: UnitStack): Promise<void> {
        return new Promise((resolve) => {
            gsap.killTweensOf([this.visual, this.visual.scale, this.view]);
            this.stopIdle();

            //  impact promise   
            this.impactPromise = new Promise<void>((res) => {
                this.resolveImpact = res;
            });

            const dx = target.x - this.x;
            const dy = target.y - this.y;
            const len = Math.hypot(dx, dy) || 1;
            const nx = dx / len;

            const forward = this.FORWARD_PX ?? 14;

            const tl = gsap.timeline();

            // tl.to(this.visual, {
            //     pixi: { tint: '#ff3333' }, //  PixiPlugin  GSAP
            //     duration: 0.1,
            //     yoyo: true,
            //     repeat: 1,
            //     onComplete: () => {
            //         this.visual.tint = 0xffffff; //   
            //     }
            // });

            // --- windup ---
            tl.to(this.visual, {
                x: `-=${nx * 6}`,
                y: 2,
                duration: (this.WINDUP_MS ?? 120) / 1000,
                ease: "power3.in"
            });

            // --- STRIKE (  impact) ---
            tl.to(this.visual, {
                x: `+=${nx * forward}`,
                y: -6,
                duration: (this.STRIKE_MS ?? 80) / 1000,
                ease: "expo.out"
            });

            //     
            tl.call(() => {
                this.triggerImpact();   // <<<   IMPACT
            });

            // --- recover ---

            tl.to(this.visual, {
                x: 0,
                y: 0,
                duration: 0.16,
                ease: "elastic.out(1, 0.5)",
                onComplete: () => {
                    this.resetPose(120).then(resolve);
                }
            });

        });
    }


    private flashRed() {
        const filter = new PIXI.ColorMatrixFilter();
        this.visual.filters = [filter];

        //    :
        //      ,   
        filter.matrix = [
            0, 0, 0, 0, 0.5, // Red:   +  0.5 
            0, 0, 0, 0, 0,   // Green:   0
            0, 0, 0, 0, 0,   // Blue:   0
            0, 0, 0, 0.5, 0    // Alpha:   
        ];

        //     100
        setTimeout(() => {
            this.visual.filters = [];
        }, 50);
    }

    public playHitReaction(attacker: UnitStack): Promise<void> {
        return new Promise(async (resolve) => {
            // kill tweens,  idle
            gsap.killTweensOf([this.visual, this.visual.scale, this.view]);
            this.stopIdle();
            
            //  knockback  
            const dx = (this.x ?? 0) - (attacker.x ?? 0);
            const dy = (this.y ?? 0) - (attacker.y ?? 0);
            const len = Math.hypot(dx, dy) || 1;
            const nx = dx / len;
            // const knock = isCrit ? this.KNOCKBACK_PX * this.CRIT_KNOCKBACK_MULT : this.KNOCKBACK_PX;
            const knock = this.KNOCKBACK_PX * 1.6;
            const impactUp = -4;
            const squashX = 0.92;
            const squashY = 1.06;

            const tl = gsap.timeline();

            

            // instant knockback + slight lift + squash
            tl.to(this.visual, {
            x: `+=${nx * knock}`,
            y: `+=${impactUp}`,
            duration: 0.06,
            ease: "power1.out"
            }, 0);

            gsap.to(this.visual, {
                pixi: { tint: '#ff3333' }, //  PixiPlugin  GSAP
                duration: 0.06,
                yoyo: true,
                repeat: 1,
                onComplete: () => {
                    this.visual.tint = 0xffffff; //   
                }
            });

            tl.to(this.visual.scale, {
                x: squashX,
                y: squashY,
                duration: 0.06,
                ease: "power1.out"
            }, 0);

            // recover -   stance
            tl.to(this.visual, {
            x: 0,          //    
            y: 0,
            duration: (this.RECOVER_MS ?? 300) / 1000,
            ease: "elastic.out(1, 0.5)"
            }, "+=0.02");

            tl.to(this.visual.scale, {
            x: 1,
            y: 1,
            duration: (this.RECOVER_MS ?? 300) / 1000,
            ease: "elastic.out(1, 0.5)"
            }, "<");

            tl.to(this.visual, {
                x: 0,
                y: 0,
                duration: 0.16,
                ease: "elastic.out(1, 0.5)",
                onComplete: () => {
                    this.resetPose(120).then(resolve);
                }
            });
        });
    }

    public playCritReaction(attacker: UnitStack): Promise<void> {
        return new Promise(async (resolve) => {
            gsap.killTweensOf([this.visual, this.visual.scale, this.view]);
            this.stopIdle();
            
            const dx = (this.x ?? 0) - (attacker.x ?? 0);
            const dy = (this.y ?? 0) - (attacker.y ?? 0);
            const len = Math.hypot(dx, dy) || 1;
            const nx = dx / len;

            const knockback = 18; //  
            const lift = -10;
            const fall = 16;
            const stunTime = 0.02;

            const tl = gsap.timeline();

            // // 1)  stretch / squash
            // tl.to(this.visual.scale, {
            // x: 1.25,
            // y: 0.75,
            // duration: 0.06,
            // ease: "power2.out"
            // }, 0);

            // 2) sharp lift + knockback
            tl.to(this.visual, {
            x: `+=${nx * knockback}`,
            y: `+=${lift}`,
            duration: 0.09,
            ease: "power3.out"
            }, 0);

            gsap.to(this.visual, {
                pixi: { tint: '#ff3333' },
                duration: stunTime,
                yoyo: true,
                repeat: 1,
                onComplete: () => {
                    this.visual.tint = 0xffffff; //   
                }
            });

            // 3)  stun
            tl.to({}, { duration: stunTime });

            // 4) fall down
            tl.to(this.visual, {
            y: `+=${fall}`,
            duration: 0.12,
            ease: "power2.in"
            });

            // // 5) collapse squash
            // tl.to(this.visual.scale, {
            // x: 0.85,
            // y: 1.2,
            // duration: 0.08,
            // ease: "power2.in"
            // }, "<");

            // 6) recovery back to neutral
            tl.to(this.visual.scale, {
            x: 1,
            y: 1,
            duration: 0.22,
            ease: "elastic.out(1, 0.5)"
            });

            tl.to(this.visual, {
            x: 0,
            y: 0,
            duration: 0.22,
            ease: "elastic.out(1, 0.5)"
            }, "<");

            // :    particles / camera shake   
            // tl.call(() => { this.emitCritParticles(); battleCamera.shake(6, 0.12); }, null, 0.09);

            tl.to(this.visual, {
                x: 0,
                y: 0,
                duration: 0.16,
                ease: "elastic.out(1, 0.5)",
                onComplete: () => {
                    this.resetPose(120).then(resolve);
                }
            });
        });
    }

    public playMissReaction(attacker: UnitStack): Promise<void> {
        return new Promise(async (resolve) => {
            gsap.killTweensOf([this.visual, this.visual.scale, this.view]);
            this.stopIdle();

            const dx = (this.x ?? 0) - (attacker.x ?? 0);
            const dy = (this.y ?? 0) - (attacker.y ?? 0);
            const len = Math.hypot(dx, dy) || 1;
            const nx = dx / len;

            const tl = gsap.timeline();

            //     (subtle)
            tl.to(this.visual, {
            x: `+=${nx * 10}`,
            rotation: nx > 0 ? 0.06 : -0.06,
            duration: 0.1,
            ease: "power2.out"
            });

            //   stance
            tl.to(this.visual, {
            x: 0,
            rotation: 0,
            duration: 0.22,
            ease: "back.out(2)"
            });

            tl.to(this.visual, {
                x: 0,
                y: 0,
                duration: 0.16,
                ease: "elastic.out(1, 0.5)",
                onComplete: () => {
                    this.resetPose(120).then(resolve);
                }
            });
        });
    }


    // Death animation (fall and fade)
    public playDeath(): Promise<void> {
        return new Promise((resolve) => {
            gsap.killTweensOf(this.visual);
            const tl = gsap.timeline({ onComplete: () => resolve() });

            tl.to(this.visual, { y: 20, alpha: 0, duration: 0.45, ease: "power2.in" });
        });
    }

    public destroy(options?: PIXI.DestroyOptions): void {
        if (this.unsubStore) this.unsubStore();
        gsap.killTweensOf(this.visual);
        gsap.killTweensOf(this.view);
        super.destroy(options);
    }
}
