import { type Hex, TerrainType, type MapOptions } from "@/core/types/battle.types";
import { mulberry32, hashSeed } from "@/core/utils/battle.utils";
import { getOffsetNeighbors, coordsToId } from "@/core/utils/hex.utils";

const OBSTACLE_CHANCE: number = 0.035;
const HILL_CHANCE: number = 0.02;

export class HexMapGenerator {
  private map: Map<string, Hex>;
  private rng: () => number;
  private size: number;
  private rows: number;

  constructor(options: MapOptions) {
    const { size, seed } = options;
    
    this.size = size;
    // Calculate row limit based on logic provided
    this.rows = Math.floor(size - (size * 0.4));
    
    // Initialize PRNG
    this.rng = mulberry32(hashSeed(seed));
    this.map = new Map<string, Hex>();
  }

  /**
   * Main public method to generate the map
   */
  public generate(): Map<string, Hex> {
    this.initializeGrid();
    this.applyTerrainFeatures();
    this.promoteCliffs();

    return this.map;
  }

  // =========================================
  // Core Generation Steps
  // =========================================

  private initializeGrid(): void {
    for (let q = 0; q < this.size; q++) {
      // Offset for Axial Rectangle shape
      const qOffset = Math.floor(q / 2);

      for (let r = -qOffset; r < this.rows - qOffset; r++) {
        const id = coordsToId({q, r});

        this.map.set(id, {
          id: id,
          q: q,
          r: r,
          x: 0, // Assuming layout calculations happen elsewhere or x/y are 0 for now
          y: 0,
          terrain: TerrainType.Flat,
          isCliff: false,
          isObstacle: false,
        });
      }
    }
  }

  private applyTerrainFeatures(): void {
    // Iterate over values to avoid modifying the Map while iterating if we were adding keys
    // (though here we only modify properties)
    for (const hex of this.map.values()) {
      this.maybePlaceObstacle(hex);

      // Attempt to grow hills
      if (
        hex.terrain === TerrainType.Flat &&
        this.rng() < this.calculateHillSeedChance(hex.q)
      ) {
        this.growHillPatch(hex.q, hex.r);
      }
    }
  }

  private promoteCliffs(): void {
    for (const hex of this.map.values()) {
      if (hex.terrain !== TerrainType.Hill) continue;

      const neighbors = getOffsetNeighbors(hex.q, hex.r);
      let hillNeighbors = 0;

      for (const nCoord of neighbors) {
        const n = this.getHex(nCoord.q, nCoord.r);
        if (n?.terrain === TerrainType.Hill) hillNeighbors++;
      }

      // If surrounded by hills, small chance to become a cliff
      if (hillNeighbors >= 3 && this.rng() < 0.15) {
        hex.terrain = TerrainType.Cliff;
        hex.isCliff = true;
      }
    }
  }

  // =========================================
  // Terrain Logic Helpers
  // =========================================

  private maybePlaceObstacle(hex: Hex): void {
    if (this.rng() < OBSTACLE_CHANCE) {
      hex.terrain = TerrainType.Obstacle;
      hex.isObstacle = true;
    }
  }

  private calculateHillSeedChance(q: number): number {
    
    const centerQ = (this.size - 1) / 2;
    const dx = Math.abs(q - centerQ) / centerQ;
    
    // HILL_CHANCE    (, 0.1  10%)
    // (HILL_CHANCE + 0.02)      
    const hillChance = HILL_CHANCE + dx * 0.02;

    return HILL_CHANCE + (dx * 0.02); 
}

  private growHillPatch(startQ: number, startR: number): void {
    const stack = [{ q: startQ, r: startR, strength: 1 }];

    while (stack.length) {
      const cur = stack.pop()!;
      const hex = this.getHex(cur.q, cur.r);

      // If hex missing or already modified (Obstacle/Hill), skip
      if (!hex || hex.terrain !== TerrainType.Flat) continue;

      hex.terrain = TerrainType.Hill;

      const neighborsCoords = getOffsetNeighbors(cur.q, cur.r);

      for (const coords of neighborsCoords) {
        const neighbor = this.getHex(coords.q, coords.r);

        if (neighbor && neighbor.terrain === TerrainType.Flat) {
          // Decay logic
          if (this.rng() < 0.25 * cur.strength) {
            stack.push({
              q: coords.q,
              r: coords.r,
              strength: cur.strength * 0.5,
            });
          }
        }
      }
    }
  }

  private getHex(q: number, r: number): Hex | undefined {
    return this.map.get(coordsToId({q, r}));
  }
}
