import { PlanningState, PlanningContext, ActionCandidate, AIActionType, IActionGenerator, IEvaluator } from "@/core/battle/ai/types/ai.types";
import { BattleUnit } from "@/core/types/battle.types";
import { getHexDistance } from "@/core/utils/hex.utils";

export class AttackActionGenerator implements IActionGenerator {
    // constructor(private evaluator: AttackEvaluator) {}
    public generatorType: AIActionType = AIActionType.Attack;

    public generate(state: PlanningState, ctx: PlanningContext, evaluator: IEvaluator<BattleUnit>): ActionCandidate[] {
        const candidates: ActionCandidate[] = [];
        // 1.      
        // 2.    utility  evaluator
        // 3.   
        for (const target of ctx.enemies) {
            for (const skill of ctx.unit.skills) {
                const skillCost = skill.cost
                if (state.stamina < skillCost) continue;

                const distance = getHexDistance(state.position, target);
                if (distance > skill.range) continue;

                

                // const attackEval = evaluateAttack(
                //     expectedDamage,
                //     target,
                //     attacker,
                //     weights,
                //     enemies
                // );
                const evalResult = evaluator.evaluate(state, ctx, target, skill);

                candidates.push({
                    action: {
                        type: AIActionType.Attack,
                        unitId: ctx.unit.unitId,
                        targetUnitId: target.unitId,
                        skillId: skill.id,
                    },
                    utility: evalResult.utility,
                    staminaAfter: state.stamina - skillCost,
                    position: state.position,
                    components: evalResult.components,
                    metadata: {
                        type: 'attack',
                        targetEnemy: target.unitId,
                    },
                });
            }
        }
        return candidates;
    }
}