interface StaminaBarProps {
  currentPercent: number;
  drainStartPercent?: number;
  drainWidthPercent?: number;
  previewWidthPercent?: number;
  isDraining?: boolean;
  height?: number; //  ,    12
}

export const StaminaBar: React.FC<StaminaBarProps> = ({
  currentPercent,
  drainStartPercent = 0,
  drainWidthPercent = 0,
  previewWidthPercent = 0,
  isDraining = false,
  height = 12, //   
}) => {
  const fill = Math.max(0, Math.min(100, currentPercent));
  let drainW: number;
  let solidW: number;
  if (isDraining) {
    
    drainW = Math.max(0, Math.min(fill, drainWidthPercent)); //        
    solidW = fill;
  } else {
    drainW = Math.max(0, Math.min(fill, previewWidthPercent));
    
    solidW = fill - drainW;
  }
  
  
  //    ,   
  const drainX = solidW;

  console.log('[StaminaCore] Rendering with-:', {
        currentPercent,
        drainWidthPercent,
        previewWidthPercent,
        solidW,
        fill,
        drainX,
        drainW,

    });

  return (
    <svg 
      className="stamina-svg" 
      /* viewBox     */
      viewBox={`0 0 100 ${height}`} 
      preserveAspectRatio="none"
      /*      '100%',   , 
              */
      style={{ display: 'block', width: '100%', height: '100%' }}
    >
      <defs>
        <clipPath id="barRadius">
            {/*       */}
            <rect x="0" y="0" width="100" height={height} rx="1" ry="1" />
        </clipPath>
        
        {/*    ,     % */}
        <linearGradient id="staminaVert" x1="0%" y1="0%" x2="0%" y2="100%">
            <stop offset="0%" stopColor="#e0b85a"/>
            <stop offset="50%" stopColor="#b8862a"/>
            <stop offset="100%" stopColor="#e0b85a"/>
        </linearGradient>
        
        <linearGradient id="shadeGrad" x1="0%" y1="0%" x2="0%" y2="100%">
          <stop offset="0%" stopColor="rgba(255,255,255,0.15)"/>
          <stop offset="50%" stopColor="rgba(0,0,0,0)"/>
          <stop offset="100%" stopColor="rgba(255,255,255,0.15)"/>
        </linearGradient>
      </defs>

      <g clipPath="url(#barRadius)">
        <rect width="100" height={height} fill="#151510"/>

        {/* 1.   (   ,  ) */}
        <rect width={fill} height={height} fill="url(#staminaVert)" opacity="0.08" />

        {/* 2.   ( ) */}
        <rect 
            x="0" 
            width={solidW + 0.1} //  ,    
            height={height} 
            fill="url(#staminaVert)" 
        />

        {/* 3. DRAIN ( ,  ) */}
        {/* {drainW > 0 && (
            <rect
              x={drainX}
              width={drainW}
              height={height}
              fill="url(#staminaVert)"
              className="stamina-drain"
            />
        )} */}
        <rect
            x={drainX}
            width={drainW}
            height={height}
            fill="url(#staminaVert)"
            className="stamina-drain"
            style={{ 
                transition: isDraining ? 'width 0.4s ease-out' : '',
                // filter: 'brightness(1.3)' 
            }}
        />

        <rect width="100" height={height} fill="url(#shadeGrad)" pointerEvents="none" />
      </g>
    </svg>
  );
};