// ---   (  ) ---

export const FactionType = {
    Player: 'player',
    Enemy: 'enemy',
    Neutral: 'neutral',
} as const;
export type FactionType = (typeof FactionType)[keyof typeof FactionType];

export const UnitState = {
    Idle: 'idle',
    Moving: 'moving',
    Attacking: 'attacking',
    Dead: 'dead',
} as const;
export type UnitState = (typeof UnitState)[keyof typeof UnitState];


// ---  () ---

export const TerrainType = {
    Obstacle: 'obstacle',
    Flat: 'flat',
    Hill: 'hill',
    Cliff: 'cliff',
} as const;
export type TerrainType = (typeof TerrainType)[keyof typeof TerrainType];

export interface TerrainDef {
  id: TerrainType;
  name: string;
  // visuals
  color: string;
  symbol?: string;
  // generation
  canPatch: boolean;
  canBeSeed: boolean;
  // movement & combat
  moveCost: number;
  defenseBonus: number;
  attackBonus?: number;
  // rules
  blocksMovement?: boolean;
  blocksLineOfSight?: boolean;
}



export const ControlState = {
    Idle: 'idle',
    PlayerMoving: 'player_moving',
    PlayerAttacking: 'player_attacking',
    EnemyTurn: 'enemy_turn',
    SkillTargeting: 'skill_targeting',
    Initializing: 'initializing',
    Ended: 'ended',
} as const;
export type ControlState = (typeof ControlState)[keyof typeof ControlState];

export const BattleStatus = {
    Preparing: 'preparing',
    Playing: 'playing',
    Victory: 'victory',
    Defeat: 'defeat'
} as const;
export type BattleStatus = (typeof BattleStatus)[keyof typeof BattleStatus];



export interface FactionDef {
  color: number;
}

export interface Hex {
  id: string;
  //  
  q: number;
  r: number;
  
  //   ( )
  //     
  x: number;
  y: number;

  //  
  terrain: TerrainType;

  //  
  isCliff: boolean;    //  ( )
  isObstacle: boolean; //    / (   rng)
  
  //   ',     ()
  entityId?: string;   // ID ,     
};

export interface Point { 
  x: number, 
  y: number 
}

export type MapOptions = {
  size: number;
  seed: string;
};

export interface Skill {
  id: string;          //  ID: "shot", "double_shot"
  name: string;        //  
  sort: number;        //     (10, 20...)
  cost: number;        //   
  range: number;       //  (1 =  , >1 = )
  
  powerMult: number;       //  (   )
  hitChanceBonus: number;   //   (  ',  -15)
  armorPenetration: number;
  radius?: number;      //   (0 =  , 1+ = AoE)
  
  //    
  description?: string; 
  icon?: string;       //     
  iconCol?: number;       //     
  iconRow?: number;       //     
}

export interface UnitStats {
  hp: number;
  stamina: number;
  speed: number;
  armor: number;
  evasion: number;
  meleeSkill: number;
  rangedSkill: number;
  initiative: number;
  critChance: number;
  damageMin: number;
  damageMax: number;
}

//units
export interface UnitJsonConfig {
    name: string;
    stats: UnitStats;
    skills: Skill[]; //    
    anchor: { x: number; y: number };
    hitArea: number[];
};

export interface BattleUnit {
    unitId: string;      //  ID     
    unitType: string;      //     JSON (, 'Archer')
    count: number;         //    
    currentUnitHp: number; // HP ""    ( unitMaxHp  0)
    currentStamina: number;
    q: number;
    r: number;
    isDead: boolean;
    faction: FactionType;
    stats: UnitJsonConfig['stats']; //      
    skills: UnitJsonConfig['skills']; 
    opportunityAttackUsed?: boolean; // Tracks if unit used opportunity attack this round
};

export type TurnPool = {
    id?: string;
    faction: FactionType;
    unitIds: string[];
    deferredUnitIds?: Set<string>;
};


export interface CombatResult {
    targetId: string;
    damage: number;
    isCrit: boolean;
    isMiss: boolean;
    finalDefenderHp: number;
    finalDefenderCount: number;
    isDead: boolean;
    details: string; //    
    status?: BattleStatus
}

export interface SkillExecutedEvent {
    attackerId: string;
    targetId: string;
    skillId?: string;
    result: CombatResult;
}

export interface ReachableNodesResult {
  reachableIds: string[];
  visitedMap: Map<string, number>;
}

export interface PathInfo {
  path: string[];
  lastStepId: string | null;
  cost: number;
}
