import { Container, Graphics, Text, TextStyle, Texture, Rectangle, Sprite, Assets } from 'pixi.js';
import { shallow } from 'zustand/shallow';
import { useBattleStore } from '@/store/useBattleStore';
import { ControlState, type Skill } from '@/core/types/battle.types';
import { SkillManager } from '@/core/battle/SkillManager';
import { TurnManager } from '@/core/battle/TurnManager';
import { ControlStateMachine } from '@/core/battle/ControlStateMachine';

interface SkillBarConfig {
    iconSize: number;
    gap: number;
    borderWidth: number;
    direction: 'horizontal' | 'vertical'; //   
    colors: {
        borderDefault: number;
        borderSelected: number;
        borderDisabled: number;
        background: number;
        text: number;
    };
    assets: {
        atlasUrl: string; //   - 
        iconSizeInAtlas: number; //      (48px  React )
    };
}

const DEFAULT_CONFIG: SkillBarConfig = {
    iconSize: 56, //    
    gap: 12,
    borderWidth: 3,
    direction: 'horizontal', //    (  React)
    colors: {
        borderDefault: 0x8c6a2b,     //  
        borderSelected: 0xd4a63a,    //  
        borderDisabled: 0x2a1f16,    //  
        background: 0x1b1712,        //  /
        text: 0xd8c7a0,              //  
    },
    assets: {
        atlasUrl: '/assets/skills.png',
        iconSizeInAtlas: 64,
    }
};

interface SkillIcon {
    container: Container;
    background: Graphics;
    sprite: Sprite | Graphics; //   
    border: Graphics;
    skillId: string;
}

export class SkillBarRenderer extends Container {
    private config: SkillBarConfig;
    private icons: Map<string, SkillIcon> = new Map();
    private unsubscribe: (() => void) | null = null;
    private skillAtlasTexture: Texture | null = null;
    private layoutHeight = 0;


    constructor(
        private skillManager: SkillManager, 
        private turnManager: TurnManager, 
        private stateMachine: ControlStateMachine, 
        config: Partial<SkillBarConfig> = {}
    ) {
        super();
        this.config = { ...DEFAULT_CONFIG, ...config };
        
        //   ,      ,   
        this.loadAssets(); 
        this.setupStoreSubscription();
    }

    public getLayoutHeight() { return this.layoutHeight; }
    public onLayoutChange?: () => void;

    private async loadAssets() {
        try {
            //        Scene.init()
            //  ,    ,   -   
            if (Assets.cache.has(this.config.assets.atlasUrl)) {
                this.skillAtlasTexture = Assets.get(this.config.assets.atlasUrl);
            } else {
                //   -  ()
                this.skillAtlasTexture = await Assets.load(this.config.assets.atlasUrl);
            }
            //     
            this.refreshRender();
        } catch (e) {
            console.warn('SkillBarRenderer: Could not load skill atlas', e);
        }
    }

    private setupStoreSubscription(): void {
        this.stateMachine.onStateChange((from, to) => {
            if (from === ControlState.PlayerMoving || from === ControlState.PlayerAttacking || from === ControlState.EnemyTurn || to === ControlState.EnemyTurn) {

                const { selectedUnitId, selectedSkillId } = useBattleStore.getState();
                // const unit = selectedUnitId ? units.get(selectedUnitId) || null : null;
                this.render(selectedUnitId, selectedSkillId);
            }
        });
        this.unsubscribe = useBattleStore.subscribe(
            (state) => [
                state.selectedUnitId,
                state.selectedSkillId,
            ] as const,
            ([selectedUnitId, selectedSkillId]) => {

                this.render(selectedUnitId, selectedSkillId);
            },
            { equalityFn: shallow }
        );
    }

    //        
    private refreshRender() {
        const state = useBattleStore.getState();
        // const unit = state.selectedUnitId ? state.units.get(state.selectedUnitId) || null : null;
        this.render(state.selectedUnitId, state.selectedSkillId);
    }

    private render(
        unitId: string | null, //   BattleUnit
        selectedSkillId: string | null
    ): void {
        //         (), 
        if (!unitId || this.stateMachine.is(ControlState.EnemyTurn)) {
            this.clearIcons();
            return;
        }

        const unit = useBattleStore.getState().units.get(unitId);
        if (!unit) return;

        this.clearIcons();

        let offset = 0;
        const { gap, iconSize, direction } = this.config;

        // 1.  
        const sortedSkills = [...unit.skills].sort((a: Skill, b: Skill) => a.sort - b.sort);
        sortedSkills.forEach((skill: Skill) => {
            const canSelect = unit.currentStamina >= skill.cost;
            const isSelected = skill.id === selectedSkillId;
            const icon = this.createSkillIcon(skill, isSelected, canSelect);
            
            if (direction === 'vertical') {
                icon.container.position.set(0, offset);
            } else {
                icon.container.position.set(offset, 0);
            }

            this.addChild(icon.container);
            offset += iconSize + gap;
        });

        // 2.      
        offset += gap * 0.5;

        // 3.   
        const skipBtn = this.createSkipButton(unit.unitId);
        const endTurnBtn = this.createEndTurnButton(unit.unitId);
        
        this.addChild(skipBtn);
        this.addChild(endTurnBtn);

        if (direction === 'vertical') {
            // SKIP    offset
            skipBtn.position.set(0, offset);
            
            // END   (   )
            //    +  gap
            endTurnBtn.position.set(iconSize + gap, offset);
            
            this.layoutHeight = offset + (iconSize * 0.8);
        } else {
            //          
            skipBtn.position.set(offset, 0);
            
            const secondOffset = offset + iconSize + gap;
            endTurnBtn.position.set(secondOffset, 0);
            
            this.layoutHeight = iconSize;
        }

        this.onLayoutChange?.();
        

        // //     ()
        // const totalSize = offset - gap;
        // this.pivot.x = direction === 'horizontal' ? totalSize / 2 : 0;
    }

    private createSkillIcon(skill: Skill, isSelected: boolean, isEnabled: boolean): SkillIcon {
        const container = new Container();
        const { iconSize, borderWidth, colors } = this.config;

        // --- 1. Background ---
        const background = new Graphics();
        background.roundRect(0, 0, iconSize, iconSize, 6);
        background.fill(colors.background);

        //    = 
        // background.rect(0, iconSize * 0.55, iconSize, iconSize * 0.45);
        // background.fill({ color: 0x000000, alpha: 0.25 });

        // container.addChild(background);

        // --- 2. Sprite / Content ---
        let content: Sprite | Graphics | Text;
        
        if (this.skillAtlasTexture) {
            //     
            // React: backgroundPosition: -${row * 48}px -${col * 48}px
            const atlasSize = this.config.assets.iconSizeInAtlas;
            const col = skill.iconCol || 0; //   React  row/col,    ,  mapping
            const row = skill.iconRow || 0;

            const frame = new Rectangle(
                col * atlasSize, 
                row * atlasSize, 
                atlasSize, 
                atlasSize
            );
            
            //      (  )
            const texture = new Texture({
                source: this.skillAtlasTexture.source,
                frame: frame
            });

            const sprite = new Sprite(texture);
            sprite.width = iconSize - borderWidth * 2;
            sprite.height = iconSize - borderWidth * 2;
            sprite.position.set(borderWidth, borderWidth);

            content = sprite;
            
            //     -  
            if (!isEnabled && content instanceof Sprite) {
                content.tint = 0x777066;
                content.alpha = 0.6;
            }
        } else {
            // Fallback ()
            const style = new TextStyle({
                fontFamily: 'Arial',
                fontSize: 24,
                fontWeight: 'bold',
                fill: isEnabled ? colors.text : 0x888888,
            });
            content = new Text({ text: skill.name[0], style });
            content.anchor.set(0.5);
            content.position.set(iconSize / 2, iconSize / 2);
        }
        
        // container.addChild(content);

        // --- 3. Border ---
        const border = new Graphics();
        const borderColor = isSelected 
            ? colors.borderSelected 
            : (isEnabled ? colors.borderDefault : colors.borderDisabled);
            
        border.roundRect(0, 0, iconSize, iconSize, 6);
        border.stroke({ 
            width: borderWidth,
            color: borderColor,
            alpha: isEnabled ? 1 : 0.6
        });
        border.stroke({ width: borderWidth, color: 0x8c6a2b, alpha: 0.9 });

        //  
        // border.roundRect(1, 1, iconSize - 2, iconSize - 2, 6);
        // border.stroke({ width: 1, color: 0x000000, alpha: 0.4 });
        
        //   -     
        if (isSelected) {
            const shine = new Graphics();
            shine.roundRect(2, 2, iconSize - 4, iconSize - 4, 5);
            shine.stroke({ width: 2, color: colors.borderSelected, alpha: 0.9 });
            container.addChild(shine);
        }

        // container.addChild(border);
        const iconContainer = new Container();

        //  background, sprite, border  iconContainer
        iconContainer.addChild(background);
        iconContainer.addChild(content);
        iconContainer.addChild(border);

        // pivot   
        iconContainer.pivot.set(iconSize/2, iconSize/2);

        //   iconContainer   container
        iconContainer.position.set(iconSize/2, iconSize/2);

        //    
        container.addChild(iconContainer);

        // --- 4. Interactivity ---
        container.eventMode = 'static';
        container.cursor = isEnabled ? 'pointer' : 'not-allowed';


        if (isEnabled) {
            container.on('pointerdown', () => {this.handleSkillClick(skill.id)});
            
            container.on('pointerover', (e) => {
                if(!isSelected) iconContainer.scale.set(1.1); //  
                
            });

            container.on('pointerout', () => {
                iconContainer.scale.set(1.0);
                
            });
        
        }

        const skillIcon: SkillIcon = {
            container,
            background,
            sprite: content as any,
            border,
            skillId: skill.id
        };

        this.icons.set(skill.id, skillIcon);
        return skillIcon;
    }

    private createSkipButton(unitId: string): Container {
        const { iconSize, borderWidth, colors } = this.config;
        const container = new Container();
        const height = iconSize * 0.8;
        const width = iconSize;
        const bg = new Graphics();
        bg.roundRect(0, 0, width, height, 6);
        bg.fill(0x2a2016); //  
        container.addChild(bg);

        const border = new Graphics();
        border.roundRect(0, 0, width, height, 6);
        border.stroke({ width: borderWidth, color: 0x8c6a2b, alpha: 0.9 });
        container.addChild(border);

        const style = new TextStyle({
            fontFamily: 'CinzelBold',
            fontSize: 14,
            fontWeight: 'bold',
            fill: 0xd4a63a,
            align: 'center'
        });

        const text = new Text({ text: 'SKIP', style });
        text.anchor.set(0.5);
        text.position.set(iconSize / 2 , height / 2);
        container.addChild(text);

        container.eventMode = 'static';
        container.cursor = 'pointer';

        container.on('pointerdown', () => {this.handleSkipClick(unitId)});

        return container;
    }

    private createEndTurnButton(unitId: string): Container {
        const { iconSize, borderWidth, colors } = this.config;
        const container = new Container();
        const height = iconSize * 0.8;
        const width = iconSize;
        const bg = new Graphics();
        bg.roundRect(0, 0, width, height, 6);
        bg.fill(0x2a2016); //  
        container.addChild(bg);

        const border = new Graphics();
        border.roundRect(0, 0, width, height, 6);
        border.stroke({ width: borderWidth, color: 0x8c6a2b, alpha: 0.9 });
        container.addChild(border);

        const style = new TextStyle({
            fontFamily: 'CinzelBold',
            fontSize: 14,
            fontWeight: 'bold',
            fill: 0xd4a63a,
            align: 'center'
        });

        const text = new Text({ text: 'END', style });
        text.anchor.set(0.5);
        text.position.set(iconSize / 2 , height / 2);
        container.addChild(text);

        container.eventMode = 'static';
        container.cursor = 'pointer';

        container.on('pointerdown', () => {this.handleEndTurnClick(unitId)});

        return container;
    }


    private handleSkillClick(skillId: string): void {
        
        if (useBattleStore.getState().selectedSkillId === skillId) {
            this.skillManager.deselectSkill();
        } else {
            this.skillManager.selectSkill(skillId);
        }
    }

    private handleSkipClick(unitId: string): void {
        if (!this.stateMachine.canHandleInput(useBattleStore.getState().status)) return;
        this.turnManager.deferUnit(unitId); 
    }

    private handleEndTurnClick(unitId: string): void {
        if (!this.stateMachine.canHandleInput(useBattleStore.getState().status)) return;
        this.turnManager.endUnitTurn(unitId); 
    }

    private clearIcons(): void {
        this.icons.forEach((icon) => {
            icon.container.destroy({ children: true });
        });
        this.icons.clear();
        this.removeChildren();
    }

    public destroy(options?: any): void {
        if (this.unsubscribe) {
            this.unsubscribe();
        }
        this.clearIcons();
        super.destroy(options);
    }
}