// core/battle/ManagerRegistry.ts

import { ControlStateMachine } from "@/core/battle/ControlStateMachine";
import { SkillManager } from "@/core/battle/SkillManager";
import { TurnManager } from "@/core/battle/TurnManager";

/**
 * Singleton registry  battle managers
 *     BattleScene,  
 */
class ManagerRegistry {
    private static instance: ManagerRegistry | null = null;
    
    public skillManager!: SkillManager;
    public stateMachine!: ControlStateMachine;
    public turnManager!: TurnManager;
    
    private constructor() {
        // private -    
    }
    
    /**
     *   (  BattleScene)
     */
    static initialize(
        stateMachine: ControlStateMachine,
        skillManager: SkillManager,
        turnManager: TurnManager
    ): void {
        if (!ManagerRegistry.instance) {
            ManagerRegistry.instance = new ManagerRegistry();
        }
        
        const registry = ManagerRegistry.instance;
        registry.stateMachine = stateMachine;
        registry.skillManager = skillManager;
        registry.turnManager = turnManager;
        
        console.log('[ManagerRegistry] Initialized');
    }
    
    /**
     *   
     */
    static getInstance(): ManagerRegistry {
        if (!ManagerRegistry.instance) {
            throw new Error('ManagerRegistry not initialized. Create BattleScene first.');
        }
        return ManagerRegistry.instance;
    }
    
    /**
     *   ( cleanup)
     */
    static destroy(): void {
        ManagerRegistry.instance = null;
        console.log('[ManagerRegistry] Destroyed');
    }
    
    /**
     *   
     */
    static isInitialized(): boolean {
        return ManagerRegistry.instance !== null;
    }
}

export default ManagerRegistry;