import { HEX_DIRECTIONS, TERRAINS } from "@/core/constants/battle.constants";
import { Hex, PathInfo } from "@/core/types/battle.types";
import { coordsToId, getAxialNeighbors, idToCoords, getHexDistance } from "@/core/utils/hex.utils";
import { BattleHex } from "@/view/renderers/BattleHex";

/**
 * Pathfinding calculations
 * Pure utility class with static methods
 */
export class PathCalculator {
    // Static cache - shared globally
    private static pathCache = new Map<string, string[]>();
    
    // Prevent instantiation
    private constructor() {
        throw new Error('PathCalculator is a static utility class');
    }
    
    /**
     * Get reachable nodes from a starting position
     * Uses Dijkstra's algorithm with stamina cost
     */
    public static getReachableNodes(
        start: { q: number, r: number },
        unit: { currentStamina: number, speed: number },
        battleMap: Map<string, Hex>
    ): { 
        reachableIds: string[], 
        reachableMap: Map<string, number>,
        parentMap: Map<string, string | null> //   
    } {
        const BASE_MOVEMENT_COST = 30;
        const visited = new Map<string, number>();
        const parentMap = new Map<string, string | null>(); //     
        
        const startId = coordsToId(start);
        const queue: { q: number, r: number, costSoFar: number }[] = [
            { q: start.q, r: start.r, costSoFar: 0 }
        ];

        visited.set(startId, 0);
        parentMap.set(startId, null);

        while (queue.length > 0) {
            queue.sort((a, b) => a.costSoFar - b.costSoFar);
            const current = queue.shift()!;
            const currentId = coordsToId(current);

            for (const dir of HEX_DIRECTIONS) {
                const nQ = current.q + dir.q;
                const nR = current.r + dir.r;
                const nId = coordsToId({ q: nQ, r: nR });
                const neighbor = battleMap.get(nId);

                if (neighbor && !neighbor.isObstacle && !neighbor.entityId) {
                    const terrainMoveCost = TERRAINS[neighbor.terrain]?.moveCost ?? 10;
                    const stepCost = (terrainMoveCost * BASE_MOVEMENT_COST) / unit.speed;
                    const newTotalCost = current.costSoFar + stepCost;

                    if (newTotalCost <= unit.currentStamina) {
                        if (!visited.has(nId) || newTotalCost < visited.get(nId)!) {
                            visited.set(nId, newTotalCost);
                            parentMap.set(nId, currentId); //  
                            queue.push({ q: nQ, r: nR, costSoFar: newTotalCost });
                        }
                    }
                }
            }
        }

        return {
            reachableIds: Array.from(visited.keys()),
            reachableMap: visited,
            parentMap
        };
    }

    public static restorePath(targetId: string, parentMap: Map<string, string | null>): string[] {
        const path: string[] = [];
        let current: string | null = targetId;

        while (current !== null) {
            path.push(current);
            current = parentMap.get(current) || null;
        }

        //    (  startId),      " "
        return path.reverse().slice(1); 
    }
    
    /**
     * Find full A* path between two points
     * Cached for performance
     */
    public static findPath(
        startId: string,
        targetId: string,
        battleMap: Map<string, Hex>
    ): string[] {
        // Check cache
        const cacheKey = `${startId}->${targetId}`;
        if (PathCalculator.pathCache.has(cacheKey)) {
            return PathCalculator.pathCache.get(cacheKey)!;
        }
        
        // Compute
        const path = PathCalculator.computeAStarPath(startId, targetId, battleMap);
        
        // Cache
        PathCalculator.pathCache.set(cacheKey, path);
        
        return path;
    }
    
    /**
     * Get path considering stamina constraints
     */
    public static getPathToHex(
        startId: string,
        targetId: string,
        reachableHexesWithCost: Map<string, number>,
        battleMap: Map<string, Hex>
    ): PathInfo {
        const fullPath = PathCalculator.findPath(startId, targetId, battleMap);
        
        if (fullPath.length === 0) {
            return { path: [], lastStepId: null, cost: 0 };
        }

        const validPath: string[] = [];
        let lastStepId: string | null = null;
        let totalCost = 0;
        
        for (const stepId of fullPath) {
            if (reachableHexesWithCost.has(stepId)) {
                validPath.push(stepId);
                lastStepId = stepId;
                totalCost = reachableHexesWithCost.get(stepId) || totalCost;
            } else {
                break;
            }
        }

        return { path: validPath, lastStepId, cost: totalCost };
    }

    public static constructFullPath(startPos: {x: number, y: number}, pathIds: string[], hexes: Map<string, BattleHex>): Array<{ x: number; y: number }> {
        const path = [{ x: startPos.x, y: startPos.y }];
        pathIds.forEach(id => {
            const h = hexes.get(id);
            if (h) path.push({ x: h.x, y: h.y });
        });
        return path;
    }
    
    /**
     * Calculate radius around center point
     */
    public static getRadius(
        center: { q: number, r: number },
        radius: number,
        battleMap: Map<string, Hex>,
        checkBlocked: boolean = false
    ): Map<string, number> {
        const result = new Map<string, number>();
        const queue: { q: number, r: number, dist: number }[] = [
            { ...center, dist: 0 }
        ];
        const visited = new Set<string>([coordsToId(center)]);

        while (queue.length > 0) {
            const { q, r, dist } = queue.shift()!;

            if (dist > 0) {
                const id = coordsToId({ q, r });
                const hex = battleMap.get(id);
                
                if (!checkBlocked || (hex && !hex.entityId)) {
                    result.set(id, dist);
                }
            }

            if (dist >= radius) continue;

            for (const n of getAxialNeighbors(q, r)) {
                const nId = coordsToId(n);
                const hex = battleMap.get(nId);

                if (hex && !hex.isObstacle && !visited.has(nId)) {
                    visited.add(nId);
                    queue.push({ ...n, dist: dist + 1 });
                }
            }
        }
        
        return result;
    }
    
    /**
     * Clear path cache
     * Call at turn start or when map changes
     */
    public static clearCache(): void {
        PathCalculator.pathCache.clear();
    }
    
    // 
    // PRIVATE HELPERS
    // 
    
    /**
     * A* pathfinding implementation
     */
    /**
     *         (ZoC).
     */
    public static computeAStarPath(
        startId: string,
        targetId: string,
        battleMap: Map<string, Hex>,
        zocMap?: Map<string, number>,
    ): string[] {
        const start = idToCoords(startId);
        const target = idToCoords(targetId);

        //   . 
        // 0.15 =  10   1.5    (  +1.5 )
        const ZOC_PENALTY_WEIGHT = 0.15;
        //    ,       (, +10   )
        const MAX_ZOC_PENALTY = 10;

        const parents = new Map<string, string | null>();
        const gScores = new Map<string, number>(); //   
        const fScores = new Map<string, number>(); // gScore + Heuristic
        const openSet: string[] = [startId];
        
        parents.set(startId, null);
        gScores.set(startId, 0);
        fScores.set(startId, getHexDistance(start, target));

        while (openSet.length > 0) {
            //     fScore.  ,     sort
            openSet.sort((a, b) => {
                return (fScores.get(a) ?? Infinity) - (fScores.get(b) ?? Infinity);
            });

            const currentId = openSet.shift()!;

            //    
            if (currentId === targetId) {
                const path: string[] = [];
                let tempId: string | null = currentId;
                
                //  ,    startId (    -   )
                while (tempId && tempId !== startId) {
                    path.push(tempId);
                    tempId = parents.get(tempId) || null;
                }
                return path.reverse();
            }

            const { q, r } = idToCoords(currentId);
            
            for (const n of getAxialNeighbors(q, r)) {
                const nId = coordsToId(n);
                const hex = battleMap.get(nId);

                //   :
                // 1.  
                // 2.   (/)
                // 3.     (    )
                if (!hex || hex.isObstacle || (hex.entityId && nId !== startId && nId !== targetId)) {
                    continue;
                }

                // ---   (COST) ---
                
                // 1.    ( 1,    /)
                //   Hex   cost,  1
                // const baseCost = (hex as any).cost || 1; 
                const baseCost = TERRAINS[hex.terrain]?.moveCost ?? 10;

                // 2.    Zone of Control (Threat)
                let zocPenalty = 0;
                if (zocMap && zocMap.has(nId)) {
                    const damage = zocMap.get(nId) || 0;
                    zocPenalty = Math.min(MAX_ZOC_PENALTY, damage * ZOC_PENALTY_WEIGHT);
                }

                const moveCost = baseCost + zocPenalty;
                // ----------------------------------

                const tentativeG = (gScores.get(currentId) || 0) + moveCost;

                if (!gScores.has(nId) || tentativeG < gScores.get(nId)!) {
                    parents.set(nId, currentId);
                    gScores.set(nId, tentativeG);
                    
                    const heuristic = getHexDistance(idToCoords(nId), target);
                    fScores.set(nId, tentativeG + heuristic);
                    
                    if (!openSet.includes(nId)) {
                        openSet.push(nId);
                    }
                }
            }
        }

        return []; //   
    }

}