import { AITurnManager } from "@/core/battle/ai/AITurnManager";
import { AIPlan } from "@/core/battle/ai/types/ai.types";
import { AnimationController } from "@/core/battle/AnimationController";
import { CombatExecutor } from "@/core/battle/CombatExecutor";
import { ControlStateMachine } from "@/core/battle/ControlStateMachine";
import { EntityRegistry } from "@/core/battle/EntityRegistry";
import { HighlightManager } from "@/core/battle/HighlightManager";
import { InputController } from "@/core/battle/InputController";
import { MovementExecutor } from "@/core/battle/MovementExecutor";
import { PathCalculator } from "@/core/battle/PathCalculator";
import { SkillManager } from "@/core/battle/SkillManager";
import { TurnManager } from "@/core/battle/TurnManager";
import { ControlState, BattleStatus, FactionType, SkillExecutedEvent } from "@/core/types/battle.types";
import { getSplinePoints } from "@/core/utils/battle.utils";
import { coordsToId } from "@/core/utils/hex.utils";
import { BattleBus } from "@/events/Events";
import { getActivePlayerUnit, getActivePool } from "@/store/battleSelectors";
import { useBattleStore } from "@/store/useBattleStore";
import { BattleCameraManager } from "@/view/BattleCameraManager";
import { BattleHex } from "@/view/renderers/BattleHex";
import { PathRenderer } from "@/view/renderers/PathRenderer";
import { gsap } from "gsap";

import { shallow } from 'zustand/shallow';



const PATH_SMOOTHING_FACTOR = 0.3;



export class BattleManager {
    private unsubscribers: (() => void)[] = [];


    constructor(
        private pathRenderer: PathRenderer, 
        private inputController: InputController,
        private skillManager: SkillManager,
        private cameraManager: BattleCameraManager,
        private highlightManager: HighlightManager,
        private entityRegistry: EntityRegistry,
        private stateMachine: ControlStateMachine,
        private turnManager: TurnManager,
        private aiTurnManager: AITurnManager,
    ) {
        this.setupStoreSubscriptions();
    }

    // --- STORE SUBSCRIPTIONS ---
    
    private setupStoreSubscriptions(): void {
        this.unsubscribers.push(
            useBattleStore.subscribe(state => state.targetHexes, () => this.skillRangeHexHighlights()),
            useBattleStore.subscribe(state => state.reachableHexes, () => this.reachableHexesHighlight()),
            useBattleStore.subscribe(state => state.selectedHexId, hexId => this.handleHexSelection(hexId)),
            // useBattleStore.subscribe(
            //     state => [state.selectedUnitId, state.turnQueue[0]] as const, //    
            //     ([unitId, _currentPool]) => {
            //         console.log('selectedUnitId', unitId, _currentPool);
            //         if (!unitId) return;
                        
            //         this.handleUnitSelection(unitId);
            //     },
            //     { equalityFn: shallow }
            // ),
            useBattleStore.subscribe(
                state => state.selectedUnitId,
                (unitId, prevUnitId) => {
                    if (unitId === prevUnitId) return;
                    
                    
                    this.handleUnitSelection(unitId);
                }
            ),

            // Update input state based on skill selection
            // useBattleStore.subscribe(state => state.selectedSkillId, skillId => {
            //     if (skillId && this.stateMachine.isAnyOf(ControlState.Idle)) {
            //         this.stateMachine.setState(ControlState.SkillTargeting);
            //     } else if (!skillId && this.stateMachine.isAnyOf(ControlState.SkillTargeting)) {
            //         this.stateMachine.setState(ControlState.Idle);
            //     }
            // }),

            // useBattleStore.subscribe(
            //     (state) => {
            //         const unit = state.selectedUnitId ? state.units.get(state.selectedUnitId) : null;
            //         return unit ? { id: unit.unitId, stamina: unit.currentStamina } : null;
            //     },
            //     (current, previous) => {
            //         //           
            //         if (!current || (previous && current.stamina === previous.stamina && current.id === previous.id)) {
            //             return;
            //         }

            //         //  :
            //         //   -> ,     ->  UI
            //         this.handleStaminaChange(current.id);
            //     },
            //     { equalityFn: shallow } //   
            // )
        );

        this.stateMachine.onStateChange((from, to) => {
            // 1. :     ""  (   )
            if (this.stateMachine.isBlocking()) {
                this.highlightManager.clearHoverState();
                this.highlightManager.resetAllHexes();
                // this.pathRenderer.clear();
                //      PathRenderer
            }

            // 2. :         
            if (to === ControlState.Idle || to === ControlState.SkillTargeting) {
                //   Zustand     ( )
                //     
                this.highlightManager.updateSkillRangeHighlights();
                useBattleStore.getState().resetSelectedPath();
                useBattleStore.getState().setSelectedHex(null);
                //       
                // ( ,  InputController )
                const mouse = this.inputController.getLastMousePosition();
                if (mouse && useBattleStore.getState().status === BattleStatus.Playing) {
                    this.highlightManager.restoreHoverHighlightsAfterAnimation(mouse.x, mouse.y);
                }
            }

            if (from === ControlState.PlayerMoving && to === ControlState.Idle) {
                // const unit = getActivePlayerUnit(useBattleStore.getState());
                const unitId = useBattleStore.getState().selectedUnitId
                
                if (unitId) this.handleStaminaChange(unitId);
            }

            if (from === ControlState.PlayerAttacking && to === ControlState.Idle) {
                // const unit = getActivePlayerUnit(useBattleStore.getState());
                const unitId = useBattleStore.getState().selectedUnitId;
                const skillId = useBattleStore.getState().selectedSkillId;
                if (skillId && unitId) this.checkSkillAffordability(unitId, skillId);
                
                if (unitId) this.handleStaminaChange(unitId);
            }
        });

        // BattleBus.on('SKILL_EXECUTED', (event: SkillExecutedEvent) => {
        //     const unitVisual = this.entityRegistry.getUnit(event.targetId);
        //     if (unitVisual) {
        //         this.cameraManager.focusOnPoint({ x: unitVisual.x, y: unitVisual.y });

        //     }
        // });
    }

    private checkSkillAffordability(unitId: string, skillId: string) {
        const unit = useBattleStore.getState().units.get(unitId);
        if (!unit) return;
        const skill = unit.skills.find(s => s.id === skillId) || null;
        if (!skill) return;

        if (unit.currentStamina < skill.cost) {
            // Clear skill selection if not enough stamina for a second use
            this.skillManager.deselectSkill();
        } else {
            this.skillManager.selectSkill(skillId);
        }
    }

    private handleUnitSelection(unitId: string | null): void {

        this.resetSelection();
        if (!unitId) {
            useBattleStore.getState().setReachableHexes([], new Map<string, number>());
            return;
        }
        const gameStatus = useBattleStore.getState().status;
        if (gameStatus !== BattleStatus.Playing) {
            useBattleStore.getState().setSelectedUnit(null);
            return;
        }
        const state = useBattleStore.getState()
        const unitData = state.units.get(unitId);
        if (unitData && unitData.faction === FactionType.Player) {
            const resPath = PathCalculator.getReachableNodes(
                unitData, 
                { currentStamina: unitData.currentStamina, speed: unitData.stats.speed },
                state.battleMap
            )
            state.setReachableHexes(resPath.reachableIds, resPath.reachableMap);
        }

        const unitVisual = this.entityRegistry.getUnit(unitId);
        if (unitVisual) {
            this.cameraManager.focusOnPoint({ x: unitVisual.x, y: unitVisual.y });
            unitVisual.startIdle();
        }

        const activePool = getActivePool(useBattleStore.getState());
        if (activePool) {
            if (activePool.faction === FactionType.Enemy) {
                this.stateMachine.setState(ControlState.EnemyTurn);
                this.handleEnemyTurn();
                // this.turnManager.endUnitTurn(unitId);
            } else {
                this.stateMachine.setState(ControlState.Idle);
            }
        }
    }

    private handleStaminaChange(unitId: string) {
        const state = useBattleStore.getState();
        const unit = state.units.get(unitId);

        if (!unit || unit.faction !== FactionType.Player) return;
        
        if (!this.turnManager.canUnitStillAct(unitId)) {
            console.log("nextSameFactionIdx 4")
            this.turnManager.endUnitTurn(unitId);
        } else {
            // 1.  
            const resPath = PathCalculator.getReachableNodes(
                unit, 
                { currentStamina: unit.currentStamina, speed: unit.stats.speed },
                state.battleMap
            );
            state.setReachableHexes(resPath.reachableIds, resPath.reachableMap);

            // 2.   ( )
            if (state.selectedSkillId) {
                this.highlightManager.updateSkillRangeHighlights();
            }
        }
        
    }

    private skillRangeHexHighlights() {
        const { units, selectedUnitId } = useBattleStore.getState();
        const isPlayer = selectedUnitId ? units.get(selectedUnitId)?.faction === FactionType.Player : null;
        if (isPlayer) {
            this.highlightManager.updateSkillRangeHighlights();
        }
        
    }

    private reachableHexesHighlight() {
        const { units, selectedUnitId } = useBattleStore.getState();
        const isPlayer = selectedUnitId ? units.get(selectedUnitId)?.faction === FactionType.Player : null;
        if (isPlayer) {
            this.highlightManager.updateReachableHighlights();   
        }
    }
    

    private handleHexSelection(hexId: string | null) {
        // click on unit or obstacle
        if (!hexId) {
            this.pathRenderer.clear();
            return;
        };
        
        const state = useBattleStore.getState();
        if (!state.selectedUnitId) return;
        const unit = state.units.get(state.selectedUnitId);
        if (!unit) return;

        const startHexId = coordsToId(unit);
        const resPath = PathCalculator.getPathToHex(startHexId, hexId, state.reachableHexesWithCost, state.battleMap);
        useBattleStore.getState().setSelectedPath(resPath);
        this.showUnitPath(unit.unitId, resPath.path);
    }
    
    private resetSelection(): void { 
        const inputState = this.stateMachine.getState();
        console.log("selectedUnitId resetSelection", inputState);
        if (inputState === ControlState.Idle || inputState === ControlState.EnemyTurn) { 
            useBattleStore.getState().setSelectedHex(null);
            PathCalculator.clearCache();
            this.pathRenderer.clear();
            this.highlightManager.resetAllHexes();
            this.highlightManager.resetAllUnits();
            this.highlightManager.clearHoverState();
        } 
    }

    private showUnitPath(unitId: string, path: string[]): void {
        const unit = this.entityRegistry.getUnit(unitId);
        if (!unit) return;
        const fullPath = PathCalculator.constructFullPath(unit, path, this.entityRegistry.getAllHexes());

        const smoothedPath = this.pathRenderer.smoothPath(fullPath, PATH_SMOOTHING_FACTOR);
        const spline = getSplinePoints(smoothedPath);
        this.pathRenderer.drawPathFromPoints(spline);
    }
    

    /**
     * Handle the entire enemy turn phase
     * Simplified - delegates to AITurnManager
     */
    private async handleEnemyTurn(): Promise<void> {
        const state = useBattleStore.getState();
        const currentPool = getActivePool(state);
        
        if (!state.selectedUnitId || !currentPool || currentPool.faction !== FactionType.Enemy) {
            return;
        }
        const enemyUnit = state.units.get(state.selectedUnitId);

        if (!enemyUnit || enemyUnit.isDead || enemyUnit.faction !== FactionType.Enemy) return;
        
        // const availableIds = currentPool.unitIds.filter(
        //     id => !state.unitsFinishedTurn.has(id)
        // );
        
        console.log('[EnemyTurn] Starting with:', enemyUnit.unitId);

        await this.aiTurnManager.executeUnitTurn(enemyUnit.unitId);
        
        // if (availableIds.length > 0) {
        //     const unitId = availableIds[0];
        //     if (!unitId) return;
        //     const unit = state.units.get(unitId);
            
        //     if (!unit || unit.isDead) return;
            
        //     await this.aiTurnManager.executeUnitTurn(unitId);
        // }
        
        console.log('[EnemyTurn] Completed');
    }
    


    // ---   ---
    public destroy(): void {
        console.warn("BattleManager: ...");

        // 1.   Zustand (  RAM)
        this.unsubscribers.forEach(unsub => unsub());
        this.unsubscribers = [];

        // 3.  GSAP (    ')
        gsap.killTweensOf(this);
        
        // 6.   
        this.pathRenderer.clear();

        console.log("BattleManager: ' .");
    }

}