import { BattleBus } from '../../events/Events';
import { useBattleStore } from '../../store/useBattleStore';
import { FactionType } from '../types/battle.types';

export class TooltipManager {
    private element: HTMLElement;
    private displayTimeout: ReturnType<typeof setTimeout> | null = null;
    private mousePos = { x: 0, y: 0 };
    private currentUnitId: string | null = null;
    private readonly HOVER_DELAY = 400;

    //    -    
    private storeUnsubscribe: () => void;

    constructor() {
        this.element = this.initElement();
        this.setupListeners();
        
        //   Zustand    
        this.storeUnsubscribe = useBattleStore.subscribe(
            (state) => state.selectedSkillId,
            (selectedSkillId) => this.handleSelectionChange(selectedSkillId)
        );
    }

    private setupListeners() {
        BattleBus.on('UNIT_HOVER', this.handleUnitHover);
        BattleBus.on('UNIT_MOUSEMOVE', this.handleMouseMove);
        BattleBus.on('UNIT_OUT', this.handleUnitOut);
        BattleBus.on('TOOLTIP_UPDATE', this.handleTooltipUpdate);
    }

    //      ( )
    private handleUnitHover = ({ id, position }: { id: string, position: { x: number, y: number } }) => {
        this.currentUnitId = id;
        this.mousePos = position;
        this.startIdlingTimer();
    };

    private handleMouseMove = ({ position }: { position: { x: number, y: number } }) => {
        this.mousePos = position;
        if (this.element.style.display === 'block') {
            this.updatePosition();
        } else {
            this.startIdlingTimer();
        }
    };

    private handleUnitOut = () => {
        this.currentUnitId = null;
        this.clearTimer();
        this.hide();
    };

    private handleTooltipUpdate = () => {
        
        if (this.currentUnitId) {
            const unit = useBattleStore.getState().units.get(this.currentUnitId);
            if (unit?.isDead) {
                this.currentUnitId = null;
                this.hide();
            } else {
                this.render();
            }
        }
    };

    private handleSelectionChange(skillId: string | null) {
        if (this.element.style.display === 'block' && skillId) {
            this.handleUnitOut();
        }
    }

    private initElement(): HTMLElement {
        let el = document.getElementById('game-tooltip');
        if (!el) {
            el = document.createElement('div');
            el.id = 'game-tooltip';
            document.body.appendChild(el);
        }
        return el;
    }

    private startIdlingTimer() {
        this.clearTimer();
        this.displayTimeout = setTimeout(() => {
            if (this.currentUnitId) {
                const { selectedSkillId, units } = useBattleStore.getState()
                const unit = units.get(this.currentUnitId);
                if (!selectedSkillId && unit) this.show();
            }
        }, this.HOVER_DELAY);
    }

    private show() {
        this.render(); //  
        this.element.style.display = 'block';
        this.updatePosition();
    }

    private updatePosition() {
        // ,       
        this.element.style.left = `${this.mousePos.x + 15}px`;
        this.element.style.top = `${this.mousePos.y + 15}px`;
    }

    private hide() {
        this.element.style.display = 'none';
    }

    private clearTimer() {
        if (this.displayTimeout) {
            clearTimeout(this.displayTimeout);
            this.displayTimeout = null;
        }
    }

    private render() {
        if (!this.currentUnitId) return;
        
        //    ,      '
        const { units, selectedUnitId, activeFaction } = useBattleStore.getState();
        
        const unitData = units.get(this.currentUnitId);
        
        if (!unitData) return;

        const isFriendly = unitData.faction === FactionType.Player;
        const isSelected = selectedUnitId === unitData.unitId;
        const totalHP = unitData.stats.hp * unitData.count;
        const currentTotalHP = ((unitData.count - 1) * unitData.stats.hp) + unitData.currentUnitHp
        this.element.innerHTML = `
            ${isSelected ? '<div class="tooltip-status selected"></div>' : ''}
            <div class="tooltip-title ${isFriendly ? 'friend' : 'enemy'}">${unitData.unitId.split('_')[0]}</div>
            <div class="tooltip-stat">HP: ${currentTotalHP} / ${totalHP}</div>
            <div class="tooltip-footer">
                ${activeFaction === unitData.faction ? ' ' : ' '}
            </div>
        `;
    }

    /**
     *   (  BattleScene.destroy)
     */
    public destroy() {
        this.clearTimer();
        
        // 1.    
        BattleBus.off('UNIT_HOVER', this.handleUnitHover);
        BattleBus.off('UNIT_MOUSEMOVE', this.handleMouseMove);
        BattleBus.off('UNIT_OUT', this.handleUnitOut);
        BattleBus.off('TOOLTIP_UPDATE', this.handleTooltipUpdate);

        // 2.   
        // if (this.storeUnsubscribe) this.storeUnsubscribe();

        // 3.    DOM
        if (this.element && this.element.parentNode) {
            this.element.parentNode.removeChild(this.element);
        }
        
        console.log("TooltipManager: Cleaned up.");
    }
}