/**
 * AI Strategic Manager
 * 
 * Manages goal creation and assignment for enemy AI units.
 * Provides strategic layer above tactical decision-making.
 */

import type { BattleUnit, Hex } from '@/core/types/battle.types';

import { 
    type Goal, 
    GoalType, 
    type GoalAssignment,
    type GoalSuitabilityEvaluator,
    type ManagerState
} from '@/core/battle/ai/strategic/AIGoals';
import { getHexDistance } from '@/core/utils/hex.utils';
import { PlanningContext } from '@/core/battle/ai/types/ai.types';

export class AIStrategicManager {
    private state: ManagerState = {
        goals: [],
        assignments: new Map(),
        goalProgress: new Map(),
        goalsCreated: 0,
        goalsCompleted: 0,
        goalsFailed: 0,
    };
    
    private currentTurn: number = 0;
    
    // 
    // PUBLIC API
    // 
    
    /**
     * Called at the start of each enemy turn
     * Generates goals based on current battlefield state
     */
    onTurnStart(
        allies: BattleUnit[],    // Enemy units (our side)
        enemies: BattleUnit[],   // Player units (targets)
        battleMap: Map<string, Hex>,
        turn: number,
        ctx: PlanningContext
    ): void {
        this.currentTurn = turn;

        // Clear previous turn's data
        this.state.goals = [];
        this.state.assignments.clear();
        
        // Generate new goals
        this.state.goals = this.generateGoals(allies, enemies, battleMap);
        this.assignGoalsToAll(allies, enemies, ctx);
        console.log(`[AIManager] Turn ${turn}: Generated goals`, this.state.goals);
    }
    
    /**
     * Get goal assignment for a specific unit
     * Called when unit is ready to act
     */
    getGoalForUnit(
        unit: BattleUnit,
        allies: BattleUnit[],
        enemies: BattleUnit[],
        battleMap: Map<string, Hex>
    ): Goal | null {
        // Check if unit already has valid assignment
        const existingGoalId = this.state.assignments.get(unit.unitId);
        if (existingGoalId) {
            const existingGoal = this.state.goals.find(g => g.id === existingGoalId);
            if (existingGoal && this.isGoalValid(existingGoal, enemies)) {
                console.log(`[AIManager] Unit ${unit.unitId}: Keeping existing goal ${existingGoal.type} - ${existingGoal.targetId}`);
                return existingGoal;
            }
        }
        
        // Find best available goal
        const assignment = this.assignBestGoal(unit, allies, enemies, battleMap);
        
        if (assignment.goal) {
            // Record assignment
            this.state.assignments.set(unit.unitId, assignment.goal.id);
            assignment.goal.assignedUnits.push(unit.unitId);
            
            console.log(`[AIManager] Unit ${unit.unitId}: Assigned ${assignment.goal.type} (score: ${assignment.score.toFixed(1)})`, this.state.goals);
        } else {
            console.log(`[AIManager] Unit ${unit.unitId}: No suitable goal, going OPPORTUNISTIC`);
        }
        
        return assignment.goal;
    }
    
    /**
     * Mark goal as completed (target killed, objective reached, etc)
     */
    markGoalCompleted(goalId: string): void {
        const goal = this.state.goals.find(g => g.id === goalId);
        if (goal) {
            goal.isActive = false;
            this.state.goalsCompleted++;
        }
    }
    
    /**
     * Get current manager state (for debugging)
     */
    getState(): Readonly<ManagerState> {
        return this.state;
    }
    
    // 
    // GOAL GENERATION
    // 
    
    private generateGoals(
        allies: BattleUnit[],
        enemies: BattleUnit[],
        battleMap: Map<string, Hex>
    ): Goal[] {
        const goals: Goal[] = [];
        
        // For now: Create ELIMINATE goal for each enemy
        // Priority based on distance to closest ally
        
        for (const enemy of enemies) {
            const closestAllyDistance = this.findClosestAllyDistance(enemy, allies);
            
            // Priority: closer enemies = higher priority
            // Range: 10 (adjacent) to 1 (far away)
            const priority = Math.max(1, Math.min(10, 11 - closestAllyDistance));
            
            const goal: Goal = {
                id: `eliminate_${enemy.unitId}_t${this.currentTurn}`,
                type: GoalType.ELIMINATE,
                priority,
                targetId: enemy.unitId,
                assignedUnits: [],
                isActive: true,
                maxUnits: 1,
                reason: `Closest ally distance: ${closestAllyDistance}`,
            };
            
            goals.push(goal);
        }
        
        // Sort by priority (highest first)
        goals.sort((a, b) => b.priority - a.priority);
        
        this.state.goalsCreated += goals.length;
        
        return goals;
    }

    /**
     *      
     */
    // assignGoalsToAll(
    //     allies: BattleUnit[],
    //     enemies: BattleUnit[],
    //     battleMap: Map<string, Hex>
    // ): void {
    //     // 1.   
    //     // this.state.assignments.clear();
    //     // this.state.goals.forEach(g => g.assignedUnits = []);

    //     // 2.    (   )
    //     const availableUnits = [...allies];

    //     for (const unit of availableUnits) {
    //         //    assignBestGoal
    //         const assignment = this.assignBestGoal(unit, allies, enemies, battleMap);

    //         if (assignment.goal) {
    //             this.state.assignments.set(unit.unitId, assignment.goal.id);
    //             assignment.goal.assignedUnits.push(unit.unitId);
    //         }
    //     }
    // }
    assignGoalsToAll(allies: BattleUnit[], enemies: BattleUnit[], ctx: PlanningContext): void {
        const possibilities: {u: string, g: Goal, score: number}[] = [];

        for (const unit of allies) {
            //  ,   
            // if (unit.isDone) continue; 

            for (const goal of this.state.goals) {
                const target = enemies.find(e => e.unitId === goal.targetId);
                if (!target || target.isDead) continue;
                
                //   ctx   
                const score = this.calculateWeight(unit, target, ctx);
                possibilities.push({ u: unit.unitId, g: goal, score });
            }
        }

        //      
        possibilities.sort((a, b) => b.score - a.score);

        const busyUnits = new Set<string>();
        const goalSaturation = new Map<string, number>();

        for (const p of possibilities) {
            if (busyUnits.has(p.u)) continue;
            
            const assignedCount = goalSaturation.get(p.g.id) ?? 0;
            //  maxUnits ,  Infinity
            const limit = p.g.maxUnits ?? Infinity;

            if (assignedCount < limit) {
                this.state.assignments.set(p.u, p.g.id);
                p.g.assignedUnits.push(p.u);
                
                busyUnits.add(p.u);
                goalSaturation.set(p.g.id, assignedCount + 1);
            }
        }
    }

    private calculateWeight(unit: BattleUnit, target: BattleUnit, context: PlanningContext): number {
        const dist = context.pathsToEnemies.get(target.unitId)?.length || 5;

        //  AP  ,  0,   
        const unitAP = unit.skills[0]?.armorPenetration ?? 0;
        //  AP  (  )
        const targetAP = target.skills[0]?.armorPenetration ?? 0;

        // 1.   ( Armor Pen)
        const effectiveArmor = Math.max(0, target.stats.armor - unitAP);
        const potentialDmg = Math.max(0, unit.stats.damageMin - effectiveArmor);
        
        // 2.   (Threat)
        const targetThreat = target.stats.damageMin * (1 + targetAP / 20);
        
        // 3.   (Matchup)
        let efficiencyBonus = 0;
        if (unitAP > 0 && target.stats.armor > 0) {
            const wasteRatio = target.stats.armor / unitAP;
            //  Math.min/max   
            efficiencyBonus = wasteRatio > 0.5 && wasteRatio <= 1.2 ? 20 : 0;
        }

        // 4. 
        const distancePenalty = Math.pow(dist, 1.5) * 2;

        return (potentialDmg * (targetThreat / 10)) + efficiencyBonus - distancePenalty;
    }
    
    // 
    // ASSIGNMENT LOGIC
    // 
    
    private assignBestGoal(
        unit: BattleUnit,
        allies: BattleUnit[],
        enemies: BattleUnit[],
        battleMap: Map<string, Hex>
    ): GoalAssignment {
        const context = {
            allies,
            enemies,
            battleMap,
            getDistance: getHexDistance,
            getTarget: (targetId: string) => enemies.find(e => e.unitId === targetId),
        };
        
        // Evaluate all active goals
        const scored = this.state.goals
            .filter(goal => goal.isActive)
            .filter(goal => this.canUnitPursueGoal(unit, goal, context))
            .map(goal => ({
                goal,
                score: this.evaluateGoalSuitability(unit, goal, context),
            }))
            .sort((a, b) => b.score - a.score);
        
        const bestChoice = scored[0];

        if (!bestChoice) {
            return { unitId: unit.unitId, goal: null, score: 0 };
        }
        
        return {
            unitId: unit.unitId,
            goal: bestChoice.goal,
            score: bestChoice.score,
            alternatives: scored.slice(1, 3).map(s => s.goal),
        };
    }
    
    /**
     * Check if unit can pursue this goal
     */
    private canUnitPursueGoal(
        unit: BattleUnit,
        goal: Goal,
        context: any
    ): boolean {
        // Check if goal already has enough units
        if (goal.maxUnits && goal.assignedUnits.length >= goal.maxUnits) {
            return false;
        }
        
        // Check if target still exists
        if (goal.targetId) {
            const target = context.getTarget(goal.targetId);
            if (!target) return false;
        }
        
        return true;
    }
    
    /**
     * DISTANCE-BASED SUITABILITY (simple version)
     * 
     * Score = priority * (100 / (distance + 1))
     * Closer units get higher scores
     * 
     * TODO: Replace with more sophisticated evaluator
     */
    private evaluateGoalSuitability(
        unit: BattleUnit,
        goal: Goal,
        context: any
    ): number {
        const basePriority = goal.priority * 10;  // Scale to 10-100
        
        if (goal.targetId) {
            const target = context.getTarget(goal.targetId);
            if (!target) return 0;
            
            const distance = context.getDistance(
                { q: unit.q, r: unit.r },
                { q: target.q, r: target.r }
            );
            
            // Distance penalty: score decreases with distance
            const distanceFactor = 100 / (distance + 1);
            
            return basePriority * (distanceFactor / 100);
        }
        
        return basePriority;
    }
    
    // 
    // VALIDATION
    // 
    
    private isGoalValid(goal: Goal, enemies: BattleUnit[]): boolean {
        if (!goal.isActive) return false;
        
        // Check if target still alive
        if (goal.targetId) {
            const targetExists = enemies.some(e => e.unitId === goal.targetId);
            if (!targetExists) {
                goal.isActive = false;
                goal.reason = 'Target eliminated';
                return false;
            }
        }
        
        return true;
    }
    
    // 
    // HELPERS
    // 
    
    private findClosestAllyDistance(
        target: BattleUnit,
        allies: BattleUnit[]
    ): number {
        let minDistance = Infinity;
        
        for (const ally of allies) {
            const distance = getHexDistance(
                { q: ally.q, r: ally.r },
                { q: target.q, r: target.r }
            );
            
            if (distance < minDistance) {
                minDistance = distance;
            }
        }
        
        return minDistance;
    }
    
    // 
    // FUTURE: Custom evaluators
    // 
    
    /**
     * Allow custom suitability evaluators to be registered
     * This is where you can replace distance-based logic with:
     * - Damage potential
     * - Unit type matchups
     * - Strategic importance
     * - Etc.
     */
    private customEvaluators: Map<GoalType, GoalSuitabilityEvaluator> = new Map();
    
    setEvaluator(goalType: GoalType, evaluator: GoalSuitabilityEvaluator): void {
        this.customEvaluators.set(goalType, evaluator);
    }

    public getCurrentTurn() {
        return this.currentTurn;
    }
}