import * as PIXI from 'pixi.js';
import { Viewport } from 'pixi-viewport';

export class BattleCameraManager {
    constructor(private viewport: Viewport) {
        this.initDefaultBehavior();
    }

    private initDefaultBehavior() {
        this.viewport
            .drag({ mouseButtons: 'left' })
            .pinch()
            .wheel()
            .clamp({
                direction: 'all',
                underflow: 'center' 
            })
            .clampZoom({
                minScale: 0.4,
                maxScale: 1.35
            });

        this.viewport.decelerate({ friction: 0.85 });
    }

    public updateSize(width: number, height: number) {
        this.viewport.screenWidth = width;
        this.viewport.screenHeight = height;
        
        //       (clamp)
        this.viewport.dirty = true;
    }

    public initConstraints(bounds: PIXI.Rectangle) {
        this.viewport.worldWidth = bounds.x + bounds.width;
        this.viewport.worldHeight = bounds.y + bounds.height;

        //      
        const centerX = bounds.x + bounds.width / 2;
        const centerY = bounds.y + bounds.height / 2;

        this.viewport.moveCenter(centerX, centerY);
        this.viewport.setZoom(1, true);
    }
    

    public applyGridMask(targetLayer: PIXI.Container, bounds: PIXI.Rectangle) {
            const mask = new PIXI.Graphics()
                .rect(0, 0, bounds.width - 180, bounds.height - 180)
                .fill(0xffffff);
    
            mask.eventMode = 'none';
            
            //      ,   
            targetLayer.addChild(mask);
            targetLayer.mask = mask;
            
            return mask; //  ,     destroy
        }

    public lock() {
        this.viewport.plugins.pause('drag');
        this.viewport.plugins.pause('wheel');
        this.viewport.plugins.pause('pinch');
    }

    public unlock() {
        this.viewport.plugins.resume('drag');
        this.viewport.plugins.resume('wheel');
        this.viewport.plugins.resume('pinch');
    }


    /**
     *      '
     */
    public async focusOnBattleCenter(units: { x: number, y: number }[]) {
        if (units.length === 0) return;

        const avgX = units.reduce((sum, u) => sum + u.x, 0) / units.length;
        const avgY = units.reduce((sum, u) => sum + u.y, 0) / units.length;

        return new Promise<void>((resolve) => {
            //  .snap()    
            //          
            this.viewport.snap(avgX, avgY, {
                removeOnInterrupt: true,
                time: 1000,
                ease: 'easeInOutExpo',
            });

            //  snap       , 
            //     
            this.viewport.animate({
                scale: 0.5,
                time: 1000,
                ease: 'easeInOutExpo',
                callbackOnComplete: () => resolve()
            });
        });
    }

    /**
     *     
     * @param point  
     * @param force  true,      
     */
    public async focusOnPoint(
        point: { x: number, y: number }, 
        force = false, 
        onlyBringIntoView = true //   
    ) {
        const padding = 100;
        this.viewport.plugins.pause('snap');
        const visibleBounds = this.viewport.getVisibleBounds();
        const { width, height, x: vx, y: vy } = visibleBounds;

        // ,     
        const isSafe = (
            point.x > vx + padding &&
            point.x < vx + width - padding &&
            point.y > vy + padding &&
            point.y < vy + height - padding
        );

        if (isSafe && !force) return;

        //     '
        let targetCenterX = vx + width / 2;
        let targetCenterY = vy + height / 2;

        if (onlyBringIntoView) {
            //     X
            const newPadding = padding * 2;
            if (point.x < vx + padding) {
                //   :  ' 
                targetCenterX = point.x - newPadding + width / 2;
            } else if (point.x > vx + width - padding) {
                //   :  ' 
                targetCenterX = point.x + newPadding - width / 2;
            }

            //     Y (   -50  UI)
            const topBound = vy + padding;
            const bottomBound = vy + height - padding;
            const targetYWithOffset = point.y - 50;

            if (targetYWithOffset < topBound) {
                targetCenterY = targetYWithOffset - newPadding + height / 2;
            } else if (targetYWithOffset > bottomBound) {
                targetCenterY = targetYWithOffset + newPadding - height / 2;
            }
        } else {
            //   
            targetCenterX = point.x;
            targetCenterY = point.y - 50;
        }

        return new Promise<void>((resolve) => {
            //  animate  moveCenter  snap, 
            //  snap    '
            this.viewport.animate({
                position: { x: targetCenterX, y: targetCenterY },
                time: 800,
                ease: 'easeInOutCubic',
                removeOnInterrupt: true,
                callbackOnComplete: () => resolve()
            });

        });
    }

    /**
     *    ""   (,  )
     */
    public jumpToPoint(x: number, y: number) {
        this.viewport.moveCenter(x, y);
    }

}