import { HexDistanceInfo, PathData, PathsToEnemyData } from "@/core/battle/ai/types/ai.types";
import { PLANNING_CONFIG } from "@/core/battle/ai/utils/AIConfig";
import { CombatCalculator } from "@/core/battle/CombatCalculator";
import { PathCalculator } from "@/core/battle/PathCalculator";
import { EFFECTIVE_COUNT } from "@/core/constants/battle.constants";
import { BattleUnit, Hex } from "@/core/types/battle.types";
import { coordsToId, getHexDistance, idToCoords } from "@/core/utils/hex.utils";


export class PathsToEnemyCalculator {
    public static calculate(
        attacker: BattleUnit,
        position: { q: number; r: number },
        enemies: BattleUnit[],
        reachableHexesWithCost: Map<string, number>,
        battleMap: Map<string, Hex>,
        zocMap?: Map<string, number>,
    ): PathsToEnemyData {
        const pathHexes = new Map<string, PathData>();
        const pathsToEnemies = new Map<string, string[]>();
        
        if (!PLANNING_CONFIG.enablePathBased || enemies.length === 0) {
            return { pathHexes, pathsToEnemies };
        }
        
        const currentId = coordsToId(position);
        const allPaths: Array<{ path: string[], weight: number, enemyDmg: number, isKillable: boolean }> = [];
        
        let minPathLength = Infinity;
        
        for (const enemy of enemies) {
            const targetId = coordsToId(enemy);
            // const path = PathCalculator.computeAStarPath(currentId, targetId, battleMap, zocMap);
            const path = PathCalculator.findPath(currentId, targetId, battleMap);
            
            if (path && path.length > 0) {
                if (path.length < minPathLength) minPathLength = path.length;
                
                const potentialDmg = CombatCalculator.calculateRawDamage(attacker, enemy, 1, 0, true);
                const totalEnemyHP = Math.max(0, (enemy.count - 1) * enemy.stats.hp + enemy.currentUnitHp);
                const isKillable = potentialDmg >= totalEnemyHP * 1.5;
                const effectiveDmg = Math.min(potentialDmg, totalEnemyHP * 1.5);
                // const effectiveDmg = potentialDmg;
                const effectiveEnemyCount = Math.pow(attacker.count, EFFECTIVE_COUNT);
                const enemyDmg = ((enemy.stats.damageMin + enemy.stats.damageMax) / 2) * effectiveEnemyCount;
                
                allPaths.push({ path, weight: effectiveDmg, enemyDmg: enemyDmg, isKillable: isKillable });
                pathsToEnemies.set(enemy.unitId, path);
            }
        }
        
        if (allPaths.length === 0) return { pathHexes, pathsToEnemies };
        
        //      
        for (const { path, weight, enemyDmg, isKillable } of allPaths) {
            for (const hexId of path) {
                if (reachableHexesWithCost.has(hexId)) {
                    const current = pathHexes.get(hexId) || { 
                        maxDmg: 0, 
                        sumDmg: 0,
                        enemyMaxDmg: 0,
                        enemySumDmg: 0,
                        dist: Infinity, 
                        hasKill: false 
                    };
                    const hasKill = isKillable || current.hasKill;
                    
                    pathHexes.set(hexId, {
                        maxDmg: Math.max(current.maxDmg, weight),
                        sumDmg: current.sumDmg + weight,
                        enemyMaxDmg: Math.max(current.enemyMaxDmg, enemyDmg),
                        enemySumDmg: current.enemySumDmg + enemyDmg,
                        hasKill: hasKill,
                        dist: Math.min(current.dist, path.length)
                    });
                }
            }
        }
        return { pathHexes, pathsToEnemies };
    }
}