import React from 'react';
import './SmartBlock.css';
import { CombatCalculator } from '@/core/battle/CombatCalculator';
import { BattleUnit, Skill } from '@/core/types/battle.types';

interface SmartBlockProps {
  skill?: Skill | null; // Selected skill or null
  unit: BattleUnit; // Selected skill or null
}



export function SmartBlock({ unit, skill }: SmartBlockProps) {
  if (!skill) {
    return (
      <div className="smart-block">
        <div className="idle-state">
          <div className="idle-text"></div>
        </div>
      </div>
    );
  }
  const effectiveCount = CombatCalculator.getEffectiveCount(unit!);
  const minDmg = Math.round(unit?.stats.damageMin * effectiveCount * (skill.powerMult || 1));
  const maxDmg = Math.round(unit?.stats.damageMax * effectiveCount * (skill.powerMult || 1));

  return (
    <div className="smart-block">
      <div className="skill-display">
        {/* Skill Icon */}
        <div className="skill-icon">
          {skill.icon ? (
            <img src={skill.icon} alt={skill.name} />
          ) : (
            <div className="skill-icon-placeholder">
              {skill.name.charAt(0).toUpperCase()}
            </div>
          )}
        </div>

        {/* Skill Stats */}
        <div className="skill-stats">
          <div className="skill-name">{skill.name}</div>
          
          
          
          {minDmg !== undefined && (
            <div className="skill-stat">
              <span className="stat-label">Dmg:</span> {minDmg} - {maxDmg}
            </div>
          )}

          {skill.range !== undefined && (
            <div className="skill-stat">
              <span className="stat-label">Range:</span> {skill.range}
            </div>
          )}

          {skill.cost !== undefined && (
            <div className="skill-stat stamina-cost">
              <span className="stat-label">Cost:</span> {skill.cost}
            </div>
          )}
          
          {skill.armorPenetration !== undefined && (
            <div className="skill-stat">
              <span className="stat-label">Armor Pen:</span> {skill.armorPenetration * 100}%
            </div>
          )}

          
        </div>
      </div>
    </div>
  );
}

