import { type UtilityComponents } from '../AIDecisionLogger';
import { type AIWeights } from '../utils/AIConfig';
import { type BattleUnit, Hex, PathInfo } from '../../../types/battle.types';
import { Goal } from '@/core/battle/ai/strategic/AIGoals';
import { BehaviorStrategy } from '@/core/battle/ai/BehaviorAnalyzer';


export const StrategicStance = {
    AGGRESSIVE: 'AGGRESSIVE',
    BALANCED: 'BALANCED',
    DEFENSIVE: 'DEFENSIVE', 
} as const;

export type StrategicStance = typeof StrategicStance[keyof typeof StrategicStance];

export const AIActionType = {
    Attack: 'attack',
    MoveAttack: 'move_attack',
    Move: 'move',
    Wait: 'wait',
} as const;
export type AIActionType = (typeof AIActionType)[keyof typeof AIActionType];


export interface AIPlan {
    type: AIActionType;
    unitId: string;
    targetHexId?: string;
    targetPath?: PathInfo;
    targetUnitId?: string;
    skillId?: string;
}

export interface ActionSequence {
    actions: AIPlan[];
    totalUtility: number;
    finalStamina: number;
}

export interface HexDistanceInfo {
    distances: Map<string, number>;
    minDist: number;
    closestEnemyId: string | null;
}

export interface PrecomputedHexData {
    distanceMatrix: Map<string, HexDistanceInfo>;
    oppRiskCache: Map<string, number>;
}

export interface PathsToEnemyData {
    pathHexes: Map<string, PathData>; 
    pathsToEnemies: Map<string, string[]>
}

export interface PathData {
    maxDmg: number;    //  ,     
    sumDmg: number;    //      ( )
    enemySumDmg: number;
    enemyMaxDmg: number;
    hasKill: boolean;
    dist: number;   //       
}

// 
// TYPES
// 

export interface PlanningContext {
    unit: BattleUnit;
    goal: Goal | null;
    
    allies: BattleUnit[];
    enemies: BattleUnit[];

    target?: BattleUnit;
    battleMap: Map<string, Hex>;
    reachableMap: Map<string, number>;
    parentMap: Map<string, string | null>;

    stance: StrategicStance;
    weights: AIWeights;
    behavior: BehaviorStrategy;
    threatMap: Map<string, number>;
    zocMap: Map<string, number>;
    distanceMatrix: Map<string, HexDistanceInfo>;
    oppRiskCache: Map<string, number>;
    pathHexes: Map<string, PathData>;
    pathsToEnemies: Map<string, string[]>;
}

export interface PlanningState {
    position: { q: number; r: number };
    stamina: number;
    actionsUsed: AIPlan[];
    totalUtility: number;
    depth: number;
}

export interface EvaluatedCandidate {
    utility: number;
    components?: UtilityComponents;
}

export interface ActionCandidate {
    action: AIPlan;
    utility: number;
    staminaAfter: number;
    position: { q: number; r: number };
    components?: UtilityComponents;
    metadata?: {
        type: 'path-based' | 'opportunistic' | 'attack' | 'retreat' | 'wait';
        targetEnemy?: string;
        pathAlignment?: boolean;
    };
}

export interface IEvaluator<T> {
    evaluate(state: PlanningState, ctx: PlanningContext, target: T, additionalData?: any): EvaluatedCandidate;
}

export interface IActionGenerator {
    generatorType: AIActionType;
    //   ,   
    generate(state: PlanningState, context: PlanningContext, evaluator: IEvaluator<any>): ActionCandidate[];
}

export interface ActionComponent {
    generator: IActionGenerator;
    evaluator: IEvaluator<any>;
}