import { HEX_DIRECTIONS, HEX_SIZE, TERRAINS } from '@/core/constants/battle.constants';
import type { Hex } from '@/core/types/battle.types';


export function axialToPixel(q: number, r: number) {
  // HEX_SIZE       
  const x = HEX_SIZE * (3/2 * q);
  const y = HEX_SIZE * (Math.sqrt(3) * (r + q / 2));
  
  return { x, y };
}

/**
 * Convert hex ID "q:r" to coordinates {q, r}
 */
export function idToCoords(id: string): { q: number; r: number } {
    const [q, r] = id.split(':').map(Number);
    return { q: q ?? 0, r: r ?? 0 };
}

/**
 * Convert coordinates {q, r} to hex ID "q:r"
 */
export function coordsToId({ q, r }: { q: number; r: number }): string {
    return `${q}:${r}`;
}

/**
 * Convert Axial to Cube coordinates
 */
export function axialToCube(q: number, r: number) {
    return {
        x: q,
        y: r,
        z: -q - r
    };
}

/**
 * Calculate hex distance between two points
 */
export function getHexDistance(
    a: { q: number; r: number },
    b: { q: number; r: number }
): number {
    return Math.max(
        Math.abs(a.q - b.q),
        Math.abs(a.r - b.r),
        Math.abs((-a.q - a.r) - (-b.q - b.r))
    );
}

/**
 * Get axial neighbors
 */
export function getAxialNeighbors(q: number, r: number) {
    return HEX_DIRECTIONS.map(d => ({
        q: q + d.q,
        r: r + d.r
    }));
}

/**
 * Get hex line between two points
 */
export function getHexLine(
    a: { q: number; r: number },
    b: { q: number; r: number }
): string[] {
    const dist = getHexDistance(a, b);
    const results: string[] = [];
    
    for (let i = 0; i <= dist; i++) {
        const t = dist === 0 ? 0 : i / dist;
        const q = Math.round(a.q + (b.q - a.q) * t);
        const r = Math.round(a.r + (b.r - a.r) * t);
        results.push(`${q}:${r}`);
    }
    
    return results;
}

/**
 * Offset neighbors (legacy, for backward compatibility)
 */
export function getOffsetNeighbors(q: number, r: number) {
    const isEven = q % 2 === 0;
    return isEven 
        ? [
            {q: q+1, r: r-1}, {q: q+1, r: r}, {q: q, r: r+1},
            {q: q-1, r: r}, {q: q-1, r: r-1}, {q: q, r: r-1}
          ]
        : [
            {q: q+1, r: r}, {q: q+1, r: r+1}, {q: q, r: r+1},
            {q: q-1, r: r+1}, {q: q-1, r: r}, {q: q, r: r-1}
          ];
}



