import * as PIXI from 'pixi.js';
// import { BattleHex } from '../../core/battle/battleHex';

export class PathRenderer {
    //   ,    
    public container: PIXI.Container;
    private footprints: PIXI.Sprite[] = [];
    private readonly STEP_DISTANCE = 45; //    
    private readonly ASSET_URL = '/assets/footsteps.png';

    constructor(parent: PIXI.Container) {
        this.container = new PIXI.Container();
        this.container.eventMode = 'none'; 
        this.container.interactiveChildren = false; 

        parent.addChild(this.container);
        //       Scene Preloader, 
        //      
        PIXI.Assets.load(this.ASSET_URL);
    }

    public drawPathFromPoints(rawPoints: {x: number, y: number}[]) {
        if (rawPoints.length < 2) {
            this.clear();
            return;
        }

        // 2.   
        this.clear();

        // 1.   (Interpolation)
        const smoothedPoints = this.smoothPath(rawPoints, 0.5, 2);

        // 3.  
        let distanceTraveled = 0;
        for (let i = 0; i < smoothedPoints.length - 1; i++) {
            const [p1, p2] = [smoothedPoints[i]!, smoothedPoints[i + 1]!];

            const dx = p2.x - p1.x;
            const dy = p2.y - p1.y;
            
            const segmentDist = Math.hypot(dx, dy);
            distanceTraveled += segmentDist;

            if (distanceTraveled >= this.STEP_DISTANCE) {
                const angle = Math.atan2(dy, dx);
                this.createFootprintStep(p1.x, p1.y, angle);
                distanceTraveled = 0;
            }
        }
    }

    private createFootprintStep(x: number, y: number, angle: number) {
        const texture = PIXI.Assets.get(this.ASSET_URL);
        if (!texture) return;

        const step = new PIXI.Sprite(texture);
        step.anchor.set(0.5);
        step.scale.set(0.6);
        step.rotation = angle + Math.PI / 2;
        step.x = x;
        step.y = y;
        step.alpha = 0.4; 

        this.container.addChild(step);
        this.footprints.push(step);
    }
    
    /**
     * @param points -    {x, y}
     * @param flattening -   (0.1 - 0.5)
     * @param iterations -      ( 1-2 )
     */
    public smoothPath(
        points: { x: number; y: number }[], 
        flattening: number = 0.4, 
        iterations: number = 1
    ) {

        if (points.length < 3) return points;

        //    
        let smoothed = points.map(p => ({ ...p }));

        for (let iter = 0; iter < iterations; iter++) {
            //      
            const nextIter = smoothed.map(p => ({ ...p }));

            for (let i = 1; i < smoothed.length - 1; i++) {
                //  TS,      (!)
                const prev = smoothed[i - 1]!;
                const curr = smoothed[i]!;
                const next = smoothed[i + 1]!;
                const targetCell = nextIter[i]!;

                //  "" 
                const targetX = (prev.x + next.x) / 2;
                const targetY = (prev.y + next.y) / 2;

                // LERP
                targetCell.x = curr.x + (targetX - curr.x) * flattening;
                targetCell.y = curr.y + (targetY - curr.y) * flattening;
            }
            smoothed = nextIter;
        }

        return smoothed;
    }

    public clear() {
        //   
        for (const f of this.footprints) {
            f.destroy({ 
                children: true, //   ,   
                texture: false,  //     (   )
            });
        }
        this.footprints = [];
    }

    public destroy() {
        this.clear();
        this.container.destroy({ children: true });
    }

}