import { CombatCalculator } from "@/core/battle/CombatCalculator";
import { PathCalculator } from "@/core/battle/PathCalculator";
import { BattleUnit, Hex } from "@/core/types/battle.types";
import { coordsToId } from "@/core/utils/hex.utils";

const AVERAGE_INTERRUPT_CHANCE = 0.75;

export class OpportunityRiskCalculator {

    public static calculate(
        unitId: string,
        units: Map<string, BattleUnit>,
        fromPosition: { q: number; r: number },
        toHexId: string,
        battleMap: Map<string, Hex>
    ): number {
        const unit = units.get(unitId);
        if (!unit) return 0;

        const startId = coordsToId(fromPosition);
        const fullPath = PathCalculator.findPath(startId, toHexId, battleMap);

        if (fullPath.length === 0) return 0;

        let totalRisk = 0;

        const completePath = [startId, ...fullPath];
        const adjacentEnemies = new Set<string>();
        let prevHex = null;

        for (const hexId of completePath) {
            if (prevHex) {
                const attackers = CombatCalculator.checkOpportunityAttackersOnHex(
                    unitId, prevHex, units
                );

                for (const { attackerId } of attackers) {
                    const attacker = units.get(attackerId);
                    if (!attacker || attacker.isDead) continue;

                    adjacentEnemies.add(attackerId);

                    const expectedDamage = CombatCalculator.calculateOpportunityRawDamage(
                        attacker, unit, true
                    );

                    totalRisk += expectedDamage;
                }
            }
            prevHex = hexId;
        }

        // Add interrupt risk
        if (adjacentEnemies.size > 0) {
            const avgEnemyDamage = totalRisk / Math.max(adjacentEnemies.size, 1);
            const interruptPenalty = adjacentEnemies.size * avgEnemyDamage * AVERAGE_INTERRUPT_CHANCE;
            
            totalRisk += interruptPenalty;
        }

        return totalRisk;
    }
}