/**
 * HighlightManager
 * 
 * Manages hex and unit highlighting in battle
 * - Hover highlights
 * - Skill range highlights
 * - State-aware highlighting (restores correct state)
 */

import type { EntityRegistry } from "@/core/battle/EntityRegistry";
import { HEX_HIGHLIGHT_COLOR } from "@/core/constants/battle.constants";
import { FactionType } from "@/core/types/battle.types";

import type { BattleHex } from "@/view/renderers/BattleHex";
import { coordsToId } from '@/core/utils/hex.utils';
import { useBattleStore } from "@/store/useBattleStore";
import { ControlStateMachine } from "@/core/battle/ControlStateMachine";
import { isUnitFinished, isUnitInActivePool } from "@/store/battleSelectors";


export class HighlightManager {
    private currentHoverState: {
        hexId: string | null;
        unitId: string | null;
    } = { hexId: null, unitId: null };
    
    constructor(
        private registry: EntityRegistry,
        private stateMachine: ControlStateMachine
    ) {}
    
    // 
    // HOVER MANAGEMENT
    // 
    
    /**
     * Updates hover state and synchronizes hex + unit highlighting
     */
    updateHoverState(hexId: string | null, unitId: string | null, force: boolean = false): void {
        // Skip if already hovering the same position

        if (!this.stateMachine.allowsHover() && !force) {
            
            if (this.currentHoverState.hexId || this.currentHoverState.unitId) {
                this.clearHoverState();
            }
            return;
        }

        if (!force && (this.currentHoverState.hexId === hexId && 
            this.currentHoverState.unitId === unitId)) {
            return;
        }
        
        // Clear previous highlights
        this.clearPreviousHoverHighlights();
        
        // Update state
        this.currentHoverState = { hexId, unitId };
        
        // Apply new highlights
        if (hexId) {
            this.highlightHex(hexId);
        }
        if (unitId) {
            this.highlightUnit(unitId);
        }
    }
    
    /**
     * Clears all hover state and highlights
     */
    clearHoverState(): void {
        this.clearPreviousHoverHighlights();
        this.currentHoverState = { hexId: null, unitId: null };
    }
    
    /**
     * Removes highlights from previously hovered hex/unit
     * Restores skill range highlights if in skill mode
     */
    private clearPreviousHoverHighlights(): void {
        // Restore previous hex to its base state
        if (this.currentHoverState.hexId) {
            this.restoreHexHighlight(this.currentHoverState.hexId);
        }
        
        // Clear previous unit highlight
        if (this.currentHoverState.unitId) {
            const unit = this.registry.getUnit(this.currentHoverState.unitId);
            if (unit) {
                unit.setHighlighted(false);
            }
        }
    }
    
    /**
     * Highlights a hex with standard hover color
     * Takes skill mode into account
     */
    private highlightHex(hexId: string): void {
        const hex = this.registry.getHex(hexId);
        if (!hex) return;
        
        const { selectedSkillId, battleMap, units, targetHexes, reachableHexesWithCost } = useBattleStore.getState();
        
        if (selectedSkillId) {
            // Skill mode - enhance skill highlight on hover
            const storeHex = battleMap.get(hexId);
            if (!storeHex) return;
            const isInRange = targetHexes.includes(hexId);
            const unitOnHex = storeHex.entityId ? units.get(storeHex.entityId) : null;
            const isEnemy = unitOnHex?.faction === FactionType.Enemy;
            
            hex.setHighlight(
                isEnemy && isInRange ? 0xff0000 : 0xffffff,
                isEnemy && isInRange ? 0.4 : 0.2
            );
        } else {
            // Normal mode - simple hover highlight
            if (reachableHexesWithCost.has(hexId)) {
                hex.setHighlight(0x000000, 0.1);
            } else {
                hex.setHighlight(HEX_HIGHLIGHT_COLOR, 0.1);
            }
        }
    }
    
    /**
     * Highlights a unit if it's valid for interaction
     */
    private highlightUnit(unitId: string): void {
        const unit = this.registry.getUnit(unitId);
        if (!unit) return;
        
        const isPlayerUnit = unit.faction === FactionType.Player;
        const state = useBattleStore.getState()
        const { finishedPools, targetHexes } = state;
        
        if (isPlayerUnit) {
            const isInCurrentPool = isUnitInActivePool(state, unit.unitId);
            const hasNotFinished = !isUnitFinished(state, unit.unitId);

            
            
            // Only highlight player units that can act
            if (isInCurrentPool && hasNotFinished) {
                unit.setHighlighted(true);
            }
        } else {
            // Highlight enemy units if in target range
            const hexId = coordsToId(unit);
            if (targetHexes.includes(hexId)) {
                unit.setHighlighted(true);
            }
        }
    }
    
    /**
     * Restores hex to its base highlight state (skill range or none)
     * Single source of truth for hex highlight state
     */
    private restoreHexHighlight(hexId: string): void {
        const { selectedSkillId, targetHexes, reachableHexesWithCost, units, battleMap } = useBattleStore.getState();
        const hex = this.registry.getHex(hexId);
        if (!hex) return;
        
        // 1.  
        if (selectedSkillId) {
            if (targetHexes.includes(hexId)) {
                const storeHex = battleMap.get(hexId);
                const unitOnHex = storeHex?.entityId ? units.get(storeHex.entityId) : null;
                const isEnemy = unitOnHex?.faction === FactionType.Enemy;
                
                hex.setHighlight(isEnemy ? 0xff0000 : 0xffffff, 0.2, isEnemy ? 1 : 0.4);
            } else {
                hex.setHighlight(null);
            }
            return;
        }

        // 2.   (    )
        if (reachableHexesWithCost.has(hexId)) {
            hex.setHighlight(0x000000, 0.2);
        } else {
            hex.setHighlight(null);
        }
    }
    
    // 
    // SKILL RANGE HIGHLIGHTS
    // 
    
    /**
     * Update all hex highlights based on skill range
     * Called when skill is selected/deselected
     */
    updateSkillRangeHighlights(): void {
        const hexes = this.registry.getAllHexes();
        
        hexes.forEach((_hex, id) => {
            this.updateHexBaseHighlight(id);
        });
    }

    updateReachableHighlights(): void {
        const hexes = this.registry.getAllHexes();
        
        hexes.forEach((_hex, id) => {
            this.updateHexBaseHighlight(id);
        });
    }
    
    /**
     * Single source of truth for hex base highlight
     * Used by both skillRangeHighlights and restoreHexHighlight
     */
    private updateHexBaseHighlight(hexId: string): void {
        const { 
            selectedSkillId, 
            targetHexes, 
            reachableHexes, 
            units, 
            battleMap 
        } = useBattleStore.getState();

        const hex = this.registry.getHex(hexId);
        if (!hex) return;

        // ---  1:   ---
        //   ,     
        if (selectedSkillId) {
            if (targetHexes.includes(hexId)) {
                const storeHex = battleMap.get(hexId);
                const unitOnHex = storeHex?.entityId ? units.get(storeHex.entityId) : null;
                const isEnemy = unitOnHex?.faction === FactionType.Enemy;

                hex.setHighlight(
                    isEnemy ? 0xff0000 : 0xffffff, //   ,   /
                    0.2,
                    isEnemy ? 1 : 0.4
                );
            } else {
                //       ( ,   reachableHexes)   
                hex.setHighlight(null);
            }
            return; //   ,      
        }

        // ---  2:   ---
        //    selectedSkillId === null
        if (reachableHexes.includes(hexId)) {
            hex.setHighlight(
                0x000000, //    
                0.2, 
                
            );
        } else {
            hex.setHighlight(null);
        }
    }
    
    // 
    // RESET & CLEANUP
    // 
    
    /**
     * Reset all hex highlights
     */
    resetAllHexes(): void {
        const hexes = this.registry.getAllHexes();
        hexes.forEach(h => h.setHighlight(null));
    }
    
    /**
     * Reset all unit highlights
     */
    resetAllUnits(): void {
        // Note: This would need access to all units
        // For now, highlights are cleared individually when needed
        const units = this.registry.getAllUnits();
        units.forEach(u => u.stopIdle());
    }
    
    /**
     * Finds hex at given screen coordinates
     * Returns null if no hex found at that position
     */
    findHexAtPosition(x: number, y: number): BattleHex | null {
        const hexes = this.registry.getAllHexes();
        
        for (const [_id, hex] of hexes) {
            if (hex.containsPoint(x, y)) {
                return hex;
            }
        }
        
        return null;
    }
    
    /**
     * Restores hover highlights based on mouse position
     * Called after animations that blocked input (movement, attacks)
     */
    restoreHoverHighlightsAfterAnimation(mouseX: number, mouseY: number, force: boolean = false): void {
        if (mouseX === null || mouseY === null) {
            // No mouse position tracked, clear hover state
            this.clearHoverState();
            return;
        }
        
        // Find what's under the mouse cursor
        const hoveredHex = this.findHexAtPosition(mouseX, mouseY);
        
        if (!hoveredHex) {
            // Mouse is off-screen or not over any hex
            this.clearHoverState();
            return;
        }
        
        const hexId = hoveredHex.id;
        const hex = useBattleStore.getState().battleMap.get(hexId);
        const unitId = hex?.entityId ?? null;
        
        // Restore highlights as if user just hovered
        this.updateHoverState(hexId, unitId, force);
    }
}